import gql from 'graphql-tag'

export const photoSummaryFragment = gql`
fragment photoSummaryFragment on PhotoSummary {
  id
  takenOn
  seriesCreatedBy
  updatedOn
  createdOn
  seriesId
  createdUser {
    ...userSummaryFragment
  }
  paths {
    base
    thumb
    medium
    large
    __typename
  }
  __typename
}
`
export const photoSummaryKeys = [
  'id',
  'takenOn',
  'seriesCreatedBy',
  'updatedOn',
  'createdOn',
  'seriesId',
  'createdUser',
  'applyPatches'
]

export const userSummaryFragment = gql`
fragment userSummaryFragment on UserSummary {
  id
  gravatar
  gravatarProfile
  username
  __typename
}
`
export const userSummaryKeys = [
  'id',
  'gravatar',
  'gravatarProfile',
  'username'
]

export const seriesSummaryFragment = gql`
fragment seriesSummaryFragment on SeriesSummary {
  id
  name
  access
  createdUser {
    ...userSummaryFragment
  }
  seriesStatus
  updatedOn
  createdOn
  photoCount
  coords
  direction
  watchers
  featured {
    ...photoSummaryFragment
  }
  latest {
    ...photoSummaryFragment
  }
  __typename
}
${photoSummaryFragment}
${userSummaryFragment}
`
