export const photoFragment = gql`
fragment photoFragment on Photo {
  id
  meta
  createdOn
  takenOn
  updatedOn
  coords
  seriesId
  seriesCreatedBy
  createdBy
  updatedBy
  uploadToken
  createdUser {
    ...userSummaryFragment
  }
  updatedUser {
    ...userSummaryFragment
  }
  tags {
    label
    typeId
    coords {
      x
      y
      __typename
    }
    __typename
  }
  paths {
    base
    large
    thumb
    medium
    __typename
  }
  __typename
}
${userSummaryFragment}
`
