import React from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import IconButton from '@material-ui/core/IconButton'
import Avatar from '@material-ui/core/Avatar'

import PlaceIcon from '@material-ui/icons/Place'
import PersonIcon from '@material-ui/icons/Person'
import LabelIcon from '@material-ui/icons/Label'
import CancelIcon from '@material-ui/icons/Cancel'

import { makeStyles } from '@material-ui/core/styles'
import { Typography } from '@material-ui/core'
const useStyles = makeStyles(theme => ({
  root: {

  },
  chosenItem: {
    color: theme.palette.primary.contrastText,
    background: theme.palette.primary.dark
  },
  avatar: {
    background: theme.palette.primary.main
  },
  closeIcon: {
    color: theme.palette.primary.contrastText,
    paddingRight: 0
  },
  avatarImg: {
    height: '100%',
    width: '100%',
    objectFit: 'cover'
  }
}))

const SearchContextItem = ({ className, variant, name, description, gravatar, onClick, onCancel }) => {
  const classes = useStyles()
  let TheIcon = PlaceIcon

  const getAvatar = (src) => () => (
    <Avatar className={classes.avatar}>
      <img className={classes.avatarImg} alt='profile' src={src} />
    </Avatar>
  )

  switch (variant) {
    case 'tag':
      TheIcon = LabelIcon
      break
    case 'location':
      TheIcon = PlaceIcon
      break
    case 'user':
      TheIcon = gravatar ? getAvatar(gravatar) : PersonIcon
      break
    default:
      break
  }
  return (
    <ListItem
      className={classNames(className, classes.root, onCancel ? classes.chosenItem : null)}
      button={!!onClick}
      onClick={onClick}
    >
      <ListItemAvatar>
        <Avatar className={classes.avatar}>
          <TheIcon color='inherit' />
        </Avatar>
      </ListItemAvatar>
      <ListItemText disableTypography>
        <Typography variant='body1' color='inherit'>{name}</Typography>
        <Typography variant='body2' color='inherit'>{description}</Typography>
      </ListItemText>
      {onCancel && (
        <ListItemIcon>
          <IconButton onClick={onCancel} className={classes.closeIcon}>
            <CancelIcon color='inherit' />
          </IconButton>
        </ListItemIcon>
      )}
    </ListItem>
  )
}

SearchContextItem.propTypes = {
  variant: PropTypes.oneOf(['tag', 'location', 'user']),
  onClick: PropTypes.func,
  gravatar: PropTypes.string,
  className: PropTypes.string,
  name: PropTypes.string.isRequired,
  description: PropTypes.string,
  onCancel: PropTypes.func
}

export default SearchContextItem

export const SearchContexItemCosmos = () => {
  return (
    <List>
      <SearchContextItem variant='tag' name='SomeTag' description='Some description of what I am' />
      <SearchContextItem variant='location' name='Some Location' description='Some description of what I am' />
      <SearchContextItem variant='user' name='User McUserSon' description='Some description of what I am' />
    </List>
  )
}
