import React, { useState } from 'react'
import PropTypes from 'prop-types'
import Link from '@material-ui/core/Link'
import TextField from '@material-ui/core/TextField'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import Toolbar from '@material-ui/core/Toolbar'

import { makeStyles } from '@material-ui/core/styles'
import style from './auth.style'
import AuthContainer from './AuthContainer'

const useStyles = makeStyles(style)

const ForgotPassword = ({ handleSubmit, handleInputChange, handleSendCode, handleBack }) => {
  const [sent, setSent] = useState(false)
  const classes = useStyles()
  return (
    <AuthContainer>
      <Typography>Forgot Password</Typography>
      {!sent && (
        <TextField
          id='username'
          autoFocus
          fullWidth
          label='Username'
          name='username'
          className={classes.textField}
          margin='normal'
          onChange={handleInputChange}
        />
      )}
      {sent && (
        <TextField
          id='changecode'
          fullWidth
          autoFocus
          label='Code'
          name='code'
          className={classes.textField}
          margin='normal'
          onChange={handleInputChange}
        />
      )}
      {sent && (
        <TextField
          id='newpassword'
          fullWidth
          label='New Password'
          className={classes.textField}
          type='password'
          name='password'
          autoComplete='current-password'
          margin='normal'
          onChange={handleInputChange}
        />
      )}
      {sent && (
        <Link component='button' variant='body2' className={classes.resendCode} onClick={handleSendCode}>
          Resend Code
        </Link>

      )}
      <Toolbar disableGutters>
        <Button
          color='secondary'
          variant='outlined'
          onClick={() => {
            sent ? setSent(false) : handleBack()
          }}
        >
          Back
        </Button>
        <div className={classes.spacer} />
        {!sent && (
          <Button
            color='primary'
            variant='contained'
            className='enterAction'
            onClick={() => {
              setSent(true)
              handleSendCode()
            }}
          >
          Send Code
          </Button>
        )}
        {sent && (
          <Button
            className='enterAction'
            color='primary'
            variant='contained'
            onClick={handleSubmit}
          >
          Submit
          </Button>
        )}
      </Toolbar>
    </AuthContainer>
  )
}

ForgotPassword.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  handleInputChange: PropTypes.func.isRequired,
  handleSendCode: PropTypes.func.isRequired,
  handleBack: PropTypes.func.isRequired
}

export default ForgotPassword
