import React, { useEffect } from 'react'
import { Query, ApolloProvider, compose } from 'react-apollo'
import { IntlWrapper } from './translations'
import { getUser } from './graphql/queries'
import themes from './themes'
import { AppContext } from './contexts'
import Loading from './components/auth/Loading'
import ErrorBoundary from './lib/ErrorBoundary'
import ErrorConnect from './graphql/Error.connect'
import useGraphQL, { APOLLOSTATES } from './graphql/useGraphQL'
import useAuthentication from './lib/useAuthentication'
import useShowAddToHome from './lib/useShowAddToHome'
import useOnline from './lib/useOnline'
import useResponsive from './lib/useResponsive'
import withReduxProvider from './redux/withReduxProvider'
import { connect } from 'react-redux'
import actions from './redux/actions'
import { imgStore } from './graphql/offline'
import { home as homeConfig } from './config'

import App from './App'
import { ThemeProvider } from '@material-ui/styles'

/**
 * HOC for AppContext and ApolloProvider
 */
export const withApolloAuthProvider = Wrapped => ({
  onQRClick, onAvatarClick, setUseOffline, dismissInstall, flags, ...props
}) => {
  // console.log('withApolloAuthProvider', props)
  const { isGod, signedIn, authData, signOut, authScreen, forceSignIn } = useAuthentication(flags.useLoggedOut, setUseOffline)

  const { client: apolloClient, done: apolloDone, currState: apolloState } = useGraphQL()

  // This triggers all the PWA Goodness
  const { online, offlineSince } = useOnline()
  const { installPrompt, addToHomeScreen } = useShowAddToHome(flags.install, dismissInstall)
  const responsive = useResponsive()

  // If the user hasn't decided to be anonymous or sign in yet show them the login screen
  // console.log('Rendering AppConnect', { flags, responsive, props, signedIn, isGod, authData })
  useEffect(() => {
    if (signedIn === true && flags.useLoggedOut) setUseOffline(false)
    else if (signedIn === false && !flags.useLoggedOut) setUseOffline(true)
  }, [signedIn])

  if (!apolloDone) return <Loading message={APOLLOSTATES[apolloState]} />

  if (signedIn === null && !flags.useLoggedOut) return authScreen

  // These are the things the rest of the app needs to find:
  const appContext = {
    signOut,
    forceSignIn: () => {
      setUseOffline(false)
      forceSignIn()
    },
    getPhotoUrl: async (photo, size) => {
      // console.log('getPhotoUrl::', { photo, size })
      if (photo.uploadToken) {
        const dbKeys = await imgStore.keys()
        if (dbKeys[photo.uploadToken]) {
          const dataUrl = imgStore.getItem(photo.uploadToken)
          // console.log('getPhotoUrl:: Found DB Entry. using dataUrl', { url: dataUrl.substring(0, 100) })
          return dataUrl
        }
      }

      try {
        const url = `${photo.paths.base}${photo.paths[size]}`
        // console.log('getPhotoUrl:: Didn\'t find BD entry', { url })
        return url
      }
      catch (e) {
        return null
      }
    },
    onAvatarClick,
    online,
    offlineSince,
    ...responsive,
    signedIn: Boolean(signedIn),
    role: !signedIn
      ? 'nonuser'
      : isGod
        ? 'admin'
        : 'user',
    currentUser: authData, // TODO: Need this supported by a graphql query
    onQRClick,
    addToHomeScreen
  }

  return (
  // We wrap the outside in an error handler
    <ThemeProvider theme={themes.home}>
      <ApolloProvider client={apolloClient}>
        <Query query={getUser} skip={!signedIn}>
          {({ loading, error, data }) => {
            if (loading) return <Loading message='UserData' />
            if (error) {
              console.error(error)
            }
            if (signedIn) appContext.user = data.user
            return (
              <AppContext.Provider value={appContext}>
                <IntlWrapper>
                  <Wrapped {...props}>
                    {installPrompt}
                  </Wrapped>
                </IntlWrapper>
              </AppContext.Provider>
            )
          }}
        </Query>
      </ApolloProvider>
    </ThemeProvider>
  )
}

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { domain } = state.home.current
  return { domain, flags: state.flags }
}

const mapDispatchToProps = dispatch => ({
  setHomeState: (newDomain, newParams) => {
    dispatch(actions.setHomeState(newDomain, newParams))
  },
  onCameraClick: () => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.NEW_SERIES_CAMERA))
  },
  onLibraryClick: () => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.NEW_SERIES_LIBRARY))
  },
  dismissInstall: () => {
    dispatch(actions.setFlag('install', false))
  },
  setUseOffline: (bVal) => {
    dispatch(actions.setFlag('useLoggedOut', bVal))
  },
  onQRClick: () => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.QRCAPTURE))
  },
  onMenuBack: () => {
    dispatch(actions.homeStateBack())
  },
  onAvatarClick: (username) => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.SEARCH, {
      search: { tab: homeConfig.SEARCH_TABS.USER, text: username }
    }))
  },
  onQRCapture: (type, activeSeries) => {
    switch (type) {
      case 'series':
        dispatch(actions.setHomeState(homeConfig.AppStateTypes.SERIES, { activeSeries }))
        break
      default:
        break
    }
  }
})

const withErrorBoundary = (Wrapped) => (props) => (
  <ErrorBoundary fallback={ErrorConnect}>
    <Wrapped {...props} />
  </ErrorBoundary>
)

/**
 * Nearly everything we do depends on who you are as a user so we commit this to state
 */

// The order we compose things here matters a lot
export default compose(
  // ORDER IS CRITICAL HERE
  withReduxProvider,
  connect(
    mapStateToProps,
    mapDispatchToProps
  ),
  withApolloAuthProvider,
  withErrorBoundary
)(App)
