import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'

import SearchContextResults from './SearchContextResults'
import SearchResults from './SearchResults'
import SearchPlaceholder from './SearchPlaceholder'
import TopBar from '../TopBar'
import SearchInput from './SearchInput'
import useOnline from '../../lib/useOnline'
import { home } from '../../config'
import PortableWifiOffIcon from '@material-ui/icons/PortableWifiOff'
import LabelIcon from '@material-ui/icons/Label'
import PlaceIcon from '@material-ui/icons/Place'
import PersonIcon from '@material-ui/icons/Person'
import { makeStyles } from '@material-ui/core/styles'

const TABS = home.SEARCH_TABS

const style = theme => ({
  root: {
    height: '100%',
    display: 'flex',
    overflow: 'hidden',
    flexDirection: 'column'
  },
  tabs: {
    // backgroundColor: theme.palette.primary.main,
    // color: theme.palette.primary.contrastText,
    // background: `linear-gradient(90deg, ${theme.palette.primary.dark} 0%, ${theme.palette.secondary.dark} 100%)`

  },
  ListWrapper: {
    flexGrow: 1,
    overflow: 'hidden',
    display: 'flex',
    flexDirection: 'column'
  },
  tab: {
    flexGrow: 1
  },
  grow: {
    flexGrow: 1
  }
})

const useStyles = makeStyles(style)

const Search = ({
  onMenuBack, currentTab, setCurrentTab, defaultText, searchText,
  setSearchText, contextResults, searchResults, contextChoice,
  loading, setContextChoice, onMapClick, onResultClick, fetchMore
}) => {
  const classes = useStyles()

  const { online } = useOnline()

  // If there's only one thing then use it
  useEffect(() => {
    if (contextResults && !searchResults && contextResults.length === 1) {
      setContextChoice(contextResults[0])
    }
  }, [contextResults])

  let placeholder = {}
  switch (currentTab) {
    case TABS.TAG:
      placeholder = { primary: 'Tag search', secondary: 'example: \'beaver...\'', icon: LabelIcon }
      break
    case TABS.USER:
      placeholder = { primary: 'User search', secondary: 'example: \'SuperGreg123...\'', icon: PersonIcon }
      break
    case TABS.LOCATION:
      placeholder = { primary: 'Location Search', secondary: 'example: \'Vancouver...\'', icon: PlaceIcon }
      break

    default:
      break
  }

  let content = null
  if (!online) {
    content = (
      <SearchPlaceholder
        primary='Search Disabled'
        secondary='(Offline)'
        icon={PortableWifiOffIcon}
      />
    )
  }
  else if (contextChoice && searchResults) {
    content = (
      <SearchResults
        variant={currentTab}
        loading={loading}
        onResultClick={onResultClick}
        fetchMore={fetchMore}
        seriesList={searchResults}
        contextChoice={contextChoice}
        onCancel={() => setContextChoice(null)}
      />
    )
  }
  else if (contextResults) {
    content = (
      <SearchContextResults
        loading={loading}
        variant={currentTab}
        contextResults={contextResults}
        setContextChoice={currentTab === TABS.LOCATION ? onMapClick : setContextChoice}
      />
    )
  }
  else if (defaultText.length > 0) {
    content = (
      <Typography>Searching for {`'${searchText}'` || '...'}</Typography>
    )
  }
  else {
    content = <SearchPlaceholder {...placeholder} />
  }

  return (
    <div className={classes.root}>
      <TopBar>
        <SearchInput
          disabled={!online}
          // autoFocus
          onChange={setSearchText}
          defaultValue={searchText}
        />
      </TopBar>
      {online && (
        <Tabs
          value={currentTab}
          disabled={!online}
          onChange={(e, val) => setCurrentTab(val)}
          indicatorColor='primary'
          classes={{ flexContainer: classes.tabs }}
          centered
        >
          <Tab className={classes.tab} value={TABS.TAG} label='Tags' />
          <Tab className={classes.tab} value={TABS.LOCATION} label='Places' />
          <Tab className={classes.tab} value={TABS.USER} label='Users' />
        </Tabs>
      )}
      <div className={classes.ListWrapper} >
        {content}
      </div>
    </div>
  )
}

Search.propTypes = {
  onMenuBack: PropTypes.func.isRequired,
  loading: PropTypes.bool,
  currentTab: PropTypes.string.isRequired,
  setCurrentTab: PropTypes.func.isRequired,
  searchText: PropTypes.string.isRequired,
  setSearchText: PropTypes.func.isRequired,
  fetchMore: PropTypes.func,
  contextResults: PropTypes.array,
  searchResults: PropTypes.array,
  contextChoice: PropTypes.object,
  setContextChoice: PropTypes.func.isRequired,
  onMapClick: PropTypes.func.isRequired,
  onResultClick: PropTypes.func.isRequired
}
Search.defaultProps = {
  currentTab: TABS.TAGS,
  defaultText: ''
}

export default Search
