import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core/styles'

import TagChip from '../tags/TagChip'
import style from './TagControl.style'

const useStyles = makeStyles(style)
/**
 * Just to be clear: the state here is being stored as []
 */
const TagControl = ({ tags, label, tagConfig, className }) => {
  const classes = useStyles()
  // Filter to just this tag type
  const tagValues = tags.filter(tag => tag.typeId === tagConfig.id && !tag.coords)

  return (
    <div className={classNames(classes.root, className)}>
      <Typography variant='overline' className={classes.fieldTitle}>
        {tagConfig.label}
      </Typography>
      <div className={classes.tagContainer}>
        {tagValues.map((tag, idx) => (
          <TagChip tag={tag} key={`tagchip${idx}`} color={tagConfig.color} />
        ))}
      </div>

    </div>
  )
}

TagControl.propTypes = {
  className: PropTypes.string,
  label: PropTypes.string,
  tags: PropTypes.array,
  tagConfig: PropTypes.object
}

export default TagControl
