import { Actions } from '../actions'

const initState = {
  store: [],
  activeUpload: null
}

export default (state = initState, action) => {
  const { type, data } = action
  const { store, activeUpload } = state

  switch (type) {
    case Actions.UPLOADER_START:
      if (!activeUpload) {
        return {
          ...state,
          activeUpload: data
        }
      }
      else {
        return {
          ...state,
          store: [...store, data]
        }
      }
    case Actions.UPLOADER_COMPLETE:
      if (store.length === 0) {
        return {
          ...state,
          activeUpload: null
        }
      }
      else {
        const newStore = [...store]
        const newActive = newStore.pop()
        return {
          ...state,
          store: newStore,
          activeUpload: newActive
        }
      }
    case Actions.UPLOADER_UPDATE_PROGRESS:
      return {
        ...state,
        activeUpload: { ...activeUpload, ...data }
      }
    default:
      return state
  }
}
