import React from 'react'
import PropTypes from 'prop-types'
import List from '@material-ui/core/List'
import Typography from '@material-ui/core/Typography'

import SearchContextItem from './SearchContextItem'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    backgroundColor: theme.palette.background.paper,
    position: 'relative',
    overflowX: 'hidden',
    overflowY: 'scroll',
    flexGrow: 1
  },
  listSection: {
    backgroundColor: 'inherit'
  },
  placeholderText: {
    textAlign: 'center',
    padding: theme.spacing(3, 1),
    color: theme.palette.grey[400]
  },
  ul: {
    backgroundColor: 'inherit',
    padding: 0
  }
}))

const SearchContextResults = ({ variant, loading, contextResults, setContextChoice }) => {
  const classes = useStyles()
  if (loading) {
    return <Typography className={classes.placeholderText} variant='h3'>Searching...</Typography>
  }
  else if (!contextResults || contextResults.length === 0) {
    return <Typography className={classes.placeholderText} variant='h4'>No {variant} results</Typography>
  }

  return (
    <List className={classes.root}>
      {contextResults.map((cr, idx) => (
        <SearchContextItem
          key={`scr-${idx}`}
          variant={variant}
          onClick={() => setContextChoice(cr)}
          {...cr}
        />
      ))}
    </List>
  )
}

SearchContextResults.propTypes = {
  variant: PropTypes.oneOf(['tag', 'location', 'user']),
  loading: PropTypes.bool,
  contextResults: PropTypes.array.isRequired,
  setContextChoice: PropTypes.func.isRequired
}

export default SearchContextResults
