import React, { useContext, useEffect } from 'react'
import PropTypes from 'prop-types'

import CssBaseline from '@material-ui/core/CssBaseline'

import UploaderConnect from './graphql/Uploader.connect'
import SeriesConnect from './graphql/Series.connect'
import MapConnect from './graphql/Map.connect'
import SearchConnect from './graphql/Search.connect'
import ProfileConnect from './graphql/Profile.connect'
import FeedConnect from './graphql/Feed.connect'
import QRReader from './components/PhotoSeries/views/QRReader'

import OfflineBar from './components/misc/OfflineBar'
import BottomBar from './components/BottomBar'
import { AppContext } from './contexts'

import { makeStyles } from '@material-ui/core/styles'
import { home as homeConfig } from './config'

import { ThemeProvider } from '@material-ui/styles'
import themes from './themes'
// import { DesktopWindowsTwoTone } from '@material-ui/icons'

const STATES = homeConfig.AppStateTypes

const useStyles = makeStyles(theme => ({
  outerRoot: {
    height: '100%',
    overflow: 'hidden'
  },
  root: {
    height: '100%',
    overflow: 'hidden',
    display: 'flex',
    flexDirection: 'column'
  },
  offlineBar: {
    flex: '0 0'
  },
  activeWrapper: {
    overflow: 'hidden',
    flexGrow: 1,
    flexShrink: 1,
    height: '100%'
  },
  uploaderContainer: {
    zIndex: 100,
    flex: '0 0'
  }
}))

/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 */
const App = ({ domain, onQRCapture, onMenuBack, setHomeState, onCameraClick, onLibraryClick, children }) => {
  const classes = useStyles()
  const { signedIn, user, windowSize } = useContext(AppContext)

  useEffect(() => {
    // Anonymous Users are only allowed home and search
    if (!signedIn && [STATES.MAP, STATES.SEARCH, STATES.SERIES, STATES.QRCAPTURE].indexOf(domain) < 0) setHomeState(STATES.MAP)
  }, [domain])

  let showBottomBar = true
  let activeProps = {}
  let ActiveComponent = FeedConnect
  switch (domain) {
    case STATES.FEED:
      ActiveComponent = FeedConnect
      break
    case STATES.MAP:
      ActiveComponent = MapConnect
      break
    case STATES.PROFILE:
      ActiveComponent = ProfileConnect
      activeProps = { user }
      break
    case STATES.SEARCH:
      ActiveComponent = SearchConnect
      break
    case STATES.SERIES:
      ActiveComponent = SeriesConnect
      activeProps = { user }
      showBottomBar = false
      break
    case STATES.NEW_SERIES_CAMERA:
      activeProps = { user }
      ActiveComponent = SeriesConnect
      showBottomBar = false
      break
    case STATES.NEW_SERIES_LIBRARY:
      activeProps = { user }
      ActiveComponent = SeriesConnect
      showBottomBar = false
      break
    case STATES.QRCAPTURE:
      ActiveComponent = QRReader
      showBottomBar = false
      activeProps = { onCapture: onQRCapture, onMenuBack }
      break
    default:
      break
  }

  return (
    <div className={classes.outerRoot}>
      <div className={classes.root} style={{ width: windowSize.width, height: windowSize.height }}>
        <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
        <OfflineBar className={classes.offlineBar} />
        <div className={classes.activeWrapper}>
          <ActiveComponent {...activeProps} />
        </div>
        { showBottomBar && (
          <ThemeProvider theme={themes.bottombar}>
            <BottomBar
              signedIn={signedIn}
              domain={domain}
              onHomeClick={() => setHomeState(STATES.FEED)}
              onMapClick={() => setHomeState(STATES.MAP)}
              onSearchClick={() => setHomeState(STATES.SEARCH, { search: null })}
              onProfileClick={() => setHomeState(STATES.PROFILE)}
              onCameraClick={onCameraClick}
              onLibraryClick={onLibraryClick}
            />
          </ThemeProvider>
        )}
        {/* Uploader is persistent across all views */}
        <UploaderConnect className={classes.uploaderContainer} />
        {children}
      </div>
    </div>
  )
}

App.propTypes = {
  // All of this comes in from From Redux
  domain: PropTypes.string.isRequired,
  children: PropTypes.any,
  setHomeState: PropTypes.func.isRequired,
  onCameraClick: PropTypes.func.isRequired,
  onLibraryClick: PropTypes.func.isRequired,
  onQRCapture: PropTypes.func.isRequired,
  onMenuBack: PropTypes.func.isRequired
}

export default App
