import { Actions } from '../actions'
import { isIos } from '../../lib/sensors'

const defaultFlags = {
  iOSWarning: isIos(),
  install: true,
  useLoggedOut: false
}
export default (state = defaultFlags, action) => {
  const { type, data } = action

  switch (type) {
    case Actions.SETFLAG:
      const { flagName, flagVal } = data
      return { ...state, [flagName]: flagVal }

    default:
      return state
  }
}
