// import React, { useContext } from 'react'
// import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { allSeries, getWholeSeries, getUser, getMyFeed } from '../queries'
import { createSeries, newSeriesKeys, newPhotoKeys } from '../mutations'
import { pushMutationPromise, popMutationPromise } from '../offline'
import { seriesKeys } from '../fragments'

import _ from 'lodash'

export const withMutation = graphql(createSeries, {
  props: ({ mutate, ownProps }) => {
    return {
      createSeriesMutation: (newSeries, newPhoto, ext) => {
        const { id, gravatar, gravatarProfile, username } = ownProps.user
        const meUserSummary = { id, gravatar, gravatarProfile, username, __typename: 'UserSummary' }
        if (!newSeries.access) newSeries.access = 'PRIVATE'

        const variables = {
          series: _.pick(newSeries, newSeriesKeys),
          photo: _.pick(newPhoto, newPhotoKeys),
          ext
        }
        const optimisticResponse = createOptimisticResponse({ meUserSummary, newSeries, newPhoto, ext })
        const mutationStackId = pushMutationPromise({ variables, optimisticResponse }, createSeries)
        console.log('createSeriesMutation:: start', { newSeries, newPhoto, variables, optimisticResponse, ext })
        return mutate({
          variables,
          refetchQueries: [{ query: getWholeSeries, variables: { id: newSeries.id } }],
          optimisticResponse,
          update: updateThunk(() => ownProps.doneCreatingSeries(newSeries.id))
        }).then(data => {
          // Here's where we take it out of the queue
          console.log('createSeriesMutation:: done', data)
          popMutationPromise(mutationStackId)
          return data
        })
      }
    }
  }
})

const updateThunk = (cb) => (store, { data }) => {
  // We need to write this query from scratch
  console.log('createSeriesMutation::updateThunk STARTING TO CREATE THE SERIES', { data, store })

  try {
    const qdataAll = store.readQuery({ query: allSeries, variables: { limit: 100 } })
    // console.log('createSeriesMutation qdataAll:', qdataAll)
    const newAllSeriesEntry = _.pick(data.createSeries.series, seriesKeys)
    qdataAll.allSeries.seriesSummaries.push({ ...newAllSeriesEntry, seriesStatus: null, __typename: 'SeriesSummary' })
    store.writeQuery({ query: allSeries, data: qdataAll })
    // console.log('createSeriesMutation qdataAll WRITE:', qdataAll)
  }

  catch (e) {
    // dumpLog(store.data, '##CACHE-NEW_SERIES_ERROR')
    console.log('ERROR: createSeriesMutation::updateThunk updating allSeries failed')
  }
  // We need a better place to do this ACTIONS DO NOT BELONG HERE
  console.log('Series.connect: createSeriesMutation::updateThunk Setting new series id:', data.createSeries)
  if (cb) cb()
}

const createOptimisticResponse = ({ meUserSummary, newSeries, newPhoto, ext }) => ({
  createSeries: {
    series: {
      id: newSeries.id,
      name: newSeries.name,
      note: newSeries.note || null,
      access: newSeries.access,
      meta: newSeries.meta || null,
      featured: null,
      latest: null,
      coords: newSeries.coords || null,
      direction: newSeries.direction || null,
      photoCount: 1,
      createdUser: meUserSummary,
      updatedUser: meUserSummary,
      createdBy: meUserSummary.id,
      updatedBy: meUserSummary.id,
      createdOn: Date.now().toString(),
      updatedOn: Date.now().toString(),
      watchers: 0,
      photos: [{
        id: newPhoto.id,
        meta: newPhoto.meta || null,
        seriesId: newSeries.id,
        createdOn: Date.now().toString(),
        createdUser: meUserSummary,
        updatedUser: meUserSummary,
        createdBy: meUserSummary.id,
        updatedBy: meUserSummary.id,
        uploadToken: newPhoto.uploadToken,
        updatedOn: Date.now().toString(),
        seriesCreatedBy: meUserSummary.id, // we can't answer this yet
        takenOn: newPhoto.takenOn,
        coords: newPhoto.coords || null,
        tags: newPhoto.tags || [],
        paths: {
          base: null,
          large: null,
          orig: null,
          thumb: null,
          medium: null,
          __typename: 'Paths'
        },
        __typename: 'Photo'
      }],
      __typename: 'Series'
    },
    preAuth: null,
    __typename: 'NewSeries'
  }
})

export default {
  withMutation,
  updateThunk
}
