import React from 'react'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    width: 1,
    height: 1,
    zIndex: 1
  },
  svgDot: {
    width: 20,
    height: 20,
    position: 'absolute',
    top: '50%',
    left: '50%',
    textAlign: 'center',
    transformOrigin: '50% 50%',
    transform: 'translateY(-50%) translateX(-50%)'
  },
  svgAccuracyDot: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    textAlign: 'center',
    transformOrigin: '50% 50%',
    transform: 'translateY(-50%) translateX(-50%)'
  }
})
const useStyles = makeStyles(style)

const MyLocation = ({ position, projection }) => {
  const classes = useStyles()
  const accuracy = 20 // assume 20m to start
  // metersPerPixel [12, 12, 12]
  // pixelsPerMeter [0: 0.08029229250399847, 1: -0.08029229250399847, 2: 0.08029229250399847]
  // console.log('POSITION', { position, projection })
  const accHeight = projection.pixelsPerMeter[0] * (accuracy)
  const accWidth = projection.pixelsPerMeter[1] * (accuracy)
  return (
    <div className={classes.root}>
      {/* This is the accuracy radius */}
      <svg
        style={{ height: accHeight, width: accWidth }}
        className={classes.svgAccuracyDot}
        version='1.1'
        viewBox='0, 0, 100, 100'
      >
        <circle cx='50' cy='50' r={49}
          stroke='#8888FF'
          fillOpacity='0.4'
          strokeWidth='1'
          fill='#AAAAFF'
        />
      </svg>
      {/* This is the dot */}
      <svg className={classes.svgDot} version='1.1' viewBox='0, 0, 10, 10'>
        <circle cx='5' cy='5' r='4'
          stroke='#FFFFFF'
          strokeWidth='1'
          fill='#4444FF'
        />
      </svg>
    </div>
  )
}

MyLocation.propTypes = {
  position: PropTypes.object,
  projection: PropTypes.object
}

export default MyLocation
