import React, { useEffect, useState, useMemo } from 'react'
import { isIos } from './sensors'
import InstallPrompt from '../components/misc/InstallPrompt'
/**
 * Everything below here is about the prompt for installing a PWA
 */

// Detects if device is in standalone mode
const isInStandaloneMode = () => ('standalone' in window.navigator) && (window.navigator.standalone)

const useShowAddToHome = (showInstall, dismissInstall) => {
  const [deferredPrompt, setPrompt] = useState(null)

  const handleBeforePrompt = e => {
    // Prevent Chrome 67 and earlier from automatically showing the prompt
    e.preventDefault()
    // Stash the event so it can be triggered later.
    setPrompt(e)
    // showAddToHomeScreen()
  }

  const installPrompt = useMemo(() => {
    if (!showInstall || !deferredPrompt) return null
    return <InstallPrompt open onClose={dismissInstall} onInstall={addToHomeScreen} />
  }, [showInstall, deferredPrompt])

  useEffect(() => {
    // window.orientation [0,90,180, 270]
    // screen
    // Checks if should display install popup notification:
    if (isIos() && !isInStandaloneMode()) {
      console.log('I AM on IOS and I am NOT in standalone mode.')
    // this.setState({ showInstallMessage: true })
    }

    window.addEventListener('beforeinstallprompt', handleBeforePrompt)
    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforePrompt)
    }
  }, [])

  function addToHomeScreen () {
    // Show the prompt
    deferredPrompt.prompt()
    // Wait for the user to respond to the prompt
    deferredPrompt.userChoice
      .then((choiceResult) => {
        if (choiceResult.outcome === 'accepted') {
          console.log('User accepted the A2HS prompt')
          dismissInstall()
        }
        else {
          console.log('User dismissed the A2HS prompt')
          dismissInstall()
        }
        setPrompt(null)
      })
  }

  // function showAddToHomeScreen () {
  //   const a2hsBtn = document.querySelector('.ad2hs-prompt')
  //   a2hsBtn.style.display = 'block'
  //   a2hsBtn.addEventListener('click', addToHomeScreen)
  // }

  return {
    installPrompt,
    addToHomeScreen: installPrompt ? addToHomeScreen : null
  }
}

export default useShowAddToHome
