import React, { useState } from 'react'
import PropTypes from 'prop-types'
// import Typography from '@material-ui/core/Typography'
import DialogContent from '@material-ui/core/DialogContent'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import DialogTitle from '@material-ui/core/DialogTitle'

import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {

  },
  body: {
    // height: '100%',
    // overflow: 'hidden'
  },
  textArea: {
    whiteSpace: 'pre-line',
    padding: theme.spacing(1),
    border: `1px solid ${theme.palette.grey[200]}`,
    background: theme.palette.grey[100]
  },
  dlgContent: {
    overflow: 'hidden'
  },
  title: {
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
    alignSelf: 'flex-start',
    width: '100%',
    margin: 0,
    display: 'flex',
    padding: theme.spacing(2)
  },
  closeButton: {
    padding: 0,
    alignSelf: 'flex-start'
  }
})
const useStyles = makeStyles(style)

const BigTextEdit = ({ open, onOk, title, body, multiline }) => {
  const classes = useStyles()
  const [value, setValue] = useState(body || '')
  return (
    <Dialog
      scroll='paper'
      open={open}
      fullWidth
      maxWidth='xl'
      onClose={() => onOk(value)}
    >
      <DialogTitle className={classes.title}>
        {title}
      </DialogTitle>
      <DialogContent className={classes.dlgContent}>
        <TextField
          className={classes.body}
          multiline={multiline}
          inputProps={{
            className: classes.textArea
          }}
          autoFocus
          fullWidth
          rows={6}
          rowsMax={6}
          onChange={e => setValue(e.target.value)}
          defaultValue={body}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={() => onOk(body)}>Cancel</Button>
        <div style={{ flexGrow: 1 }} />
        <Button onClick={() => onOk(value)}>Ok</Button>
      </DialogActions>
    </Dialog>
  )
}

BigTextEdit.propTypes = {
  open: PropTypes.bool,
  multiline: PropTypes.bool,
  onOk: PropTypes.func,
  title: PropTypes.string,
  body: PropTypes.string
}

export default BigTextEdit
