import React, { useState } from 'react'
import PropTypes from 'prop-types'

import MobileStepper from '@material-ui/core/MobileStepper'
import IconButton from '@material-ui/core/IconButton'
import GridListTileBar from '@material-ui/core/GridListTileBar'
import Badge from '@material-ui/core/Badge'

import LabelIcon from '@material-ui/icons/Label'
// import RemoveRedEye from '@material-ui/icons/RemoveRedEye'
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight'

import SwipeableViews from 'react-swipeable-views'
import Photo from './Photo'
import { formatDate } from '../../../../lib/datelib'
import { makeStyles, useTheme } from '@material-ui/core/styles'
import style from './SwipeShow.style'

const useStyles = makeStyles(style)

const SwipeShow = ({ photos, initialPhoto, onImageSelect }) => {
  const classes = useStyles()
  const theme = useTheme()
  const initPhotoIndex = initialPhoto ? photos.findIndex(p => p.id === initialPhoto) || 0 : 0
  const [activePhoto, setActivePhoto] = useState(initPhotoIndex >= 0 ? initPhotoIndex : 0)
  const [showTags, setShowTags] = useState(false)
  const singular = photos.length < 2

  const sortedPhotos = [...photos]
  sortedPhotos.sort((a, b) => parseInt(b.takenOn, 10) - parseInt(a.takenOn, 10))

  const handleForward = () => setActivePhoto(activePhoto >= sortedPhotos.length ? 0 : activePhoto + 1)
  const handleBack = () => setActivePhoto(activePhoto <= 0 ? sortedPhotos.length - 1 : activePhoto - 1)

  return (
    <div className={classes.root}>
      <div className={classes.inner}>
        <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={activePhoto}
          onChangeIndex={(val) => setActivePhoto(val)}
          enableMouseEvents
        >
          {sortedPhotos.map((photo, idx) => (
            <div className={classes.photoWrapper} key={`swipeshowpic-${idx}`} onClick={() => onImageSelect(sortedPhotos[activePhoto].id)}>
              <Photo photo={photo} className={classes.photo} showTags={showTags} />
            </div>
          ))}
        </SwipeableViews>
        <IconButton size='small' className={classes.manualBack} onClick={handleBack} color='inherit' disabled={activePhoto === 0}>
          {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
        </IconButton>
        <IconButton size='small' className={classes.manualForward} onClick={handleForward} color='inherit' disabled={activePhoto === photos.length - 1}>
          {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
        </IconButton>
        <GridListTileBar
          title={singular ? 'Photo' : `Photo (${activePhoto + 1}/${sortedPhotos.length})`}
          subtitle={sortedPhotos.length > 0 && (
            <span>{formatDate(sortedPhotos[activePhoto].takenOn)}</span>
          )}
          actionIcon={
            <React.Fragment>
              <IconButton className={classes.tagButton} onClick={() => setShowTags(!showTags)}>
                <Badge color={showTags ? 'primary' : 'secondary'} badgeContent={sortedPhotos[activePhoto].tags ? sortedPhotos[activePhoto].tags.filter(tag => tag.coords).length : null}>
                  <LabelIcon />
                </Badge>
              </IconButton>
            </React.Fragment>
          }
        />
      </div>
      {!singular &&
      <MobileStepper
        steps={sortedPhotos.length}
        position='static'
        activeStep={activePhoto}
        classes={{
          root: classes.mobileStepper,
          dots: classes.dots
        }}
      />
      }
    </div>
  )
}

SwipeShow.propTypes = {
  // This is just the basic shape of the photo object
  initialPhoto: PropTypes.string,
  photos: PropTypes.arrayOf(PropTypes.shape({
    img: PropTypes.string,
    tags: PropTypes.array,
    date: PropTypes.date
  })),
  transition: PropTypes.number,
  onImageSelect: PropTypes.func.isRequired
}

SwipeShow.defaultProps = {
  transition: 5000,
  isFullScreen: false,
  slides: []
}

export default SwipeShow
