import { createStore, applyMiddleware, compose } from 'redux'
import thunk from 'redux-thunk'
import rootReducer from './reducers'
import { persistStore, persistReducer } from 'redux-persist'
import { stateStore } from '../graphql/offline'
// import storage from 'redux-persist/lib/storage' // defaults to localStorage for web

// import api from '../middleware/api'
// https://github.com/reduxjs/redux/blob/master/examples/real-world/src/middleware/api.js

const persistConfig = {
  key: 'root',
  storage: stateStore
}

const persistedReducer = persistReducer(persistConfig, rootReducer)

/**
 * On Dev we want our devTools available
 */
const configureStoreDev = preloadedState => {
  const { devToolsEnhancer } = require('redux-devtools-extension')
  const { createLogger } = require('redux-logger')
  const store = createStore(
    persistedReducer,
    preloadedState,
    compose(
      devToolsEnhancer(),
      applyMiddleware(
        thunk,
        createLogger()
      )
    )
  )
  if (module.hot) {
    // Enable Webpack hot module replacement for reducers
    module.hot.accept('./reducers', () => {
      store.replaceReducer(rootReducer)
    })
  }
  return store
}

/**
 * Production gets a simpler more performant store:
 */
const configureStoreProd = preloadedState => {
  const store = createStore(
    persistedReducer,
    preloadedState,
    compose(
      applyMiddleware(
        thunk
      )
    )
  )
  return store
}

export default () => {
  const store = process.env.NODE_ENV === 'production' ? configureStoreProd() : configureStoreDev()
  return {
    store,
    persistor: persistStore(store)
  }
}
