import React, { useContext } from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'

import CssBaseline from '@material-ui/core/CssBaseline'
import AppBar from '@material-ui/core/AppBar'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'
// import Fab from '@material-ui/core/Fab'

import HomeIcon from '@material-ui/icons/HomeOutlined'
import MapIcon from '@material-ui/icons/MapOutlined'
import SearchIcon from '@material-ui/icons/SearchOutlined'
import AddIcon from '@material-ui/icons/Add'
import PersonOutline from '@material-ui/icons/PersonOutline'
import { makeStyles } from '@material-ui/core/styles'

import AddFab from '../components/PhotoSeries/views/controls/AddFab'
import OfflineBar from './misc/OfflineBar'

import { AppContext } from '../contexts'

import { home as homeConfig } from '../config'

const STATES = homeConfig.AppStateTypes

const useStyles = makeStyles(theme => ({
  root: {
    borderTop: `2px solid ${theme.palette.secondary.main}`
  },
  text: {
    padding: theme.spacing(2, 2, 0)
  },
  paper: {
    paddingBottom: 50
  },
  list: {
    marginBottom: theme.spacing(2)
  },
  subheader: {
    backgroundColor: theme.palette.background.paper
  },
  grow: {
    flexGrow: 1
  },
  addFab: {
    position: 'absolute',
    bottom: 0,
    left: '50%',
    transformOrigin: '0% 100%',
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: 'translateY(-25%) translateX(-50%) translateZ(0)',
    zIndex: 200
  },
  spacer: {
    width: 40,
    flexGrow: 1
  }
}))

const BottomBar = ({ className, domain, signedIn, onHomeClick, onMapClick, onSearchClick, onProfileClick, onCameraClick, onLibraryClick }) => {
  const classes = useStyles()
  const { isIos } = useContext(AppContext)
  return (
    <AppBar position='static' className={classNames(classes.root, className)}>
      <Toolbar>
        {signedIn && (
          <Button
            edge='start'
            className={classes.grow}
            color={domain === STATES.FEED ? 'secondary' : 'inherit'}
            onClick={onHomeClick}
          >
            <HomeIcon />
          </Button>
        )}
        <Button
          edge='start'
          className={classes.grow}
          color={domain === STATES.MAP ? 'secondary' : 'inherit'}
          onClick={onMapClick}
        >
          <MapIcon />
        </Button>
        {signedIn && (
          <AddFab
            isIos={isIos}
            className={classes.addFab}
            onCameraClick={onCameraClick}
            onLibraryClick={onLibraryClick}
          >
            <AddIcon />
          </AddFab>
        )}
        <div className={classes.spacer} />
        <Button
          color={domain === STATES.SEARCH ? 'secondary' : 'inherit'}
          className={classes.grow}
          onClick={onSearchClick}
        >
          <SearchIcon />
        </Button>
        {signedIn && (
          <Button
            edge='end'
            color={domain === STATES.PROFILE ? 'secondary' : 'inherit'}
            className={classes.grow}
            onClick={onProfileClick}
          >
            <PersonOutline />
          </Button>
        )}
      </Toolbar>
    </AppBar>
  )
}

BottomBar.propTypes = {
  className: PropTypes.string,
  domain: PropTypes.string,
  signedIn: PropTypes.bool,
  // Here are our button handlers
  onHomeClick: PropTypes.func.isRequired,
  onMapClick: PropTypes.func.isRequired,
  onSearchClick: PropTypes.func.isRequired,
  onProfileClick: PropTypes.func.isRequired,
  onCameraClick: PropTypes.func.isRequired,
  onLibraryClick: PropTypes.func.isRequired
}

export default BottomBar

export const BottomBarCosmosWrapper = (props) => {
  // const [someStateItem, setSomeStateItem] = useState()
  const classes = useWrapperStyles()
  return (
    <div className={classes.root}>
      <OfflineBar />
      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      <Paper className={classes.paper}>
        <Typography variant='h5'>
          This is a dummy component.
        </Typography>
      </Paper>
      <BottomBar {...props} />
    </div>
  )
}

const wrapperStyle = theme => ({
  root: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column'
  },
  paper: {
    flexGrow: 1
  }
})

const useWrapperStyles = makeStyles(wrapperStyle)
