import React, { useState } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Slider from '@material-ui/lab/Slider'
// import { Menu as MenuIcon } from '@material-ui/icons'
import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    color: 'white',
    zIndex: 10,
    display: 'flex',
    flexDirection: 'column',
    position: 'absolute',
    height: '50%',
    top: '45%',
    left: '0',
    transform: 'translateY(-50%)'
  },
  sliderLabel: {
    display: 'block',
    flexGrow: 1,
    textAlign: 'center',
    padding: theme.spacing(3, 0, 3, 0),
    textShadow: '0 0 5px black, 0 0 5px black'
  },
  vertical: {
    padding: '0px 31px!important',
    width: '4px!important'
  },
  rail: {
    width: `4px!important`,
    background: theme.palette.primary.dark,
    boxShadow: '0 0 10px 5px black'
  },
  track: {
    width: `4px!important`,
    background: theme.palette.primary.main
  },
  thumb: {
    // transform: 'translate(50%, -50%)',
    background: theme.palette.primary.main,
    border: `1px solid ${theme.palette.primary.dark}`,
    marginLeft: '-8px !important',
    width: 20,
    height: 20
  }
})
const useStyles = makeStyles(style)

const PhotoFadeSlider = ({ defaultValue, onChange }) => {
  const classes = useStyles()
  // we control the state internally so we get a smooth experience
  const [slider, setSlider] = useState(defaultValue)

  const onSliderMove = (event, value) => {
    setSlider(value)
    // send it back if we need to
    onChange(value)
  }

  return (
    <div className={classes.root}>
      <Typography className={classes.sliderLabel} color='inherit' variant='caption'>New</Typography>
      <Slider
        classes={{
          vertical: classes.vertical,
          rail: classes.rail,
          track: classes.track,
          thumb: classes.thumb
        }}
        value={slider}
        aria-labelledby='label'
        onChange={onSliderMove}
        orientation='vertical'
      />
      <Typography className={classes.sliderLabel} color='inherit' variant='caption'>Reference</Typography>
    </div>
  )
}

PhotoFadeSlider.propTypes = {
  defaultValue: PropTypes.number,
  onChange: PropTypes.func
}

PhotoFadeSlider.defaultProps = {
  defaultValue: 100,
  onChange: () => {}
}

export default PhotoFadeSlider
