/**
 * We put our action types at the top to keep them straight
 *
 * This is just a handy enumeration we use everywhere to make sure we're
 * all talking about the same ations
 */
export const Actions = {

  HOME_CHANGE_STATE: 'HOME_CHANGE_STATE',
  HOME_CHANGE_ACTIVE_SERIES: 'HOME_CHANGE_ACTIVE_SERIES',
  HOME_STATE_BACK: 'HOME_STATE_BACK',

  MAP_HISTORY_POP: 'MAP_HISTORY_POP',
  MAP_HISTORY_PUSH: 'MAP_HISTORY_PUSH',
  MAP_HISTORY_CLEAR: 'MAP_HISTORY_CLEAR',

  UPLOADER_START: 'UPLOADER_START',
  UPLOADER_COMPLETE: 'UPLOADER_COMPLETE',
  UPLOADER_UPDATE_PROGRESS: 'UPLOADER_UPDATE_PROGRESS',

  SETFLAG: 'SETFLAG'
}

/**
 * Now here are the actual action functions we will be dispatching
 * The actions have arguments related to what they do but they action they
 * dispatch consists only of a type and data3
 */
export default {
  setHomeState: (newDomain, params) => ({ type: Actions.HOME_CHANGE_STATE, data: { newDomain, params } }),
  homeStateBack: () => ({ type: Actions.HOME_STATE_BACK }),
  setActiveSeries: (seriesId, initialPhoto) => ({ type: Actions.HOME_CHANGE_ACTIVE_SERIES, data: { seriesId, initialPhoto } }),

  setFlag: (flagName, flagVal) => ({ type: Actions.SETFLAG, data: { flagName, flagVal } }),

  popMapHistory: () => ({ type: Actions.MAP_HISTORY_POP }),
  pushMapHistory: (viewportSave) => ({ type: Actions.MAP_HISTORY_PUSH, data: viewportSave }),
  clearMapHistory: () => ({ type: Actions.MAP_HISTORY_CLEAR }),

  queueUpload: (uploadObj) => ({ type: Actions.UPLOADER_START, data: uploadObj }),
  // startActiveUpload: () => ({ type: Actions.UPLOADER_START }),
  completeActiveUpload: () => ({ type: Actions.UPLOADER_COMPLETE }),
  updateUploadProgress: (newProgress) => ({ type: Actions.UPLOADER_UPDATE_PROGRESS, data: newProgress })

}
