import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import List from '@material-ui/core/List'
import classNames from 'classnames'
import Typography from '@material-ui/core/Typography'
import RawImage from '../PhotoSeries/views/subViews/RawImage'
import { makeStyles } from '@material-ui/core/styles'
import { formatDate } from '../../lib/datelib'

import { ACCESS } from '../../config'
import { AppContext } from '../../contexts'

const style = theme => ({
  root: {
    margin: 0,
    padding: theme.spacing(1)
  },
  chipContainer: {
    display: 'flex',
    width: '100%'
  },
  grow: { flexGrow: 1 },
  accessLabel: {
    padding: theme.spacing(0.5, 0.5)
  },
  accessPUBLIC: {
    color: 'white',
    background: ACCESS.PUBLIC.color[900]
  },
  accessPRIVATE: {
    color: 'white',
    background: ACCESS.PRIVATE.color[900]
  },
  accessOPEN: {
    color: 'white',
    background: ACCESS.OPEN.color[900]
  },
  img: {
    verticalAlign: 'top',
    objectFit: 'cover',
    height: '100%',
    width: '100%'
  },
  text: {
    height: '115px',
    margin: 0,
    padding: theme.spacing(0, 1),
    alignSelf: 'flex-start',
    display: 'flex',
    flexDirection: 'column'
  },
  titleText: {
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
    overflow: 'hidden'
  },
  dateContainer: {
    display: 'flex'
  },
  dateLabel: {
    fontWeight: 'bold',
    flex: '1 1 50px'
  },
  dateText: {

  },
  imgFiller: {
    flexGrow: 1,
    background: theme.palette.text.primary
  },
  imgContainer: {
    flex: '0 0 115px',
    alignSelf: 'flex-start',
    height: '100%',
    minHeight: 115,
    display: 'flex',
    flexDirection: 'column'
  }
})
const useStyles = makeStyles(style)

const SeriesListItem = ({ series, loading, onClick, children, showCreatedOn, showLastPhoto }) => {
  const classes = useStyles()
  if (loading || !series) return 'LOADING...'
  const { getPhotoUrl } = useContext(AppContext)

  let accessLabel = ACCESS.PRIVATE.label
  accessLabel = series.access ? ACCESS[series.access].label : ACCESS.PRIVATE.label

  return (
    <ListItem className={classes.root} button component='div' disableGutters>
      <div className={classes.imgContainer} onClick={onClick}>
        <div className={classes.imgFiller}>
          <RawImage photo={series.featured} size='thumb' getPhotoUrl={getPhotoUrl} className={classes.img} />
        </div>
        {series.access && (
          <Typography className={classNames(classes.accessLabel, classes[`access${series.access}`])}
            variant='caption'
            component='div'
          >
            {`${series.photoCount || 0} Photos`} <em>({accessLabel})</em>
          </Typography>
        )}
      </div>
      <ListItemText className={classes.text} disableTypography onClick={onClick}>
        <Typography variant='body1' component='div' className={classes.titleText}>
          {series.name || loading}
        </Typography>
        <Typography variant='body2' component='div'>
          <strong>Created by:</strong> {series.createdUser.username}
        </Typography>
        <div className={classes.grow} />
        {showCreatedOn && <div className={classes.dateContainer}>
          <Typography variant='caption' className={classes.dateLabel}>Created:</Typography>
          <Typography variant='caption' className={classes.dateText}>{formatDate(series.createdOn, 'lll')}</Typography>
        </div>}
        {showLastPhoto && <div className={classes.dateContainer}>
          <Typography variant='caption' className={classes.dateLabel}>Last Photo:</Typography>
          <Typography variant='caption' className={classes.dateText}>{formatDate(series.latest.takenOn, 'lll')}</Typography>
        </div>}
      </ListItemText>
      {/*  <TinyChip label='Watching' color='secondary' /> */}
      {children}
    </ListItem>
  )
}

SeriesListItem.propTypes = {
  series: PropTypes.object,
  loading: PropTypes.bool,
  showCreatedOn: PropTypes.bool,
  showLastPhoto: PropTypes.bool,
  onClick: PropTypes.func,
  children: PropTypes.array
}

export default SeriesListItem

// const useCosmosStyles = makeStyles(theme => ({
//   root: {

//   }
// }))

export const SeriesListItemCosmos = (props) => {
  // const classes = useCosmosStyles()
  return (
    <List disablePadding dense>
      <SeriesListItem {...props} />
    </List>
  )
}
