import React from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import DialogContent from '@material-ui/core/DialogContent'
import Dialog from '@material-ui/core/Dialog'
import IconButton from '@material-ui/core/IconButton'
import DialogTitle from '@material-ui/core/DialogTitle'

import CloseIcon from '@material-ui/icons/Close'
import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {

  },
  body: {
    overflow: 'hidden',
    whiteSpace: 'pre-line',
    paddingBottom: theme.spacing(1)
  },
  title: {
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
    alignSelf: 'flex-start',
    width: '100%',
    margin: 0,
    display: 'flex',
    padding: theme.spacing(2)
  },
  dlgTitle: {
    flexGrow: 1
  },
  closeButton: {
    padding: 0,
    alignSelf: 'flex-start'
    // color: theme.palette.grey[500]
  }
})
const useStyles = makeStyles(style)

const BigText = ({ open, onClose, title, body }) => {
  const classes = useStyles()
  return (
    <Dialog
      scroll='paper'
      open={open}
      fullWidth
      maxWidth='xl'
      onClose={onClose}
      aria-labelledby='simple-dialog-title'
    >
      <DialogTitle className={classes.title} disableTypography>
        <Typography className={classes.dlgTitle}>{title}</Typography>
        <IconButton color='inherit' className={classes.closeButton} onClick={onClose}>
          <CloseIcon color='inherit' />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        <Typography variant='body2' className={classes.body}>{body}</Typography>
      </DialogContent>
    </Dialog>
  )
}

BigText.propTypes = {
  open: PropTypes.bool,
  onClose: PropTypes.func,
  title: PropTypes.string,
  body: PropTypes.string
}

export default BigText
