import React, { useRef, useEffect } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import actions from '../redux/actions'
import UploadControl from '../components/UploadControl'
import s3Upload from '../lib/s3upload'

const UploadConntect = ({
  activeUpload,
  completeActiveUpload,
  updateUploadProgress,
  ...props
}) => {
  const uploadingRef = useRef()
  useEffect(() => {
    uploadingRef.current = uploadingRef.current && !!activeUpload

    if (activeUpload && !uploadingRef.current) {
      s3Upload(activeUpload, updateUploadProgress, completeActiveUpload)
    }
  }, [Boolean(activeUpload)])

  if (!activeUpload) return null
  return <UploadControl text={'Uploading thing'} progress={activeUpload.progress || 0} {...props} />
}
UploadConntect.propTypes = {
  // All from redux
  activeUpload: PropTypes.object,
  completeActiveUpload: PropTypes.func.isRequired,
  updateUploadProgress: PropTypes.func.isRequired
}

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { activeUpload } = state.uploader
  return { activeUpload }
}

const mapDispatchToProps = dispatch => ({
  completeActiveUpload: () => {
    dispatch(actions.completeActiveUpload())
  },
  updateUploadProgress: (newProgress) => {
    dispatch(actions.updateUploadProgress(newProgress))
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(UploadConntect)
