import React from 'react'
import PropTypes from 'prop-types'
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary'
import { makeStyles } from '@material-ui/core/styles'
import useFileInput from '../../../../lib/useFileInput'
import { Button } from '@material-ui/core'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    textAlign: 'center',
    color: theme.palette.grey[600],
    border: `20px solid ${theme.palette.grey[400]}`
  },
  icon: {
    color: theme.palette.grey[600],
    margin: 'auto',
    flex: '1 1 50%',
    height: '70%',
    width: '70%'
  },
  button: {
    margin: theme.spacing(2)
  }
}))

const LibraryFilePicker = ({ onLibraryAdd, onCancel }) => {
  const classes = useStyles()
  const { fileClickEl, forceClick } = useFileInput(onLibraryAdd)
  return (
    <div className={classes.root}>
      {fileClickEl}
      <PhotoLibraryIcon className={classes.icon}color='inherit' onClick={forceClick} />
      <Button variant='contained' className={classes.button} color='primary' onClick={forceClick}>
        Import from Library
      </Button>
      <Button variant='outlined' className={classes.button} color='secondary' onClick={onCancel}>
        Cancel
      </Button>
    </div>
  )
}

LibraryFilePicker.propTypes = {
  onLibraryAdd: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired
}

export default LibraryFilePicker
