import React, { useRef, useEffect } from 'react'
import PropTypes from 'prop-types'
import List from '@material-ui/core/List'
import Typography from '@material-ui/core/Typography'

import SearchContextItem from './SearchContextItem'
import SearchEnder from './SearchEnder'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
import SeriesListItem from '../misc/SeriesListItem'
import { debounce } from '../../lib/util'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    flexGrow: 1,
    display: 'flex',
    flexDirection: 'column',
    overflow: 'hidden'
  },
  ctxItem: {
    flexGrow: 0
  },
  list: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    position: 'relative',
    overflowX: 'hidden',
    overflowY: 'scroll'
  },
  placeholderText: {
    textAlign: 'center',
    padding: theme.spacing(3, 1),
    color: theme.palette.grey[400]
  },
  listSection: {
    backgroundColor: 'inherit'
  },
  ul: {
    backgroundColor: 'inherit',
    padding: 0
  }
}))

const SearchResults = ({ variant, loading, seriesList, contextChoice, onCancel, onResultClick, fetchMore }) => {
  const classes = useStyles()
  const listRef = useRef()
  const enderRef = useRef()

  const handleScroll = debounce(() => {
    if (!loading && fetchMore && enderRef.current && listRef.current) {
      const { offsetTop } = enderRef.current
      const { scrollTop, offsetHeight } = listRef.current
      const scrollBottom = offsetHeight + scrollTop
      if (offsetTop < scrollBottom * 2) {
        console.log('SCROLL', { offsetHeight, scrollTop, offsetTop })
        fetchMore()
      }
    }
  }, 500)

  useEffect(handleScroll, [seriesList])

  let content = null
  if (loading) {
    content = (
      <Typography className={classes.placeholderText} variant='h3'>Searching...</Typography>
    )
  }
  else if (seriesList && seriesList.length > 0) {
    content = seriesList.map((series, idx) => (
      <SeriesListItem
        key={`series-${idx}`}
        series={series}
        onClick={() => onResultClick(series.id)}
        showCreatedOn
        showLastPhoto
      />
    ))
    content.push(
      <div key='ender' ref={enderRef}>
        <SearchEnder text={fetchMore ? 'Loading...' : 'No More Results'} />
      </div>
    )
  }
  else {
    content = (
      <Typography className={classes.placeholderText} variant='h4'>No series found</Typography>
    )
  }

  return (
    <div className={classes.root}>
      <SearchContextItem className={classes.ctxItem} {...contextChoice} variant={variant} onCancel={onCancel} />
      <List className={classes.list} subheader={<li />} onScroll={handleScroll} innerRef={listRef}>
        {content}
      </List>
    </div>
  )
}

SearchResults.propTypes = {
  variant: PropTypes.oneOf(['tag', 'location', 'user']),
  seriesList: PropTypes.array.isRequired,
  loading: PropTypes.bool,
  contextChoice: PropTypes.object.isRequired,
  onResultClick: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  fetchMore: PropTypes.func
}

export default SearchResults
