import React from 'react'
import PropTypes from 'prop-types'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'

import CloseIcon from '@material-ui/icons/Close'

import { makeStyles } from '@material-ui/core/styles'
import XYTagSelect from './XYTagSelect'
import style from './XYTagChooser.style'

const useStyles = makeStyles(style)

const XYTagChooser = ({ coords, open, seriesConfig, onNewTag, onClose }) => {
  const classes = useStyles()
  const xyTags = seriesConfig.tags.filter(t => t.allowXY)
  if (!coords) return null
  return (
    <Dialog
      open={open}
      scroll='paper'
      fullScreen
      classes={{ paperScrollPaper: classes.root }}
      onClose={() => onClose()}
      aria-labelledby='simple-dialog-title'
    >
      <DialogTitle id='simple-dialog-title' className={classes.header}>
      New Tag:
        <IconButton className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <div className={classes.dlgBody}>
        {xyTags.length > 1 && (
          <Typography variant='caption'>What kind of tag would you like to create?</Typography>
        )}

        {xyTags.map((tagType, idx) => (
          <div className={classes.listItem} key={`tagSelect${idx}`}>
            <XYTagSelect tagConfig={tagType} onChange={onNewTag(coords, tagType)} />
          </div>
        ))}
      </div>
    </Dialog>
  )
}

XYTagChooser.propTypes = {
  seriesConfig: PropTypes.object.isRequired,
  open: PropTypes.bool.isRequired,
  coords: PropTypes.object,
  onClose: PropTypes.func.isRequired,
  onNewTag: PropTypes.func.isRequired
}

export default XYTagChooser
