import SeriesConfig from './SeriesConfig'

class Tags {
  constructor (seriesConfig, tags) {
    this.tags = tags
    this.seriesConfig = new SeriesConfig(seriesConfig)
  }

  findTagById = (typeId, tagId) => {
    return this.seriesConfig.getTagType(typeId).values
      .find(tt => tt.id === tagId)
  }

  // Filter the list of tags to those that are XY tags
  getXYTags = () => {
    const xyTagIds = this.seriesConfig.getXYTagTypes().map(tt => tt.id)
    return this.tags.filter(tag => xyTagIds.indexOf(tag.typeId) > -1 && tag.coords && tag.coords.x)
  }

  getNonXYTags = () => {
    const NonXYTagIds = this.seriesConfig.getNonXYTagTypes().map(tt => tt.id)
    return this.tags.filter(tag => NonXYTagIds.indexOf(tag.typeId) > -1)
  }

  createNewTagObj = (label, typeId) => (
    { label, typeId }
  )

  createExistingTagObj = (id, typeId) => ({
    ...this.findTagById(typeId, id),
    typeId
  })

  createNewXYTagObj = (label, typeId, coords) => ({
    label,
    typeId,
    coords
  })

  createExistingXYTagObj = (id, typeId, coords) => ({
    ...this.findTagById(typeId, id),
    typeId,
    coords
  })

  // Get tags in a way that's easy to render them
  // We augment these tags with relative positions
  // and a group number for coloring.
  getRelXYTags = (multiplier) => this.getXYTags().map(tag => ({
    ...tag,
    group: this.seriesConfig.getTagTypeIndex(tag.typeId),
    relCoords: {
      x: tag.coords.x * multiplier,
      y: tag.coords.y * multiplier
    }
  }))
}

export default Tags
