import React, { useState, useEffect, useRef } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import Badge from '@material-ui/core/Badge'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import CssBaseline from '@material-ui/core/CssBaseline'
import GridListTile from '@material-ui/core/GridListTile'
import GridListTileBar from '@material-ui/core/GridListTileBar'
import MobileStepper from '@material-ui/core/MobileStepper'
import Button from '@material-ui/core/Button'

import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import LabelIcon from '@material-ui/icons/Label'
import PauseIcon from '@material-ui/icons/PauseCircleFilled'
import PlayIcon from '@material-ui/icons/PlayCircleFilled'
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight'

import { makeStyles, useTheme } from '@material-ui/core/styles'

import Photo from './subViews/Photo'
import { formatDate } from '../../../lib/datelib'
import useComponentSize from '../../../lib/useComponentSize'

import style from './Slideshow.style'

const useStyles = makeStyles(style)

const Slideshow = ({ onCancel, photos, transition }) => {
  const classes = useStyles()
  const theme = useTheme()
  const { activeStep, lastStep, handleForward, handleBack, paused, pause, play } = useStepper(photos.length, transition)
  const [showXYTags, setShowXYTags] = useState(false)

  const refImgWrap = useRef(null)
  const { size: { height, width } } = useComponentSize(refImgWrap)

  return (
    <div className={classes.root}>
      <CssBaseline />
      <div ref={refImgWrap} className={classes.slideContainer}>
        {photos.map((slide, idx) => (
          <GridListTile
            key={`slide-${idx}`}
            className={classNames(
              classes.slide,
              activeStep === idx && classes.active,
              lastStep === idx && classes.last
            )}
            onClick={() => setShowXYTags(!showXYTags)}
            style={{
              height,
              width,
              transition: theme.transitions.create(['opacity'], { duration: 1 / 4 * transition })
            }}
          >
            <Photo photo={slide} className={classes.photo} showTags={showXYTags} />
            <Typography className={classes.labelBadge} >
            Tags: {showXYTags ? 'On ' : 'Off '}
              <Badge color='secondary' badgeContent={photos[activeStep].tags.filter(tag => tag.coords).length}>
                <LabelIcon color='inherit' />
              </Badge>
            </Typography>

            <GridListTileBar
              className={classes.titleBar}
              titlePosition='top'
              title={(
                <Typography color='inherit' variant='caption'>
                  <IconButton className={classes.iconButton} onClick={(e) => onCancel()}>
                    <ArrowBackIcon />
                  </IconButton>
                  {formatDate(slide.takenOn)}
                </Typography>
              )}
              subtitle={<em>{paused ? 'Paused' : '    '}</em>}
              actionIcon={paused ? (
                <IconButton className={classes.iconButton} onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  play()
                }}>
                  <PlayIcon />
                </IconButton>
              ) : (
                <IconButton className={classes.iconButton} onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  pause()
                }}>
                  <PauseIcon />
                </IconButton>
              )}
            />
          </GridListTile>
        ))}
        <IconButton size='small' className={classes.manualBack} onClick={handleBack} color='inherit'>
          {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
        </IconButton>
        <IconButton size='small' className={classes.manualForward} onClick={handleForward} color='inherit'>
          {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
        </IconButton>
      </div>

      <MobileStepper
        steps={photos.length}
        position='static'
        activeStep={activeStep}
        classes={{
          root: classes.mobileStepper,
          dotActive: classes.dotActive
        }}
        nextButton={
          <Button color='inherit' size='small' onClick={handleForward} disabled={activeStep === photos.length - 1}>
            Next
            {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
          </Button>
        }
        backButton={
          <Button color='inherit' size='small' onClick={handleBack} disabled={activeStep === 0}>
            {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            Back
          </Button>
        }
      />
    </div>
  )
}

Slideshow.propTypes = {
  // We might want to go full screen
  isFullScreen: PropTypes.bool,
  // This is just the basic shape of the photo object
  photos: PropTypes.array,
  transition: PropTypes.number,
  onCancel: PropTypes.func.isRequired
}

Slideshow.defaultProps = {
  transition: 5000,
  isFullScreen: false,
  slides: []
}

export default Slideshow

/**
 * Custom React Hook for all our timer handling
 */
const useStepper = (num, interval) => {
  const [activeStep, setActiveStep] = useState(0)
  const [paused, setPaused] = useState(false)
  const [lastStep, setLastStep] = useState(0)
  const timerRef = useRef()

  const handleForward = () => {
    setLastStep(activeStep)
    if (activeStep + 1 >= num) setActiveStep(0)
    else setActiveStep(activeStep + 1)
    if (!paused) {
      destroyTimer()
      createTimer()
    }
  }
  const handleBack = () => {
    setLastStep(activeStep)
    if (activeStep - 1 < 0) setActiveStep(num - 1)
    else setActiveStep(activeStep - 1)
    if (!paused) {
      destroyTimer()
      createTimer()
    }
  }

  const pause = () => {
    destroyTimer()
    setPaused(true)
  }
  const play = () => {
    setPaused(false)
    destroyTimer()
    createTimer()
  }

  const createTimer = () => {
    timerRef.current = window.setInterval(
      // We need to use the alternate notation for the useState function
      // because setInterval will only have access to the activeStep it was
      // initialized with
      () => setActiveStep(oldStep => {
        setLastStep(oldStep)
        return (oldStep + 1 >= num) ? 0 : oldStep + 1
      })
      , interval
    )
  }
  const destroyTimer = () => {
    if (timerRef.current) window.clearInterval(timerRef.current)
  }

  useEffect(() => {
    createTimer()
    return () => {
      destroyTimer()
    }
  }, []) // Pass in empty array to run useEffect only on mount.

  return { activeStep, lastStep, handleForward, handleBack, setActiveStep, paused, pause, play }
}
