import { useCallback, useState, useLayoutEffect } from 'react'
import { debounce } from './util'
import { isIos, isPhone } from './sensors'

const metaViewports = {
  sm: 'width=device-width, initial-scale=0.5, maximum-scale=0.5, user-scalable=0, shrink-to-fit=no',
  lg: 'width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=0, shrink-to-fit=no'
}

const getState = (state) => {
  const { orientation, innerWidth, innerHeight, screen: { width, height } } = window
  const wOrient = [90, 270].indexOf(orientation) !== 0 ? 'portrait' : 'landscape'
  const bIsIos = isIos()
  const bIsPhone = isPhone()
  const windowSize = { width: innerWidth, height: innerHeight }
  const screenSize = { width, height }

  // VirtualKeyboards are the worst because they don't tell you when they're open.
  // as a result we put the lockheight attribute on inputs so that the window height doesn't change
  if (state && window.document.activeElement.tagName.toLowerCase() === 'input') {
    console.log('preserving height')
    windowSize.height = state.windowSize.height
  }
  else {
    const minSize = Math.min(windowSize.width, windowSize.height)
    const viewport = window.document.querySelector('meta[name=viewport]')
    const contentDict = viewport.getAttribute('content').split(', ').reduce((acc, el) => {
      const elsplit = el.split('=')
      return { ...acc, [elsplit[0]]: elsplit[1] }
    }, {})
    if (minSize < 350 && contentDict['initial-scale'] === '1.0') {
      console.log('ZOOM Making it Small')
      viewport.setAttribute('content', metaViewports.sm)
    }
    else if (minSize >= 350 && contentDict['initial-scale'] === '0.5') {
      console.log('ZOOM Making it Large')
      viewport.setAttribute('content', metaViewports.lg)
    }
  }

  const returnState = {
    isPhone: bIsPhone,
    isIos: bIsIos,
    orientation: wOrient,
    windowSize,
    screenSize
    // This failed. the only way to handle this is to set it explicitly
    // isVKeyBdOn: bIsPhone && windowSize.height < screenSize.height
  }
  return returnState
}

const useResponsive = (mediaQueries) => {
  const [state, setState] = useState(getState())

  const handleResize = (e) => {
    if (e) {
      e.preventDefault()
      e.stopPropagation()
    }
    const newState = getState(state)
    console.log('handleResize', newState)
    setState(newState)
  }

  // const handleFlip = (e) => {
  //   e.preventDefault()
  //   e.stopPropagation()
  //   const newState = getState()
  //   console.log('handleFlip', newState)
  //   setState(newState)
  // }

  const handleResizeDebounced = useCallback(debounce(handleResize, 500), [])
  // const handleFlipDebounced = useCallback(debounce(handleFlip, 500), [])

  useLayoutEffect(() => {
    handleResizeDebounced()
    window.addEventListener('resize', handleResizeDebounced)
    // window.addEventListener('orientationchange', handleFlipDebounced)

    return () => {
      window.removeEventListener('resize', handleResizeDebounced)
      // window.removeEventListener('orientationchange', handleFlipDebounced)
    }
  },
  [])

  return state
}

export default useResponsive
