import React, { useState, useContext } from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'

import Toolbar from '@material-ui/core/Toolbar'
import AppBar from '@material-ui/core/AppBar'
import IconButton from '@material-ui/core/IconButton'
import AppLogoSimple from '../icons/AppLogoSimple'
import FeatureDisabled from './misc/FeatureDisabled'
import { AppContext } from '../contexts'

import QRCodeIcon from '../icons/QRCode'
import SideDrawer from './SideDrawer'

// import MenuIcon from '@material-ui/core/Menu'
import { makeStyles } from '@material-ui/core/styles'
import { ThemeProvider } from '@material-ui/styles'
import themes from '../themes'
import { Typography } from '@material-ui/core'

const style = theme => ({
  root: {
    display: 'flex',
    alignItems: 'center',
    width: '100%',
    padding: theme.spacing(0, 1),
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
    borderBottom: `2px solid ${theme.palette.primary}`,
    background: `linear-gradient(90deg, ${theme.palette.secondary.main} 0%, ${theme.palette.primary.main} 100%)`
  },
  searchIcon: {
    opacity: 0.3,
    marginRight: theme.spacing(1)
  },
  transparent: {
    backgroundColor: 'transparent',
    background: 'none',
    color: theme.palette.text.primary
    // borderBottom: `2px transparent`
  },
  iOsIcon: {
    color: 'rgba(0, 0, 0, 0.26)'
  },
  logo: {
    padding: theme.spacing(0, 0),
    height: 40,
    width: 40
  },
  transparentLogo: {
    background: `linear-gradient(90deg, ${theme.palette.secondary.main} 0%, ${theme.palette.primary.main} 100%)`,
    padding: theme.spacing(0.5, 0.5)
  },
  transparentLogoIcon: {
    color: 'white',
    height: 35,
    width: 35
  },
  children: {
    flexGrow: 1,
    padding: theme.spacing(0, 1)
  },
  qrButton: {
    padding: theme.spacing(0, 0)
  },
  qrButtonTransparent: {
    background: 'white',
    padding: theme.spacing(0.5, 0.5)
  }
})
const useStyles = makeStyles(style)

const TopBar = ({ className, children, isTransparent, menuItems, hideLogo }) => {
  const classes = useStyles()
  const [appleDlg, setAppleDlg] = useState(false)
  const [drawerOpen, setDrawerOpen] = useState(false)
  const { onQRClick, isIos } = useContext(AppContext)

  return (
    <React.Fragment>
      <AppBar position='static'>
        <Toolbar className={classNames(classes.root, className, isTransparent ? classes.transparent : null)} disableGutters>
          {!hideLogo && (
            <IconButton
              className={isTransparent ? classes.transparentLogo : classes.logo}
              color='inherit'
              onClick={() => setDrawerOpen(true)}
            >
              <AppLogoSimple className={isTransparent ? classes.transparentLogoIcon : classes.logo} />
            </IconButton>
          )}
          <div className={classes.children}>
            {children}
          </div>
          <IconButton
            color='inherit'
            className={classNames(
              isIos ? classes.iOsIcon : null,
              isTransparent ? classes.qrButtonTransparent : classes.qrButton
            )}
            aria-label='Menu'
            onClick={isIos ? () => setAppleDlg(true) : onQRClick}
          >
            <QRCodeIcon color='inherit' />
          </IconButton>
        </Toolbar>
      </AppBar>
      <SideDrawer
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
        onOpen={() => setDrawerOpen(true)}
        onDisabledClick={() => setAppleDlg(true)}
      />
      {isIos && (
        <FeatureDisabled
          open={appleDlg}
          title='iOS Feature Disabled'
          message={<Typography variant='body2' component='div'>
            <p>This feature cannot function due to a bug in iOS 12 that prevents <strong>direct
            camera access from progressive web apps</strong>.</p>
            <p>There is good evidence iOS 13 has things sorted. ETA from Apple is mid-September 2019.</p>
          </Typography>}
          onClose={() => setAppleDlg(false)}
        />
      )}
    </React.Fragment>
  )
}

TopBar.propTypes = {
  className: PropTypes.string,
  children: PropTypes.any,
  isTransparent: PropTypes.bool,
  hideLogo: PropTypes.bool,
  menuItems: PropTypes.arrayOf(PropTypes.shape({
    callback: PropTypes.func,
    text: PropTypes.string,
    icon: PropTypes.any,
    color: PropTypes.string
  }))
}

export default (props) => <ThemeProvider theme={themes.topbar}><TopBar {...props} /></ThemeProvider>
