import React from 'react'
import PropTypes from 'prop-types'

import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import TextField from '@material-ui/core/TextField'
import MenuItem from '@material-ui/core/MenuItem'

import { makeStyles } from '@material-ui/core/styles'
import Select from 'react-select'
import CreatableSelect from 'react-select/lib/Creatable'
import style from './XYTagSelect.style'

const useStyles = makeStyles(style)

const XYTagSelect = ({ tagConfig, onChange }) => {
  const classes = useStyles()
  const options = tagConfig.values.map(tc => ({ label: tc.label, value: tc.id }))
  console.log('XYTagSelect', tagConfig)
  return (
    <div className={classes.root}>
      {!tagConfig.allowAdditions && (
        <Select
          options={options}
          classes={classes}
          components={{
            Control,
            Menu,
            NoOptionsMessage,
            Option,
            Placeholder,
            ValueContainer
          }}
          value={null}
          // onBlur={e => e.preventDefault()}
          blurInputOnSelect
          isClearable={false}
          onChange={onChange}
          placeholder={tagConfig.label}
          isSearchable
        />
      ) }
      { tagConfig.allowAdditions && (
        <CreatableSelect
          options={options}
          classes={classes}
          components={{
            Control,
            Menu,
            NoOptionsMessage,
            Option,
            Placeholder,
            ValueContainer
          }}
          value={null}
          isClearable={false}
          // onBlur={e => e.preventDefault()}
          blurInputOnSelect
          onChange={onChange}
          placeholder={tagConfig.label}
          isSearchable
        />
      )}
    </div>
  )
}
XYTagSelect.propTypes = {
  tagConfig: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired
}

export default XYTagSelect

function Control (props) {
  return (
    <TextField
      fullWidth
      InputProps={{
        inputComponent,
        inputProps: {
          className: props.selectProps.classes.input,
          inputRef: props.innerRef,
          children: props.children,
          ...props.innerProps
        }
      }}
      {...props.selectProps.textFieldProps}
    />
  )
}

const NoOptionsMessage = ({ innerProps, selectProps, children }) => (
  <Typography
    color='textSecondary'
    className={selectProps.classes.noOptionsMessage}
    {...innerProps}
  >
    {children}
  </Typography>
)

const Menu = ({ selectProps, innerProps, children }) => (
  <Paper square className={selectProps.classes.paper} {...innerProps}>
    {children}
  </Paper>
)

const inputComponent = ({ inputRef, ...props }) => <div ref={inputRef} {...props} />

const Placeholder = (props) => (
  <Typography
    color='textSecondary'
    className={props.selectProps.classes.placeholder}
    {...props.innerProps}
  >
    {props.children}
  </Typography>
)

const Option = ({ innerRef, isFocused, isSelected, innerProps, children }) => (
  <MenuItem
    buttonRef={innerRef}
    selected={isFocused}
    component='div'
    style={{ fontWeight: isSelected ? 500 : 400 }}
    {...innerProps}
  >
    {children}
  </MenuItem>
)

const ValueContainer = ({ selectProps, children }) => (
  <div className={selectProps.classes.valueContainer}>{children}</div>
)
ValueContainer.propTypes = {
  selectProps: PropTypes.object.isRequired,
  children: PropTypes.any
}
