import SeriesConfig from './SeriesConfig'
import uuidv4 from 'uuid/v4'

class Series {
  constructor (seriesConfig, series) {
    this.config = new SeriesConfig(seriesConfig)
    if (series) this.series = series
    else {
      this.series = this.newSeries()
    }
  }

  setMeta = () => {
    this.meta = JSON.stringify(this.config.getSeriesMeta())
  }

  newSeries = () => ({
    id: `__LOCAL_SERIES::${uuidv4()}`,
    meta: JSON.stringify(this.config.getSeriesMeta()),
    photos: []
  })
}

export default Series
