import React, { useRef, useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { home } from '../config'
import actions from '../redux/actions'
import { connect } from 'react-redux'
import { getMyFeed } from './queries'
import MyFeed from '../components/MyFeed/MyFeed'
import useOnline from '../lib/useOnline'

const POLL_INTERVAL = 20 // (seconds)
const FEED_LIMIT = 5 // (items to load)

const FeedConnect = ({
  data, loading, fetchMore, fetchNewest,
  loadingNewest,
  loadingMore,
  jumpToSeries,
  jumpToMap,
  jumpToUser
}) => {
  const timerRef = useRef()
  const { online } = useOnline()
  const [lastCheck, setLastCheck] = useState(null)
  timerRef.current = lastCheck

  // Initiate the polling process. We simply update the timestamp every N seconds
  useEffect(() => {
    function ping () {
      if (!loading && online) fetchNewest()
      if (timerRef.current) setLastCheck(String(Date.now() - POLL_INTERVAL * 1000))
    }
    if (!timerRef.current) timerRef.current = setInterval(ping, POLL_INTERVAL * 1000)
    return () => {
      if (timerRef.current) {
        console.log('clear poll timer')
        clearInterval(timerRef.current)
        timerRef.current = null
      }
    }
  }, [])

  const events = data.myFeed ? data.myFeed.feed || [] : []
  const window = data.myFeed ? data.myFeed.window.map(v => parseInt(v, 10)) || [null, null] : [null, null]

  return (
    <MyFeed {...{ events, loading, window, fetchMore, fetchNewest, jumpToSeries }}
      onSeriesClick={() => {}}
      onImageClick={jumpToSeries}
      onProfileClick={jumpToUser}
      onMapClick={jumpToMap}
    />
  )
}

FeedConnect.propTypes = {
  data: PropTypes.object, // from graphql
  loading: PropTypes.bool, // from graphql
  loadingNewest: PropTypes.bool, // from graphql
  loadingMore: PropTypes.bool, // from graphql
  fetchMore: PropTypes.func,
  fetchNewest: PropTypes.func,
  jumpToSeries: PropTypes.func,
  jumpToMap: PropTypes.func,
  jumpToUser: PropTypes.func
}

const withFeedQuery = graphql(getMyFeed, {
  props: ({ data }) => {
    if (data.error) console.error(data.error)

    // TODO: Not sure this will propagate correctly. Test it.
    let loadingNewest = false
    let loadingMore = false

    // console.log('withFeedQuery', data)

    // One Thunk function designed for getting everything since the last check
    const fetchNewest = () => {
      loadingNewest = true
      console.log('fetchNewest', data)
      if (data.myFeed) {
        data.fetchMore({
          variables: { ...data.variables, start: data.myFeed.window[1] },
          // How do we mush the new query with the old one
          updateQuery: (prev, { fetchMoreResult }) => {
            loadingNewest = false
            return {
              myFeed: {
                ...fetchMoreResult.myFeed,
                // put the newest stuff first
                feed: [ ...fetchMoreResult.myFeed.feed, ...prev.myFeed.feed ],
                window: [prev.myFeed.window[0], fetchMoreResult.myFeed.window[1]]
              }
            }
          }
        })
      }
    }

    // A second Thunk function designed to get more elements in the feed history
    const fetchMore = () => {
      loadingMore = true
      console.log('fetchMore', data)
      if (data.myFeed) {
        data.fetchMore({
          variables: { ...data.variables, end: data.myFeed.window[0] },
          // How do we mush the new query with the old one
          updateQuery: (prev, { fetchMoreResult }) => {
            loadingMore = false
            return {
              myFeed: {
                ...fetchMoreResult.myFeed,
                feed: [ ...prev.myFeed.feed, ...fetchMoreResult.myFeed.feed ],
                window: [fetchMoreResult.myFeed.window[0], prev.myFeed.window[1]]
              }
            }
          }
        })
      }
    }

    return {
      data,
      loadingNewest,
      loadingMore,
      fetchNewest, // This is the poll we do every n seconds
      fetchMore, // This is when we scroll to the bottom of the list
      loading: data.loading
    }
  },
  // skip: ({ activeSeries, isNewSeries }) => !activeSeries || isNewSeries,
  options: ({ activeSeries }) => ({
    variables: {
      limit: FEED_LIMIT
    }
  })
})

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  return { }
}

// We're using graphql() HOC because it's convenient but it doesn't need to render anything
// const FeedQueryConnect = withFeedQuery(() => null)
const mapDispatchToProps = dispatch => ({
  jumpToSeries: (seriesId, photoId) => {
    dispatch(actions.setHomeState(home.AppStateTypes.SERIES, { activeSeries: seriesId, initialPhoto: photoId }))
  },
  jumpToUser: (username) => {
    dispatch(actions.setHomeState(home.AppStateTypes.SEARCH, { search: { tab: home.SEARCH_TABS.USER, text: username } }))
  },
  jumpToMap: (seriesId, photoId) => {
    dispatch(actions.pushMapHistory({
      mode: home.MapModeTypes.FREE
    }))
    dispatch(actions.setHomeState(home.AppStateTypes.MAP, { activeSeries: seriesId, initialPhoto: photoId }))
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(
  withFeedQuery(FeedConnect)
)
