import React from 'react'
import PropTypes from 'prop-types'
import Fab from '@material-ui/core/Fab'
import MyLocationIcon from '@material-ui/icons/MyLocation'

import { makeStyles, useTheme } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    border: '1px solid',
    background: theme.palette.grey[100],
    color: 'black',
    width: 40,
    height: 40,
    zIndex: 10,
    position: 'absolute'
    // margin: theme.spacing(0.5),
    // padding: theme.spacing(0.5),
    // border: '2px solid',
    // borderRadius: theme.spacing(1),
    // background: theme.palette.common.white,
    // fontSize: 20,
    // position: 'absolute',
    // bottom: theme.spacing(10),
    // right: theme.spacing(2)
  }
})

const useStyles = makeStyles(style)

const MyLocationButton = ({ onClick, controlPadding, disabled }) => {
  const classes = useStyles()
  const theme = useTheme()
  return (
    <Fab
      disabled={disabled}
      style={{
        top: 56 + (controlPadding.top ? controlPadding.top : theme.spacing(2)),
        right: controlPadding.right ? controlPadding.right : theme.spacing(2)
      }}
      aria-label='myLocation'
      className={classes.root}
      onClick={onClick}
    >
      <MyLocationIcon />
    </Fab>
  )
}

MyLocationButton.propTypes = {
  controlPadding: PropTypes.object,
  onClick: PropTypes.func.isRequired,
  disabled: PropTypes.bool
}

export default MyLocationButton
