import numeral from 'numeral'

export const isNumeric = num => {
  try {
    return !isNaN(parseFloat(num)) && isFinite(num)
  }
  catch (e) {
    return false
  }
}
/**
 * This is a correction to strip === signs off of base64-encoded strings
 * @param {} x
 */
export const urlizeb64 = x => x.replace('+', '-').replace('/', '_').replace(/=+$/, '')

/**
 * We need numbers displayed consistently. This is how we do it.
 * @param {*} num
 * @param {*} precision
 */
export function formatNum (num, precision) {
  try {
    const precstr = precision > 0 ? '.' + Array(precision + 1).join('0') : ''
    const newprec = isNumeric(num) ? numeral(num).format(`0,0${precstr}`) : num
    return newprec.replace(/(\.(0{0,1}[1-9]*))0+$/, '$1')
  }
  catch (e) {
    return num
  }
}

/**
 * Magic function to clear mouse selection. Use sparingly.
 */
export function clearSelection () {
  if (document.selection && document.selection.empty) {
    document.selection.empty()
  }
  else if (window.getSelection) {
    var sel = window.getSelection()
    sel.removeAllRanges()
  }
}

export function isObjectWithKeys (obj) {
  return ((typeof obj === 'object') && (obj !== null) && Object.keys(obj).length > 0)
}

/**
 * Dom node parsing is weird because it may return an array or a single element with no way to know
 * which is which.
 */
export function makeDomArray (domnode) {
  if (!domnode) return []
  const keys = Object.keys(domnode)
  // This is not the pattern we expect
  if (keys.length > 1 || keys.length === 0) throw Error('Expected nester domnode structure.')
  if (!domnode) return []
  if (!domnode[keys[0]]) return []
  return Array.isArray(domnode[keys[0]]) ? domnode[keys[0]] : [domnode[keys[0]]]
}

export function capitalizeFirstLetter (string) {
  return string.charAt(0).toUpperCase() + string.slice(1)
}

/**
 * Good for comparing two purely numeric arrays
 * @param {*} arr1
 * @param {*} arr2
 */
export function numericArrayCompare (arr1, arr2) {
  if (!Array.isArray(arr1) || !Array.isArray(arr2)) return false
  if (arr1.length !== arr2.length) return false

  return !arr1.some((el, idx) => el !== arr2[idx])
}

/**
 * We need a function to add the padding '=' signs back before we decode
 * @param {*} hash
 */
export function decodeUnbufferedBase64 (hash) {
  const missingPadding = hash.length % 4
  if (missingPadding !== 0) {
    const newhash = hash + Array(4 - missingPadding).join('=')
    return Buffer.from(newhash, 'base64').toString()
  }
  else {
    return Buffer.from(hash, 'base64').toString()
  }
}

export function encodeUnbufferedBase64 (str) {
  return urlizeb64(Buffer.from(str).toString('base64'))
}

/**
 *
 * @param {*} o
 */
export function isObject (o) {
  return o instanceof Object && o.constructor === Object
}

/**
 * XML to JSON is a non-trivial exercise. We can make certain assumptions
 * about our incoming file
 * @param {*} data
 */
export function arrToObj (data) {
  for (const key in data) {
    // Make sure we're looping over legitimate objects
    if (data.hasOwnProperty(key)) {
      const keyDat = data[key]
      if (keyDat._) {
        keyDat.val = keyDat._
        delete keyDat._
      }
      if (keyDat.$) {
        keyDat.attr = keyDat.$
        delete keyDat.$
      }
      if (typeof keyDat === 'object') {
        data[key] = arrToObj(keyDat)
        // There's a pattern we want to collapse
        // It puts every single individual value into its own array. Not cool
        if (Array.isArray(keyDat) && keyDat.length === 1 && typeof keyDat[0] === 'string') {
          if (isNumeric(keyDat[0])) data[key] = Number(keyDat[0])
          else data[key] = keyDat[0].replace(/\\/g, '/')
        }
        else if (Array.isArray(keyDat) && keyDat.length === 1 &&
        // The second case is when it puts objects into its own array
          isObject(keyDat[0]) &&
          Object.keys(keyDat[0]).length === 1) {
          // xml2js does a weird thing where attributes get put into the following structure
          // [{ "_": "475945.992587164,5125837.37170763", "$": { "units": "m"  } }]
          data[key] = keyDat[0][Object.keys(keyDat[0])[0]]
        }
        // Dynamo DB does not like empty strings
        if (typeof data[key] && data[key].length === 0) {
          delete data[key]
        }
        if (typeof data[key] === 'string' && isNumeric(data[key])) data[key] = Number(data[key])
      }
    }
  }
  return data
}

/**
 * Returns a random number between min (inclusive) and max (exclusive)
 */
export function getRandomArbitraryInt (min, max) {
  return parseInt(Math.random() * (max - min) + min, 10)
}

/**
 * Returns a function, that, as long as it continues to be invoked, will not
 * be triggered. The function will be called after it stops being called for
 * N milliseconds. If `immediate` is passed, trigger the function on the
 * leading edge, instead of the trailing.
 * @param {*} func
 * @param {*} wait
 * @param {*} immediate
 */
export function debounce (func, wait, immediate) {
  let timeout = null
  return (...innerArgs) => {
    const context = this
    // const args = arguments
    const callNow = immediate && !timeout
    clearTimeout(timeout)

    timeout = setTimeout(() => {
      timeout = null
      if (!immediate) {
        // console.log('debounce', innerArgs)
        func.apply(context, innerArgs)
      }
    }, wait)
    if (callNow) {
      // console.log('debounce', innerArgs)
      func.apply(context, innerArgs)
    }
  }
};

