import React from 'react'
import PropTypes from 'prop-types'
import { graphql, compose } from 'react-apollo'
import actions from '../redux/actions'
import { connect } from 'react-redux'
import { home } from '../config'

import { userSeries, seriesFromList } from './queries'
import MyProfile from '../components/MyProfile/MyProfile'

const ProfileConnect = ({ data, jumpToSeries, jumpToMap }) => {
  const { seriesQ, watchedSeriesQ } = data

  const mySeries = seriesQ && seriesQ.userSeries && seriesQ.userSeries.seriesSummaries ? seriesQ.userSeries.seriesSummaries : []
  const watchingSeries = watchedSeriesQ && watchedSeriesQ.arraySeries ? watchedSeriesQ.arraySeries : []
  // console.log('ProfileConnect', { mySeries, watchedSeriesQ, watchingSeries })

  const loading = Boolean(
    (seriesQ && seriesQ.loading) ||
    (watchedSeriesQ && watchedSeriesQ.loading)
  )

  return <MyProfile {...{ loading, mySeries, watchingSeries, jumpToSeries, jumpToMap }} />
}

ProfileConnect.propTypes = {
  data: PropTypes.object, // from graphql
  jumpToSeries: PropTypes.func, // from redux
  jumpToMap: PropTypes.func // from redux
}

const withuserSeriesQuery = graphql(userSeries, {
  props: ({ data, ownProps }) => {
    // console.log('ProfileConnect withuserSeriesQuery', { data, ownProps })
    return { data: { ...ownProps.data, seriesQ: data } }
  }
})

const withMyWatchedSeriesQuery = graphql(seriesFromList, {
  props: ({ data, ownProps }) => {
    // console.log('ProfileConnect withMyWatchedSeriesQuery', { data, ownProps })
    return { data: { ...ownProps.data, watchedSeriesQ: data } }
  },
  skip: ({ data, user }) => {
    try {
      return user.watching.length < 1
    }
    catch (e) {
      return true
    }
  },
  options: ({ data, user }) => {
    return {
      variables: { seriesList: user.watching }
    }
  }
})

/**
 * Compose all our queries together
 */
const queryWrapped = compose(
  withuserSeriesQuery,
  // withMyPhotosQuery,
  withMyWatchedSeriesQuery
)(ProfileConnect)

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  return { }
}

const mapDispatchToProps = dispatch => ({
  jumpToSeries: (seriesId) => {
    dispatch(actions.setHomeState(home.AppStateTypes.SERIES, { activeSeries: seriesId }))
  },
  jumpToMap: (seriesId) => {
    dispatch(actions.setHomeState(home.AppStateTypes.MAP, { activeSeries: seriesId }))
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(queryWrapped)
