import React from 'react'
import PropTypes from 'prop-types'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import Avatar from '@material-ui/core/Avatar'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => ({
  root: {
  },
  imgContainer: {
    // flex: '0 0 50px',
    // alignSelf: 'flex-start',
    // height: '100%',
  },
  gravatarImg: {
    objectFit: 'cover',
    height: '100%',
    width: '100%'
  }
}))

const UserListItem = ({ user, loading, onAvatarClick, secondary }) => {
  const classes = useStyles()

  if (loading || !user) return 'LOADING...'
  // console.log('USER', user)

  return (
    <ListItem className={classes.root} button={!!onAvatarClick} onClick={onAvatarClick}>
      <ListItemAvatar className={classes.imgContainer}>
        <Avatar>
          <img alt='profile' className={classes.gravatarImg} src={user.gravatar} />
        </Avatar>
      </ListItemAvatar>
      <ListItemText
        primary={user.username}
        secondary={secondary}
      />
    </ListItem>
  )
}

UserListItem.propTypes = {
  user: PropTypes.object,
  loading: PropTypes.bool,
  secondary: PropTypes.string,
  onAvatarClick: PropTypes.func
}

export default UserListItem
