import React from 'react'
import PropTypes from 'prop-types'
import Snackbar from '@material-ui/core/Snackbar'
import Typography from '@material-ui/core/Typography'
import Paper from '@material-ui/core/Paper'
import AppLogo from '../../icons/AppLogo'
import { makeStyles } from '@material-ui/core/styles'
import { Button } from '@material-ui/core'
const useStyles = makeStyles(theme => ({
  root: {

  },
  content: {

  },
  messageWrapper: {
    display: 'flex'
  },
  logo: {
    height: 50,
    width: 50
  },
  paper: {
    background: theme.palette.text.primary,
    color: theme.palette.primary.contrastText,
    border: `2px solid ${theme.palette.primary.contrastText}`,
    boxShadow: `0 0 0 0 ${theme.palette.primary.main}`,
    animation: '$installPulse 2s infinite',
    padding: theme.spacing(2, 3),
    width: '100%'
  },
  '@keyframes installPulse': {
    '0%': {
      boxShadow: `0 0 0 0px ${theme.palette.primary.main}`
    },
    '50%': {
      boxShadow: `0 0 0 0px ${theme.palette.primary.main}`
    },
    '100%': {
      boxShadow: `0 0 0 20px transparent`
    }
  },
  textWrapper: {
    flexGrow: 1,
    paddingLeft: theme.spacing(1)
  },
  buttons: {
    display: 'flex'
  },
  spacer: {
    flexGrow: 1
  }
}))

const InstallPrompt = ({ open, onClose, onInstall }) => {
  const classes = useStyles()

  return (
    <Snackbar
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'center'
      }}
      open={open}
      // autoHideDuration={6000}
      onClose={onClose}
    >
      <Paper className={classes.paper} elevation={24}>
        <div id='client-snackbar' className={classes.messageWrapper}>
          <AppLogo className={classes.logo} />
          <div className={classes.textWrapper}>
            <Typography variant='body1' color='inherit'>Install MorPhoto</Typography>
            <Typography variant='body2' color='inherit' paragraph>This app can be installed and used on your device natively and offline if you would like.</Typography>
            <div className={classes.buttons}>
              <Button color='secondary' variant='contained' onClick={onClose}>No thanks</Button>
              <div className={classes.spacer} />
              <Button color='primary' variant='contained' onClick={onInstall}>Install</Button>
            </div>
          </div>
        </div>
      </Paper>
    </Snackbar>
  )
}

InstallPrompt.propTypes = {
  open: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
  onInstall: PropTypes.func.isRequired
}

export default InstallPrompt
