import { convertJSDatetoTimestamp } from './datelib'
import SeriesConfig from './SeriesConfig'
import uuidv4 from 'uuid/v4'
import exifParser from 'exif-parser'
import { isNumeric } from './util'

class Photo {
  constructor (seriesConfig, photo) {
    this.config = new SeriesConfig(seriesConfig)
    if (photo) this.photo = photo
    else {
      this.photo = this.newPhoto()
    }
  }

  setMeta = () => {
    this.meta = JSON.stringify(this.config.getPhotoMeta())
  }

  newPhoto = () => ({
    id: `__LOCAL_PHOTO::${uuidv4()}`,
    uploadToken: uuidv4(),
    takenOn: convertJSDatetoTimestamp(new Date()),
    meta: JSON.stringify(this.config.getPhotoMeta()),
    tags: []
  })
}

export default Photo

export const getObjectUrl = async (store, key) => {
  try {
    const dbval = await store.getItem(key)
    const urlVal = window.URL.createObjectURL(dbval)
    if (urlVal && urlVal.indexOf('data:image') === 0) {
      return urlVal
    }
  }
  catch (e) {
    console.error(e)
  }

  return null
}

export const getExif = (dataUrl) => {
  console.log('onLibraryAdd Buffered')

  let exif = null
  let exifDateTime = null
  let exifGPS = null
  try {
    const find = 'base64,'
    const foundIdx = dataUrl.indexOf(find) + find.length
    const newData = dataUrl.slice(foundIdx)
    const parser = exifParser.create(Buffer.from(newData, 'base64'))
    exif = parser.parse()
    console.log('got EXIF', exif)
  }
  catch (err) {
  // got invalid data, handle error
    console.error('EXIF ERROR', err)
  }
  if (exif && exif.tags) {
    // Try to parse the DateTime of the original image
    try {
      if (exif.tags && exif.tags.DateTimeOriginal && String(exif.tags.DateTimeOriginal)) {
        exifDateTime = String(exif.tags.DateTimeOriginal)
      }
    }
    catch (err) {
      console.log('EXIF', exif)
      console.error('ERROR GETTING TIMESTAMP', err)
    }
    // Try to pull out the GPS info from the image
    try {
      if (exif.tags.GPSLatitude && isNumeric(exif.tags.GPSLatitude) && exif.tags.GPSLongitude && isNumeric(exif.tags.GPSLongitude)) {
        exifGPS = [exif.tags.GPSLongitude, exif.tags.GPSLatitude]
      }
    }
    catch (e) {
      console.error('GPS Longitude and Latitude could not be determined')
    }
  }
  return { exif, exifDateTime, exifGPS }
}
