import React from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'
import Paper from '@material-ui/core/Paper'
import CssBaseline from '@material-ui/core/CssBaseline'
import LinearProgress from '@material-ui/core/LinearProgress'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    background: 'black',
    color: theme.palette.primary.contrastText,
    padding: theme.spacing(1) * 2
  },
  message: {
    // width: '100%'
  },
  msgText: {
    display: 'flex'
  },
  spacer: {
    flexGrow: 1
  },
  messageInner: {
  }
})
const useStyles = makeStyles(style)

const UploadControlUnstyled = ({ className, text, progress }) => {
  const classes = useStyles()
  if (!text) return null
  const message = `Uploading: ${text}`
  return (
    <div className={classNames(classes.root, className)}>
      <div className={classes.messageInner}>
        <div className={classes.msgText}>
          <Typography color='inherit'>{message}</Typography>
          <div className={classes.spacer} />
          <Typography color='inherit'>{progress}%</Typography>
        </div>
        <LinearProgress color='secondary' variant='determinate' value={progress} />
      </div>
    </div>
  )
}

UploadControlUnstyled.propTypes = {
  className: PropTypes.string,
  text: PropTypes.string,
  progress: PropTypes.number
}

const UploadControl = UploadControlUnstyled
export default UploadControl

export const UploadControlCOSMOSWrapper = ({ text, progress }) => {
  // const [someStateItem, setSomeStateItem] = useState()
  const classes = useWrapperStyles()
  return (
    <div className={classes.root}>
      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      <Paper className={classes.paper}>
        <Typography variant='h5'>
          This is a dummy component.
        </Typography>
      </Paper>
      <UploadControl text={text} progress={progress} />
    </div>
  )
}

UploadControlCOSMOSWrapper.propTypes = {
  text: PropTypes.string,
  progress: PropTypes.number
}
const wrapperStyle = theme => ({
  root: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column'
  },
  paper: {
    flexGrow: 1
  }
})

const useWrapperStyles = makeStyles(wrapperStyle)

UploadControlCOSMOSWrapper.displayName = UploadControl.displayName
export const UploadControlTest = UploadControlCOSMOSWrapper
