import React from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import CircularProgress from '@material-ui/core/CircularProgress'
import BrokenImageIcon from '@material-ui/icons/BrokenImage'

import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    padding: '10%',
    display: 'flex',
    flexDirection: 'column',
    opacity: 0.3,
    textAlign: 'center',
    color: theme.palette.grey[100],
    background: theme.palette.getContrastText(theme.palette.grey[100])
  },
  space: {
    flexGrow: 1
  },
  icon: {
    width: '100%',
    flex: '1 1 40%',
    color: theme.palette.grey[100],
    opacity: 0.5
  },
  circularProgress: {
    margin: '0 auto'
  },
  primary: {
    color: theme.palette.grey[100],
    textAlign: 'center',
    width: '100%'
  },
  secondary: {
    flex: '1 1 30%',
    width: '100%'
  }
}))

const BrokenImg = ({ loading }) => {
  const classes = useStyles()
  return (
    <div className={classes.root}>
      <div className={classes.spacer} />
      {loading
        ? <CircularProgress color='inherit' className={classes.circularProgress} />
        : <BrokenImageIcon className={classes.icon} />
      }
      <Typography className={classes.primary}>
        {loading ? 'Loading' : 'Img not found' }
      </Typography>
      <div className={classes.spacer} />
    </div>
  )
}
BrokenImg.propTypes = {
  loading: PropTypes.bool
}

export default BrokenImg
