import React, { useState, useEffect, useCallback } from 'react'
import PropTypes from 'prop-types'
import CssBaseline from '@material-ui/core/CssBaseline'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Snackbar from '@material-ui/core/Snackbar'
import SnackbarContent from '@material-ui/core/SnackbarContent'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import QRcodeIcon from '../../../icons/QRCode'
import jsQR from 'jsqr'
import { debounce } from '../../../lib/util'
import {
  green, red
} from '@material-ui/core/colors'

import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import Paper from '@material-ui/core/Paper'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
import { useCamera } from '../../../lib/useCamera'

const CAMERAWIDTH = 400
const CAMERAHEIGHT = 400

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    height: '100%',
    flexDirection: 'column'
  },
  qrIcon: {
    border: '5px solid red',
    padding: theme.spacing(2),
    position: 'absolute',
    color: 'white',
    opacity: 0.4,
    zIndex: 2,
    width: '70%',
    height: '70%',
    top: '50%',
    left: '50%',
    transformOrigin: '50% 50%',
    transform: 'translateY(-50%) translateX(-50%)'
  },
  appBar: {

  },
  inner: {
    padding: theme.spacing(2),
    height: '100%',
    flexGrow: 1,
    overflow: 'hidden'
  },
  tipPaper: {
    padding: theme.spacing(2),
    margin: theme.spacing(2)
  },
  docContainer: {
    flexGrow: 1,
    padding: theme.spacing(2)
  },
  cameraContainer: {
    width: '80%',
    margin: '0 auto',
    position: 'relative',
    // this is a CSS trick: https://spin.atomicobject.com/2015/07/14/css-responsive-square/
    '&::before': {
      content: ' ',
      display: 'block',
      paddingTop: '100%'
    }
  },
  snackErr: {
    background: red[500],
    color: 'white'
  },
  snackSuccess: {
    background: green[500],
    color: 'white'
  },
  snackMsg: {

  },
  video: {
    height: '100%',
    width: '100%',
    verticalAlign: 'top'
    // display: 'none'
    // position: 'absolute'
    // top: 0,
    // left: 0,
    // zIndex: 1
    // '@media (orientation: portrait)': {
    //   transform: 'rotate(90)'
    // }
  }
}))

const QRReader = ({ onMenuBack, onCapture }) => {
  const classes = useStyles()
  const [capturedImg, setCapturedImg] = useState()
  const [err, setErr] = useState(null)
  const [code, setCode] = useState(null)

  const { cameraError, captureRawData, cameraOnline } = useCamera(setCapturedImg, CAMERAWIDTH, CAMERAHEIGHT)
  // limit to 10 frames per second
  const captureFunc = useCallback(debounce(captureRawData, 100), [cameraOnline, cameraError])

  useEffect(() => {
    if (cameraOnline && !cameraError && !err && !code) {
      if (!capturedImg || !capturedImg.data) captureFunc()
      else {
        const scan = async () => {
          let scannedCode = null
          try {
            scannedCode = await jsQRFromBase64(capturedImg)
          }
          catch (e) {
            console.error(e)
          }
          try {
            if (scannedCode) {
              const find = '?fromqr='
              const foundIdx = scannedCode.data.indexOf(find) + find.length
              const newData = scannedCode.data.slice(foundIdx)
              console.log('DEBUG', { data: scannedCode.data, newData })
              const decodedUrl = JSON.parse(decodeURI(newData))
              if (!decodedUrl.type || !decodedUrl.id) throw new Error('Type or ID missing from QR Code')
              setCode(decodedUrl)
              setErr(null)
              console.log('QRCODE', scannedCode)
            }
            captureFunc()
          }
          catch (e) {
            setErr('Not a valid QR Code')
            console.error(e)
            captureFunc()
          }
        }
        scan()
      }
    }
  }, [cameraOnline, capturedImg, err, code])

  // console.log(capturedImg)
  return (
    <div className={classes.root}>
      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      <AppBar position='static' className={classes.appBar}>
        <Toolbar disableGutters>
          <IconButton className={classes.menuButton} color='inherit' onClick={onMenuBack} aria-label='Menu'>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant='subtitle1' color='inherit' className={classes.toolbarTitle}>
            Capture QR Code
          </Typography>
        </Toolbar>
      </AppBar>
      <div className={classes.inner}>
        <Paper className={classes.cameraContainer}>
          <QRcodeIcon className={classes.qrIcon} />
          <video
            autoPlay
            playsInline
            muted
            id='webcam'
            className={classes.video}
          />
        </Paper>
        <Paper className={classes.tipPaper}>
          <Typography>TIP: You can find the QR code for a series in the SHARE tab of that series</Typography>
        </Paper>
      </div>
      <Snackbar
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        open={!!err}
        autoHideDuration={2000}
        onClose={() => setErr(null)}
      >
        <SnackbarContent
          className={classes.snackErr}
          message={<span className={classes.snackMsg}>{err}</span>}
        />
      </Snackbar>
      <Snackbar
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        open={cameraError}
      >
        <SnackbarContent
          className={classes.snackErr}
          message={<span className={classes.snackMsg}>Camera Error. Check your device's permissions</span>}
        />
      </Snackbar>
      <Snackbar
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        open={!!code}
        autoHideDuration={500}
        onClose={() => onCapture(code.type, code.id)}
      >
        <SnackbarContent
          className={classes.snackSuccess}
          message={<span className={classes.snackMsg}>Valid QR Code found!</span>}
        />
      </Snackbar>

    </div>
  )
}

QRReader.propTypes = {
  onMenuBack: PropTypes.func.isRequired,
  onCapture: PropTypes.func.isRequired
}

export default QRReader

const jsQRFromBase64 = ({ data, imageHeight, imageWidth }) => {
  return new Promise((resolve, reject) => {
    try {
      // console.log(imageHeight, imageWidth)
      const qrCode = jsQR(data, imageWidth, imageHeight)
      resolve(qrCode)
    }
    catch (e) {
      reject(e)
    }
  })
}
