import React, { useState } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import Fab from '@material-ui/core/Fab'
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer'
import CssBaseline from '@material-ui/core/CssBaseline'
import Typography from '@material-ui/core/Typography'

import CameraIcon from '@material-ui/icons/Camera'
import CancelIcon from '@material-ui/icons/Cancel'
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary'

import PhotoFadeSlider from './controls/PhotoFadeSlider'
import PhotoGrid from './subViews/PhotoGrid'
import Photo from './subViews/Photo'

import { makeStyles } from '@material-ui/core/styles'
import style from './CameraTool.style'
import { useCamera } from '../../../lib/useCamera'

const useStyles = makeStyles(style)

// https://googlechrome.github.io/samples/image-capture/grab-frame-take-photo.html
const CameraTool = ({ photos, onCancel, onShutter, defaultRefPhotoId }) => {
  const classes = useStyles()
  const [slider, setSlider] = useState(50)
  const newRefPhoto = defaultRefPhotoId
    ? photos.find(p => p.id === defaultRefPhotoId)
    : photos && photos.length > 0 ? photos[0] : null

  const [refPhoto, setRefPhoto] = useState(newRefPhoto)
  const [drawerOpen, setDrawerOpen] = useState(false)
  const { cameraError, captureImage, cameraOnline } = useCamera((data) => onShutter(data, refPhoto ? refPhoto.id : null))

  const handleTileClick = (id) => {
    setRefPhoto(photos.find(p => p.id === id))
    setDrawerOpen(false)
  }

  return (
    <div className={classes.root}>
      <CssBaseline />
      { !cameraError && Boolean(refPhoto) && (
        <PhotoFadeSlider defaultValue={slider} onChange={setSlider} />
      )}

      { cameraError && (
        <div className={classes.cameraErrorText}>
          <Typography variant='subtitle1' color='inherit'>No Camera Access</Typography>
          <Typography variant='body2' color='inherit'>Please enabled access in your settings.</Typography>
        </div>
      )}

      <div className={classes.captured}>
        { Boolean(refPhoto) && (
          <div className={classes.reference} >
            <Photo
              className={classes.referenceContainer}
              opacity={(100 - slider) / 100}
              photo={refPhoto}
            />
          </div>
        )}
        { !cameraError && (
          <video
            autoPlay
            playsInline
            muted
            id='webcam'
            width={window.innerWidth}
            height={window.innerHeight}
            className={classes.video}
          />
        )}
      </div>

      <Fab
        color='primary'
        aria-label='Take Photo'
        classes={{
          disabled: classes.disabledFab
        }}
        disabled={cameraError || !cameraOnline}
        className={classNames(classes.cameraFab, classes.buttonFab)}
        onClick={captureImage}
      >
        <CameraIcon className={classes.cameraIcon} />
      </Fab>

      <Fab
        color='secondary'
        aria-label='cancel'
        onClick={onCancel}
        classes={{
          disabled: classes.disabledFab
        }}
        className={classNames(classes.cancelFab, classes.buttonFab)}
      >
        <CancelIcon className={classes.cancelIcon} />
      </Fab>

      <Fab
        aria-label='cancel'
        className={classNames(classes.libraryFab, classes.buttonFab)}
        classes={{
          disabled: classes.disabledFab
        }}
        disabled={!photos || photos.length === 0}
        onClick={() => setDrawerOpen(true)}
      >
        <PhotoLibraryIcon className={classes.libraryIcon} />
      </Fab>

      <SwipeableDrawer
        anchor='left'
        open={drawerOpen}
        classes={{
          paperAnchorLeft: classes.swipeDrawer
        }}
        onClose={() => setDrawerOpen(false)}
        onOpen={() => setDrawerOpen(true)}
      >
        <div
          tabIndex={0}
          className={classes.swipeDrawerContainer}
          role='button'
          onClick={() => setDrawerOpen(false)}
          onKeyDown={() => setDrawerOpen(false)}
        >
          <PhotoGrid
            multiSelect={false}
            columns={1}
            photos={photos}
            onTileClick={handleTileClick}
          />
        </div>
      </SwipeableDrawer>

    </div>
  )
}

CameraTool.propTypes = {
  photos: PropTypes.array,
  defaultRefPhotoId: PropTypes.string, // id of the reference photo
  onCancel: PropTypes.func.isRequired,
  onShutter: PropTypes.func.isRequired
}

export default CameraTool
