import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'

import PhotoReviewTool from './PhotoReviewTool'
import CameraTool from './CameraTool'
import NewPhotoInfo from './NewPhotoInfo'
import LibraryFilePicker from './controls/LibraryFilePicker'

import PhotoClass, { getExif } from '../../../lib/Photo'
import SeriesClass from '../../../lib/Series'
import { useDeviceOrientation, useGeolocation } from '../../../lib/sensors'

import { SeriesContext } from '../contexts'

const MODES = {
  CAMERA: 'camera',
  FILEPICKER: 'library',
  PHOTOREVIEW: 'photoreview',
  PHOTOINFOEDIT: 'photoinfoedit'
}

const NewPhoto = ({ inputMethod, onCreate, onCancel, isNewSeries }) => {
  const { seriesConfig, series } = useContext(SeriesContext)

  // This will hold our urlData base64-encoded image
  const [newPhotoData, setNewPhotoData] = useState(null)

  // Instantiate common-sense empty objects.
  const [newSeries, setNewSeries] = useState(isNewSeries ? new SeriesClass(seriesConfig).series : series)
  const [newPhoto, setNewPhoto] = useState(new PhotoClass(seriesConfig).photo)

  const [photoRefId, setPhotoRefId] = useState(null)
  const [mode, _changeMode] = useState({ current: inputMethod, history: [] })
  const orientation = useDeviceOrientation()
  const geo = useGeolocation()

  const photos = !isNewSeries && series && series.photos ? series.photos : []

  const changeMode = (newModeName) => {
    console.log('NEWPHOTO changeMode', mode, newModeName)
    _changeMode({
      current: newModeName,
      history: [...mode.history, mode.current]
    })
  }
  const lastMode = () => {
    if (mode.history.length > 0) {
      const oldHistory = [...mode.history]
      const oldMode = oldHistory.pop()
      _changeMode({
        current: oldMode,
        history: oldHistory
      })
    }
    else {
      onCancel()
    }
  }

  const onLibraryAdd = (data) => {
    console.log('onLibraryAdd Debug GOT DATA', { data, isNewSeries })
    setNewPhotoData(data)
    // Ok, now try to get the datetime off the EXIF
    const { exif, exifDateTime, exifGPS } = getExif(data.dataUrl)
    let coords = exifGPS
    const date = exifDateTime || String(Date.now())
    if (!coords) {
      if (geo && geo.latitude && geo.longitude) {
        const { latitude, longitude } = geo
        coords = [longitude, latitude]
      }
      else {
        // Stanley park as a fallback if we can't get the geo or exif data
        coords = [-123.1398254, 49.3050394]
      }
    }

    if (isNewSeries) setNewSeries({ ...newSeries, coords })

    console.log('LATLONG FOR LIB PICK', { coords, exifGPS, geo })

    const _newPhoto = {
      ...newPhoto,
      takenOn: date,
      rawData: JSON.stringify({
        exif,
        sensors: {
          orientation,
          geo
        }
      })
    }
    setNewPhoto(_newPhoto)
    console.log('onLibraryAdd', _newPhoto)
    changeMode(MODES.PHOTOREVIEW)
    console.log('onLibraryAdd FINISHED', _newPhoto)
  }

  const onShutter = (data, refId) => {
    setNewPhotoData(data)
    console.log('onShutter Debug GOT DATA', { data })
    const { exif, exifGPS } = getExif(data.dataUrl)

    const date = String(Date.now())
    // with the camera we prioritize the geoLocation over EXIF gps data
    let coords = geo
    if (!coords) {
      if (geo && exifGPS) {
        coords = exifGPS
      }
      else {
        // Stanley park as a fallback if we can't get the geo or exif data
        coords = [-123.1398254, 49.3050394]
      }
    }

    if (isNewSeries) setNewSeries({ ...newSeries, coords })

    const _newPhoto = {
      ...newPhoto,
      takenOn: date,
      rawData: JSON.stringify({
        exif,
        sensors: {
          orientation,
          geo
        }
      })
    }
    setNewPhoto(_newPhoto)
    console.log('onShutter', _newPhoto)
    setPhotoRefId(refId)
    changeMode(MODES.PHOTOREVIEW)
  }

  const onFinalCreate = (seriesDelta, photoDelta) => onCreate(seriesDelta, photoDelta, newPhotoData)

  let component = null
  switch (mode.current) {
    case MODES.CAMERA:
      component = (
        <CameraTool
          photos={photos}
          onShutter={onShutter}
          onCancel={onCancel}
        />
      )
      break
    case MODES.FILEPICKER:
      component = <LibraryFilePicker
        onCancel={onCancel}
        onLibraryAdd={onLibraryAdd}
      />
      // fileClick() // move this to useEffect to help with click handling
      break
    case MODES.PHOTOREVIEW:
      component = (
        <PhotoReviewTool
          defaultRefPhotoId={photoRefId}
          photos={photos}
          captured={newPhoto}
          dataUrl={newPhotoData.dataUrl}
          onConfirm={() => changeMode(MODES.PHOTOINFOEDIT)}
          onCancel={onCancel}
          onRetry={lastMode}
        />
      )
      break
    case MODES.PHOTOINFOEDIT:
      component = (
        <NewPhotoInfo
          photo={newPhoto}
          newSeries={newSeries}
          onSave={onFinalCreate}
          onCancel={onCancel}
        />
      )
      break
    default:
      console.log('NEWPHOTO: default state. you should NEVER see this', mode)
      onCancel()
  }

  return component
}

NewPhoto.propTypes = {
  inputMethod: PropTypes.oneOf(['camera', 'library']),
  onCreate: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  isNewSeries: PropTypes.bool,
  seriesConfig: PropTypes.object,
  // This might be a new series so we might have
  // actions to work on that
  // This will be provided EITHER photos or onSeriesCreate
  photos: PropTypes.array
}

export default NewPhoto
