import React, { useState } from 'react'
import PropTypes from 'prop-types'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import IconButton from '@material-ui/core/IconButton'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import Typography from '@material-ui/core/Typography'
import DialogActions from '@material-ui/core/DialogActions'

import CloseIcon from '@material-ui/icons/Close'
import { makeStyles } from '@material-ui/core/styles'

import CancelConfirm from '../dialogs/CancelConfirm'

import PhotoInfoInner from './PhotoInfoInner'

const style = theme => ({
  root: {
    height: '90%',
    width: '85%'
  },
  dlgTitleContainer: {
    display: 'flex',
    padding: 0
  },
  dlgTitle: {
    flexGrow: 1,
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(1),
    paddingLeft: theme.spacing(3)
  },
  field: {
    marginBottom: theme.spacing(2)
  }
})
const useStyles = makeStyles(style)

const DIALOGS = {
  NOTEEDIT: 'noteEdit',
  NOTEVIEW: 'noteView',
  PHOTOTAKEN: 'taken',
  TAGEDIT: 'tag',
  CANCEL: 'cancel'
}

const PhotoInfo = ({ canEdit, photo, onCancel, onOk, open }) => {
  const classes = useStyles()
  const [activeDialog, setActiveDialog] = useState(null)
  const [delta, setDelta] = useState({})

  const dirty = delta && Object.keys(delta).length > 0

  return (
    <Dialog
      fullScreen
      fullWidth
      onClose={() => {
        if (dirty) setActiveDialog(DIALOGS.CANCEL)
        else onCancel()
      }}
      aria-labelledby='customized-dialog-title'
      open={open}
    >
      <DialogTitle disableTypography className={classes.dlgTitleContainer}>
        <Typography variant='h5' className={classes.dlgTitle}>
          Photo Properties
        </Typography>
        <IconButton onClick={onCancel}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        <PhotoInfoInner
          photo={photo}
          delta={delta}
          onChange={newDelta => setDelta({ ...delta, ...newDelta })}
          canEdit={canEdit}
        />
      </DialogContent>

      <DialogActions>
        {!canEdit && (
          <Button onClick={onCancel} color='primary'>
            Ok
          </Button>
        )}
        {canEdit && !dirty && (
          <Button onClick={onCancel} color='primary'>
            Back
          </Button>
        )}
        {canEdit && dirty && (
          <React.Fragment>
            <Button onClick={() => setActiveDialog(DIALOGS.CANCEL)} color='primary'>
              Cancel
            </Button>
            <Button onClick={() => setDelta({})} color='primary'>
              Reset
            </Button>
            <Button onClick={() => onOk(delta)} color='primary'>
              Ok
            </Button>
          </React.Fragment>
        )}
      </DialogActions>
      {activeDialog && activeDialog === DIALOGS.CANCEL && (
        <CancelConfirm
          open
          title='Lose unsaved changes?'
          message='You will lose any changes you made to this form'
          onConfirm={confirm => {
            setActiveDialog(null)
            if (confirm) onCancel()
          }}
        />
      )}
    </Dialog>
  )
}

PhotoInfo.propTypes = {
  canEdit: PropTypes.bool,
  onCancel: PropTypes.func.isRequired,
  onOk: PropTypes.func.isRequired,
  open: PropTypes.bool,
  photo: PropTypes.object.isRequired
}

export default PhotoInfo
