import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Divider from '@material-ui/core/Divider'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'

import SeriesListItem from '../misc/SeriesListItem'
import { makeStyles } from '@material-ui/core/styles'
import TopBar from '../TopBar'
import MeCard from './MeCard'
import { Button } from '@material-ui/core'

import { AppContext } from '../../contexts'

const useStyles = makeStyles(theme => ({
  root: {
    background: theme.palette.background.paper,
    display: 'flex',
    flexDirection: 'column',
    height: '100%'
  },
  filterContainer: {
    color: theme.palette.primary.secondary,
    background: theme.palette.background.default,
    textAlign: 'center',
    padding: theme.spacing(1, 1)
  },
  filterChip: {

  },
  seriesListItemWrap: {
    margin: theme.spacing(1, 0.5)
  },
  seriesList: {
    height: '100%',
    overflowX: 'hidden',
    overflowY: 'scroll'
  },
  tabs: {
    // background: theme.palette.background.default
  },
  noneFound: {
    color: theme.palette.text.secondary,
    textAlign: 'center',
    padding: theme.spacing(2)
  }
}))

const SORT = {
  ACTIVITY: 'Last Photo',
  CREATED: 'Created',
  NAME: 'Name'
}

const MyProfile = ({ mySeries, watchingSeries, loading, jumpToSeries, jumpToMap }) => {
  const classes = useStyles()
  const [currentTab, setCurrentTab] = useState(0)
  const [sortVal, setSort] = useState(SORT.ACTIVITY)
  const [sortAsc, setSortAsc] = useState(false)

  const { user } = useContext(AppContext)

  const sortSetter = (newSort) => {
    if (newSort === sortVal) setSortAsc(!sortAsc)
    else setSort(newSort)
  }

  let tabFeed = currentTab === 0 ? mySeries : watchingSeries

  switch (sortVal) {
    case SORT.ACTIVITY:
      if (sortAsc) tabFeed.sort((a, b) => a.latest.takenOn - b.latest.takenOn)
      else tabFeed.sort((a, b) => b.latest.takenOn - a.latest.takenOn)
      break
    case SORT.CREATED:
      if (sortAsc) tabFeed.sort((a, b) => a.createdOn - b.createdOn)
      else tabFeed.sort((a, b) => b.createdOn - a.createdOn)
      break
    case SORT.NAME:
      if (sortAsc) {
        tabFeed.sort((a, b) => {
          if (a.name < b.name) {
            return -1
          }
          if (a.name > b.firstname) {
            return 1
          }
          return 0
        })
      }
      else {
        tabFeed.sort((a, b) => {
          if (b.name < a.name) {
            return -1
          }
          if (b.name > a.name) {
            return 1
          }
          return 0
        })
      }
      break
    default:
      break
  }

  return (
    <div className={classes.root}>
      <TopBar>
        <Typography>My Profile</Typography>
      </TopBar>
      <MeCard user={user} />
      {/* <Divider /> */}
      <Tabs
        value={currentTab}
        onChange={(e, val) => setCurrentTab(val)}
        indicatorColor='primary'
        centered
        classes={{ flexContainer: classes.tabs }}
      >
        <Tab className={classes.tab} value={0} label='My Series' />
        <Tab className={classes.tab} value={1} label='Watching' />
      </Tabs>
      <Divider />
      <div className={classes.filterContainer}>
        <FilterSortItem asc={sortAsc} active={sortVal === SORT.NAME} onClick={() => sortSetter(SORT.NAME)} label={SORT.NAME} />
        |
        <FilterSortItem asc={sortAsc} active={sortVal === SORT.ACTIVITY} onClick={() => sortSetter(SORT.ACTIVITY)} label={SORT.ACTIVITY} />
        |
        <FilterSortItem asc={sortAsc} active={sortVal === SORT.CREATED} onClick={() => sortSetter(SORT.CREATED)} label={SORT.CREATED} />
      </div>
      <Divider />
      <div className={classes.seriesList}>
        {!loading && (!tabFeed || tabFeed.length === 0) && (
          <Typography className={classes.noneFound} component='div'>No Series Found</Typography>
        )}
        {loading && (
          <Typography className={classes.noneFound} component='div'>Loading...</Typography>
        )}
        {tabFeed.map((series, idx) => (
          <SeriesListItem
            key={`seriesListItem${idx}`}
            series={series}
            showCreatedOn
            showLastPhoto
            onClick={() => jumpToSeries(series.id)}
          />
        ))}
      </div>
    </div>
  )
}

MyProfile.propTypes = {
  loading: PropTypes.bool,
  mySeries: PropTypes.array.isRequired,
  watchingSeries: PropTypes.array.isRequired,
  jumpToSeries: PropTypes.func.isRequired,
  jumpToMap: PropTypes.func.isRequired
}

export default MyProfile

const useFilterStyles = makeStyles(theme => ({
  root: {
    margin: theme.spacing(0, 1, 0, 1)
  }
}))

const FilterSortItem = ({ active, asc, onClick, label }) => {
  const classes = useFilterStyles()

  const arrow = asc === true ? '' : ''

  return (
    <Button
      size='small'
      className={classes.root}
      onClick={onClick}
      // variant='caption'
      color={active ? 'secondary' : 'inherit'}
    >
      {label}
      <span style={{ opacity: active ? 1 : 0 }}>
        {arrow}
      </span>
    </Button>
  )
}

FilterSortItem.propTypes = {
  active: PropTypes.bool,
  asc: PropTypes.bool,
  onClick: PropTypes.func.isRequired,
  label: PropTypes.string.isRequired
}
