import React from 'react'
import PropTypes from 'prop-types'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemIcon from '@material-ui/core/ListItemIcon'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
import { formatDate } from '../../lib/datelib'

const useStyles = makeStyles(theme => ({
  root: {
  },
  imgContainer: {
    flex: '0 0 100px',
    // alignSelf: 'flex-start',
    // height: '100%',
    margin: theme.spacing(1, 1, 0, 2)
  },
  gravatarImg: {
    verticalAlign: 'top'
    // objectFit: 'cover',
    // height: '100%',
    // width: '100%'
  }
}))

const MeCard = ({ user, loading, onAvatarClick }) => {
  const classes = useStyles()

  if (loading || !user) return 'LOADING...'
  // console.log('USER', user)

  return (
    <ListItem className={classes.root} onClick={onAvatarClick} disableGutters>
      <ListItemIcon className={classes.imgContainer}>
        <img alt='profile' className={classes.gravatarImg} src={user.gravatar} />
      </ListItemIcon>
      <ListItemText
        primary={user.username}
        secondary={`Member since: ${formatDate(user.createdOn, 'LL')}`}
      />
    </ListItem>
  )
}

MeCard.propTypes = {
  user: PropTypes.object,
  loading: PropTypes.bool,
  onAvatarClick: PropTypes.func
}

export default MeCard
