import AccessControl from 'accesscontrol'

class AccessHelper {
  constructor (userSub, role, permissionObj) {
    this.userSub = userSub
    this.role = role
    this.permissionObj = permissionObj
    this.ac = new AccessControl(permissionObj)
  }

  // Return the string 'Own' or the string 'Any'
  ownOrAny = (obj) => {
    if (!obj || !obj.createdUser || !obj.createdUser.id) throw new Error(`No Created user found`)
    try {
      return obj.createdUser.id === this.userSub ? 'Own' : 'Any'
    }
    catch (e) {
      return 'Any'
    }
  }

  isOwner = (obj) => {
    if (!obj || !obj.createdUser || !obj.createdUser.id) throw new Error(`No Created user found`)
    try {
      return obj.createdUser.id === this.userSub
    }
    catch (e) {
      return false
    }
  }

  toString () {
    console.log('AccessHelper:', { sub: this.userSub, role: this.role })
  }

  canUser = (action, obj, type, attrs) => {
    if (['series', 'photo'].indexOf(type) < 0) throw new Error(`canUser: type not supported ${type}`)

    try {
      const ownAny = this.ownOrAny(obj)

      const perm = this.ac.can(this.role)[`${action}${ownAny}`](type)
      let otherPerm = null
      if (type === 'series') {
        if (obj.access === 'PUBLIC') otherPerm = this.ac.can(this.role)[`${action}${ownAny}`]('series_public')
        else if (obj.access === 'OPEN') otherPerm = this.ac.can(this.role)[`${action}${ownAny}`]('series_open')
      }

      // Trying to recreate the form `can(role).readAny`
      if (attrs) {
        if (otherPerm) {
          return !!perm.attributes.find(p => p === '*' || attrs.indexOf(p) > -1) ||
           !!otherPerm.attributes.find(p => p === '*' || attrs.indexOf(p) > -1)
        }
        else return !!perm.attributes.find(p => p === '*' || attrs.indexOf(p) > -1)
      }
      else {
        if (otherPerm) return perm.granted || otherPerm.granted
        else return perm.granted
      }
    }
    catch (e) {
      console.error(e)
      return false
    }
  }
}

export default AccessHelper
