import gql from 'graphql-tag'
import {
  photoSummaryFragment,
  photoFragment,
  seriesSummaryFragment,
  seriesFragment,
  userFragment
} from './fragments'

export const allSeries = gql`
query allSeries($limit: Int, $nextToken: String) {
  allSeries(limit: $limit, nextToken: $nextToken) @connection(key: "allSeries"){
    seriesSummaries {
      ...seriesSummaryFragment
    }
    nextToken
    __typename
  }
  __typename
}
${seriesSummaryFragment}
`

export const pollQuery = gql`
query pollQuery($lastCheck: String!, $limit: Int, $nextToken: String) {
  changedSeries(lastCheck: $lastCheck, limit: $limit, nextToken: $nextToken) @connection(key: "changedSeries"){
    seriesSummaries {
      ...seriesSummaryFragment
    }
    __typename
  }
  __typename
}
${seriesSummaryFragment}
`

export const userSeries = gql`
query userSeries($createdBy: ID) {
  userSeries(createdBy: $createdBy ) {
    seriesSummaries {
      ...seriesSummaryFragment
    }
    nextToken
    __typename
  }
  __typename
}
${seriesSummaryFragment}
`

export const seriesSummaryFromList = gql`
query seriesFromList($seriesList: [ID]) {
  arraySeries(seriesIds: $seriesList) {
    ...seriesSummaryFragment
  }
  __typename
}
${seriesSummaryFragment}
`

export const seriesFromList = gql`
query seriesFromList($seriesList: [ID]) {
  arraySeries(seriesIds: $seriesList) {
    ...seriesFragment
    photos {
      ...photoFragment
    }
  }
  __typename
}
${seriesFragment}
${photoFragment}
`

export const seriesWithTag = gql`
query seriesWithTag($tag: String!, $limit: Int, $nextToken: String) {
  tagSeries(tag: $tag, limit: $limit, nextToken: $nextToken) {
    seriesSummaries{
      ...seriesSummaryFragment
    }
    nextToken
    __typename
  }
  __typename
}
${seriesSummaryFragment}
`

export const tagSearch = gql`
query tagSearch($limit: Int, $tag: String) {
  tags(limit: $limit, tag: $tag ){
    _id
    name
    count
    __typename
  }
  __typename
}
`

export const geoSeries = gql`
query seriesInGeoBounds($bounds: [Float], $limit: Int, $nextToken: String) {
  seriesInGeoBounds(bounds: $bounds, limit: $limit, nextToken: $nextToken) {
    seriesSummaries {
      ...seriesSummaryFragment
    }
    nextToken
    __typename
  }
  __typename
}
${seriesSummaryFragment}
`

export const getUser = gql`
query getUser($userId: ID) {
  user(id: $userId){
    ...userFragment
  }
  __typename
}
${userFragment}
`

export const geocode = gql`
query geocode($search: String!) {
  geocoder(search: $search) {
    relevance
    text
    placeName
    bbox
    center
    # geoType
    # context
    __typename
  }
  __typename
}
`
export const getMyFeed = gql`
query myFeed($start: String, $end: String, $limit: Int) {
  myFeed(start: $start, end: $end, limit: $limit) @connection(key: "myFeed"){
    feed {
      _id
      series {
        ...seriesSummaryFragment
      }
      photo {
        ...photoSummaryFragment
      }
      relation
      __typename
    }
    window
    __typename
  }
  __typename
}
${seriesSummaryFragment}
${photoSummaryFragment}
`

export const userSearch = gql`
query userSearch($searchStr: String!, $limit: Int) {
  userSearch(searchStr: $searchStr, limit: $limit ){
    ...userFragment
  }
  __typename
}
${userFragment}
`

export const getWholeSeries = gql`
query getWholeSeries($id: ID!) {
  series(id: $id) {
    ...seriesFragment
    photos {
      ...photoFragment
    }
  }
  __typename
}
${seriesFragment}
${photoFragment}
`
