import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import Paper from '@material-ui/core/Paper'
import TextField from '@material-ui/core/TextField'
import Typography from '@material-ui/core/Typography'
import MenuItem from '@material-ui/core/MenuItem'

import Select from 'react-select'
import CreatableSelect from 'react-select/lib/Creatable'
import { makeStyles } from '@material-ui/core/styles'

import TagChip from '../tags/TagChip'
import style from './TagControl.style'
const useStyles = makeStyles(style)

/**
 * Just to be clear: the state here is being stored as []
 */
const TagControl = ({
  tags, onChange,
  hint, tagConfig, className
}) => {
  const classes = useStyles()
  // console.log('TagControl Rnder', { tags, tagConfig })
  const onSelectChange = (inputValue, { action, removedValue, option }) => {
    // If this was the last tag then close the menu
    switch (action) {
      case 'clear':
        onChange([])
        break
      case 'remove-value':
        // Remember we need to potentially remove it from the old AND the new
        onChange(tags.filter(val => val.id !== removedValue.value))
        break
      case 'select-option':
        const newVal = {
          ...tagConfig.values.find(tcv => tcv.id === option.value),
          typeId: tagConfig.id
        }
        onChange([...tags, newVal])
        break
      case 'create-option':
        const newLabel = inputValue.find(v => v.__isNew__).value
        const newCreateVal = {
          label: newLabel,
          typeId: tagConfig.id
        }
        onChange([...tags, newCreateVal])
        break
      default:
        break
    }
  }

  const SingleValue = ({ removeProps, innerProps, data, ...props }) => {
    // console.log('SingleValue PROPS', { removeProps, innerProps, data, props })
    return (
      <TagChip tag={data} onDelete={(e) => {
        // console.log('EVENT', e)
        removeProps.onClick()
      }} color={tagConfig.color} edit />
    )
  }
  SingleValue.propTypes = {
    removeProps: PropTypes.any,
    innerProps: PropTypes.any,
    data: PropTypes.any
  }

  const MultiValue = ({ removeProps, innerProps, data, ...props }) => {
    // console.log('MultiValue PROPS', { removeProps, innerProps, data, props })
    return (
      <TagChip tag={data} onDelete={(e) => {
        // console.log('EVENT', e)
        removeProps.onClick()
      }} color={tagConfig.color} edit />
    )
  }
  MultiValue.propTypes = {
    removeProps: PropTypes.any,
    innerProps: PropTypes.any,
    data: PropTypes.any
  }

  // Filter to just this tag type
  const tagValues = tags.filter(tag => tag.typeId === tagConfig.id && !tag.coords)
  // Now format it as {label, value: id} for react-select
  const selectOptions = tagValues.map(val => ({ label: val.label, value: val.id }))

  // Filter out things we haven't chosen yet
  // Also transform the options into a label/value pair for react-select
  const remainingTags = tagConfig.values
    .filter(val => tagValues.map(t => t.id).indexOf(val.id) === -1)
    .map(val => ({ label: val.label, value: val.id }))

  // console.log('TAG DEBUG', { value, tagValues, tagConfig })

  return (
    <div className={classNames(classes.root, className)}>
      <Typography variant='overline' className={classes.fieldTitle}>
        {tagConfig.label}
      </Typography>
      {!tagConfig.allowAdditions && (
        <Select
          options={remainingTags}
          classes={classes}
          isFocused
          components={{
            Control,
            Menu,
            //   MultiValue,
            NoOptionsMessage,
            Option,
            Placeholder,
            //   SingleValue,
            ValueContainer
          }}
          deleteRemoves
          value={selectOptions}
          isClearable={false}
          onChange={onSelectChange}
          placeholder='Select Tags'
          isMulti
          isSearchable
        />
      ) }
      { tagConfig.allowAdditions && (
        <CreatableSelect
          options={remainingTags}
          classes={classes}
          isFocused
          deleteRemoves
          components={{
            Control,
            Menu,
            //   MultiValue,
            NoOptionsMessage,
            Option,
            Placeholder,
            //   SingleValue,
            ValueContainer
          }}
          value={selectOptions}
          isClearable={false}
          onChange={onSelectChange}
          placeholder='Select Tags'
          isMulti
          isSearchable
        />
      )}
      {hint && <Typography variant='caption' className={classes.hint}>{hint}</Typography>}
    </div>
  )
}

TagControl.propTypes = {
  className: PropTypes.string,
  hint: PropTypes.string,
  tags: PropTypes.array,
  onChange: PropTypes.func.isRequired,
  tagConfig: PropTypes.object
}

export default TagControl

function Control (props) {
  // console.log('CONTROL PROPS', props)
  return (
    <TextField
      fullWidth
      InputProps={{
        inputComponent,
        inputProps: {
          className: props.selectProps.classes.input,
          inputRef: props.innerRef,
          children: props.children,
          // innerProps: {
          onMouseDown: (e) => {
            props.innerProps.onMouseDown(e)
          },
          onTouchEnd: (e) => {
            props.innerProps.onTouchEnd(e)
          }
          // }
          // ...props.innerProps
        }
      }}
      {...props.selectProps.textFieldProps}
    />
  )
}

const NoOptionsMessage = ({ innerProps, selectProps, children }) => (
  <Typography
    color='textSecondary'
    className={selectProps.classes.noOptionsMessage}
    {...innerProps}
  >
    {children}
  </Typography>
)

const Menu = ({ selectProps, innerProps, children }) => (
  <Paper square className={selectProps.classes.paper} {...innerProps}>
    {children}
    {/* {console.log('MENU PROPS', { selectProps, innerProps, children })} */}
  </Paper>
)

const inputComponent = ({ inputRef, ...props }) => <div ref={inputRef} {...props} />

const Placeholder = (props) => (
  <Typography
    color='textSecondary'
    className={props.selectProps.classes.placeholder}
    {...props.innerProps}
  >
    {props.children}
  </Typography>
)

const Option = ({ innerRef, isFocused, isSelected, innerProps, children }) => (
  <MenuItem
    buttonRef={innerRef}
    selected={isFocused}
    component='div'
    style={{ fontWeight: isSelected ? 500 : 400 }}
    {...innerProps}
  >
    {children}
  </MenuItem>
)

const ValueContainer = ({ selectProps, children }) => (
  <div className={selectProps.classes.valueContainer}>{children}</div>
)
ValueContainer.propTypes = {
  selectProps: PropTypes.any,
  children: PropTypes.any
}
