import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import {
  TextField,
  Typography
} from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import { timestampToHTML5Input, HTML5InputToTimestamp } from '../../../../lib/datelib'

const style = theme => ({
  root: {
  },
  rootEdit: {
  },
  formControl: {
    width: '100%'
  },
  textBody: {
    whiteSpace: 'pre-line'
  },
  actionButtons: {
    textAlign: 'right'
  }
})
const useStyles = makeStyles(style)

const DateControl = ({
  label,
  value,
  hint,
  onChange,
  className
}) => {
  const classes = useStyles()
  return (
    <div className={classNames(classes.root, className)}>
      <Typography variant='overline' className={classes.fieldTitle}>
        {label}
      </Typography>
      <TextField
        type='datetime-local'
        fullWidth
        value={timestampToHTML5Input(value)}
        className={classes.textField}
        onChange={(e) => onChange(HTML5InputToTimestamp(e.target.value))}
      />
      {hint && <Typography variant='caption' className={classes.hint}>{hint}</Typography>}
    </div>
  )
}

DateControl.propTypes = {
  className: PropTypes.string,
  label: PropTypes.string.isRequired,
  hint: PropTypes.string,
  value: PropTypes.string, // all timestamps come in as integerstrings
  onChange: PropTypes.func.isRequired
}

export default DateControl
