import React, { useState } from 'react'
import PropTypes from 'prop-types'
// import Typography from '@material-ui/core/Typography'
import DialogContent from '@material-ui/core/DialogContent'
import Typography from '@material-ui/core/Typography'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import DialogTitle from '@material-ui/core/DialogTitle'
import IconButton from '@material-ui/core/IconButton'

import CloseIcon from '@material-ui/icons/Close'
import TagControl from '../controls/TagControl'

import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
  },
  dlgContent: {
    // position: 'relative',
    // maxWidth: '200px',
    overflowY: 'visible!important',
    paddingBottom: 300
    // overflowX: 'hidden'
  },
  title: {
    width: '100%',
    margin: 0,
    display: 'flex',
    padding: theme.spacing(2)
  },
  dlgTitle: {
    flexGrow: 1
  },
  closeButton: {
    color: theme.palette.grey[500]
  }
})
const useStyles = makeStyles(style)

const TagEdit = ({ open, title, onOk, onCancel, tags, tagConfig }) => {
  const classes = useStyles()
  const [value, setValue] = useState(tags)
  return (
    <Dialog
      className={classes.root}
      scroll='body'
      fullScreen
      open={open}
      onClose={() => onOk(value)}
    >
      <DialogTitle className={classes.title} disableTypography>
        <Typography className={classes.dlgTitle}>{title}</Typography>
        <IconButton className={classes.closeButton} onClick={onCancel}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dlgContent}>
        <TagControl
          className={classes.field}
          tags={value}
          tagConfig={tagConfig}
          onChange={setValue}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={onCancel}>Cancel</Button>
        <div style={{ flexGrow: 1 }} />
        <Button onClick={() => setValue(tags)}>Reset</Button>
        <Button onClick={() => onOk(value)}>Ok</Button>
      </DialogActions>
    </Dialog>
  )
}

TagEdit.propTypes = {
  open: PropTypes.bool,
  title: PropTypes.string,
  onOk: PropTypes.func,
  onCancel: PropTypes.func,
  tags: PropTypes.array.isRequired,
  tagConfig: PropTypes.object.isRequired
}

export default TagEdit
