import React from 'react'
import PropTypes from 'prop-types'

import Typography from '@material-ui/core/Typography'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    padding: '10%',
    display: 'flex',
    flexDirection: 'column',
    opacity: 0.3,
    textAlign: 'center'
  },
  icon: {
    width: '100%',
    flex: '1 1 40%',
    color: theme.palette.primary.light,
    opacity: 0.5
  },
  primary: {
    flex: '1 1 30%',
    width: '100%'
  },
  secondary: {
    flex: '1 1 30%',
    width: '100%'
  }
}))

const SearchPlaceholder = ({ icon, primary, secondary }) => {
  const classes = useStyles()
  const PlaceholderIcon = icon
  return (
    <div className={classes.root}>
      <PlaceholderIcon className={classes.icon} />
      {primary && (
        <Typography className={classes.primary} variant='h3'>
          {primary}
        </Typography>
      )}
      {secondary && (
        <Typography className={classes.secondary} variant='h4'>
          {secondary}
        </Typography>
      )}
    </div>
  )
}

SearchPlaceholder.propTypes = {
  icon: PropTypes.object,
  primary: PropTypes.string,
  secondary: PropTypes.string
}

export default SearchPlaceholder
