import React, { useState, useMemo } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import DialogContent from '@material-ui/core/DialogContent'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import DialogTitle from '@material-ui/core/DialogTitle'
import FormLabel from '@material-ui/core/FormLabel'
import Slider from '@material-ui/lab/Slider'

import MapDot from '../../../maps/MapDot'
import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {

  },
  dlgContent: {
    display: 'flex',
    flexDirection: 'column',
    padding: 0
  },
  mapWrapper: {
    flexGrow: 1
  },
  sliderWrapper: {
    padding: '20px'
  },
  slider: {

  },
  sliderRoot: {

  },
  body: {
    overflow: 'hidden',
    whiteSpace: 'pre-line',
    paddingBottom: theme.spacing(1)
  },
  title: {
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
    margin: 0,
    padding: theme.spacing(2)
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500]
  }
})
const useStyles = makeStyles(style)

const MapDirPicker = ({ open, onOk, onCancel, title, latitude: initLat, longitude: initLong, direction: initDir }) => {
  const classes = useStyles()
  const [{ longitude, latitude }, setCoords] = useState({ longitude: initLong, latitude: initLat })
  const [direction, setRotation] = useState(initDir)

  const initViewport = useMemo(() => ({
    zoom: 18,
    latitude: initLat,
    longitude: initLong
  }), [initLat, initLong])

  const mapChange = ({ viewport: { longitude, latitude } }) => setCoords({ longitude, latitude })

  return (
    <Dialog
      scroll='paper'
      open={open}
      fullWidth
      fullScreen
      onClose={onCancel}
    >
      <DialogTitle className={classes.title} disableTypography>
        <Typography color='inherit'>{title}</Typography>
      </DialogTitle>
      <DialogContent className={classes.dlgContent}>
        <div className={classes.mapWrapper}>
          <MapDot
            editable
            className={classes.map}
            direction={direction > 0 ? direction : null}
            viewport={initViewport}
            onChange={mapChange}
          />
        </div>
        <div className={classes.sliderWrapper}>
          <FormLabel className={classes.mapFormLabel}>Direction {direction > -1 ? '' : '(off)'}</FormLabel>
          <Slider
            classes={{
              root: classes.sliderRoot
            }}
            value={direction || -1}
            min={-1}
            max={360}
            step={1}
            onChange={(e, value) => setRotation(value)}
          />
        </div>
      </DialogContent>
      <DialogActions>
        <Button onClick={onCancel}>Cancel</Button>
        <div style={{ flexGrow: 1 }} />
        <Button onClick={() => onOk({ longitude, latitude, direction })}>Ok</Button>
      </DialogActions>
    </Dialog>
  )
}

MapDirPicker.propTypes = {
  open: PropTypes.bool,
  latitude: PropTypes.number,
  longitude: PropTypes.number,
  direction: PropTypes.number,
  onOk: PropTypes.func,
  onCancel: PropTypes.func,
  title: PropTypes.string
}

export default MapDirPicker
