import moment from 'moment'

/**
 * Takes a UTC unix timestamp integer or string (milliseconds)
 * and a valid moment format string and returns a formatted
 * date string
 * @param {*} utctimestamp
 * @param {*} format
 */
export const formatDate = (utctimestamp, format = 'LLLL') => {
  return moment(utctimestamp, 'x').format(format)
}

export const formatDatAgo = (utctimestamp) => {
  return moment(utctimestamp, 'x').fromNow()
}

/**
 * Takes a UTC timestamp and returns a moment object in LOCAL mode
 * @param {*} utctimestamp
 */
export const parseDate = (utctimestamp) => {
  return moment(utctimestamp, 'x')
}

/**
 * This returns the object in LOCAL mode
 * @param {*} jsDate
 */
export const convertJSDatetoTimestamp = (jsDate) => {
  return moment
    .utc(jsDate)
    .format('x')
}

/**
 *
 * @param {*} utctimestamp
 */
export function timestampToHTML5Input (utctimestamp) {
  // // trim the Z and milliseconds + seconds
  return moment(utctimestamp, 'x').format('YYYY-MM-DDTkk:mm')
}

/**
 * Returns an integer timestamp
 * @param {*} isoStr
 */
export function HTML5InputToTimestamp (isoStr) {
  return moment(new Date(isoStr), 'YYYY-MM-DDTkk:mm').format('x')
}
