import React, { useState } from 'react'
import PropTypes from 'prop-types'
import SpeedDial from '@material-ui/lab/SpeedDial'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction'
// import MapLayersIcon from '@material-ui/icons/Layers'
import { makeStyles, useTheme } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    zIndex: 10,
    position: 'absolute'
  },
  speedDialFab: {
    border: '2px solid',
    background: theme.palette.text.primary,
    color: theme.palette.primary.contrastText,
    width: 40,
    height: 40
  },
  speedDialAction: {
    border: '2px solid',
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
    marginTop: 0,
    marginBottom: 0
  },
  speedDialIcon: {
    // border: '1px solid red'
  },
  speedDialLabel: {
    background: 'black',
    color: 'white'
  },
  actionsClosed: {
    display: 'none'
  }
})

const useStyles = makeStyles(style)

const StyleButtons = ({ controlPadding, currStyle, mapStyles, setCurrStyle }) => {
  const [open, setOpen] = useState(false)
  const classes = useStyles()
  const theme = useTheme()
  const noEventSetOpen = (e, state) => {
    console.log('noEventSetOpen', e, state)
    e.preventDefault()
    e.stopPropagation()
    setOpen(state)
  }

  if (!mapStyles || !Array.isArray(mapStyles) || mapStyles.length < 2 || !mapStyles[currStyle]) return null

  const currStyleObj = mapStyles[currStyle]
  const ActiveIcon = currStyleObj.ActiveIcon

  return (
    <div className={classes.root} style={{
      top: controlPadding.top ? controlPadding.top : theme.spacing(2),
      right: controlPadding.right ? controlPadding.right : theme.spacing(2)
    }}>
      {mapStyles.length > 1 && (
        <SpeedDial
          ariaLabel='SpeedDial example'
          className={classes.speedDial}
          classes={{
            fab: classes.speedDialFab,
            actionsClosed: classes.actionsClosed
          }}
          icon={<ActiveIcon className={classes.icon} />}
          // onBlur={(e) => noEventSetOpen(e, false)}
          onClick={(e) => noEventSetOpen(e, !open)}
          onClose={(e) => noEventSetOpen(e, false)}
          // onFocus={(e) => noEventSetOpen(e, true)}
          // onMouseEnter={(e) => noEventSetOpen(e, true)}
          // onMouseLeave={(e) => noEventSetOpen(e, false)}
          open={open}
          direction='left'
        >

          {mapStyles.map(({ Icon, ActiveIcon, toolTip }, idx) => (
            <SpeedDialAction
              key={`stylebutton${idx}`}
              // Bit of a hack here. This function isn't quite ready for primetime
              icon={<Icon className={classes.speedDialIcon} />}
              tooltipPlacement='bottom'
              ButtonProps={{
                classes: {
                  root: classes.speedDialAction
                }
              }}
              TooltipClasses={{
                tooltip: classes.speedDialLabel
              }}
              tooltipTitle={toolTip}
              tooltipOpen
              onClick={() => {
                console.log('SET', idx)
                setCurrStyle(idx)
                setOpen(false)
              }}
            />
          ))}

        </SpeedDial>
      ) }
    </div>
  )
}

StyleButtons.propTypes = {
  // controlPadding: {top, right, bottom, left}
  controlPadding: PropTypes.object,
  currStyle: PropTypes.number,
  mapStyles: PropTypes.array,
  setCurrStyle: PropTypes.func
}
StyleButtons.defaultProps = {
  controlPadding: {}
}

export default StyleButtons
