import React, { useState, useContext, useEffect } from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'

import GridList from '@material-ui/core/GridList'
import GridListTile from '@material-ui/core/GridListTile'
import GridListTileBar from '@material-ui/core/GridListTileBar'
import Snackbar from '@material-ui/core/Snackbar'
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab'

import AddIcon from '@material-ui/icons/Add'
import RemoveIcon from '@material-ui/icons/Remove'

import { SeriesContext } from '../../contexts'
import RawImage from './RawImage'

import { makeStyles } from '@material-ui/core/styles'
import style from './PhotoGrid.style'
import DeleteDialog from '../dialogs/DeleteDialog'
import { formatDate } from '../../../../lib/datelib'

const useStyles = makeStyles(style)

const PhotoGrid = ({ photos, columns, multiSelect, onDelete, onTileClick }) => {
  const [selectMode, setSelectMode] = useState(false)
  const classes = useStyles()
  const [selectedItems, setSelectedItems] = useState([])
  const [urls, setUrls] = useState({})
  const [deleteConfirm, setDeleteConfirm] = useState(false)

  const { seriesConfig, ac } = useContext(SeriesContext)

  const handleAddToSelection = (e, id) => {
    e.preventDefault()
    e.stopPropagation()

    setSelectMode(true)
    toggleListItem(id)
  }

  const handleCancelSelection = () => {
    setSelectMode(false)
    setSelectedItems([])
  }

  const onDeleteConfirm = (confirmation) => {
    setDeleteConfirm(false)
    if (confirmation === true && selectedItems.length < photos.length) onDelete(selectedItems)
    handleCancelSelection()
  }

  const toggleListItem = (id) => {
    const index = selectedItems.indexOf(id)
    if (index < 0) setSelectedItems([...selectedItems, id])
    else {
      const remaining = selectedItems.filter(arrId => arrId !== id)
      if (remaining.length === 0) handleCancelSelection()
      setSelectedItems(remaining)
    }
  }

  useEffect(() => {
    photos.forEach(async ph => {
      setUrls({ ...urls, [ph.id]: await seriesConfig.getPhotoUrl(ph, 'thumb') })
    })
  }, [photos])

  const handlePhotoTap = (e, photo) => {
    e.preventDefault()
    e.stopPropagation()
    // Select / deselect the item in edit mode
    if (selectMode) toggleListItem(photo.id)
    // navigate if tileClick selected
    else onTileClick(photo.id)
  }

  const sortedPhotos = [...photos]
  sortedPhotos.sort((a, b) => parseInt(b.takenOn, 10) - parseInt(a.takenOn, 10))

  return (
    <div>
      <GridList cols={columns} cellHeight={200} className={classes.root}>
        {sortedPhotos.map((photo, idx) => {
          const selected = selectedItems.indexOf(photo.id) > -1

          const canDelete = ac.canUser('delete', photo, 'photo')

          return (
            <GridListTile
              key={`gridTile${idx}`}
              onClick={(e) => canDelete && handlePhotoTap(e, photo)}
              className={classNames(
                classes.tile,
                selectMode && !canDelete && classes.notDeleteable,
                selectMode && canDelete && !selected && classes.unSelectedTile,
                selectMode && selected && classes.selectedTile
              )}
            >
              <RawImage
                photo={photo}
                size='medium'
                cover
                getPhotoUrl={seriesConfig.getPhotoUrl}
                className={classes.img}
              />

              {canDelete && multiSelect && (
                <Fab size='small' aria-label='Add' className={classes.addFab} onClick={(e) => handleAddToSelection(e, photo.id)}>
                  {!selectMode || !selected ? <AddIcon /> : <RemoveIcon />}
                </Fab>
              )}
              <GridListTileBar
                title={<span>{formatDate(photo.takenOn)}</span>}
              />
            </GridListTile>
          )
        })}
      </GridList>
      <Snackbar
        open={selectMode}
        autoHideDuration={null}
        ContentProps={{
          'aria-describedby': 'snackbar-fab-message-id',
          className: classes.snackbarContent
        }}
        message={<span id='snackbar-fab-message-id'>
          {selectedItems.length >= photos.length ? 'You can\'t delete all the photos. delete the series instead.' : `${selectedItems.length} Photos selected`}
        </span>}
        action={<div>
          <Button color='inherit' disabled={selectedItems.length >= photos.length} size='small' onClick={() => setDeleteConfirm(true)}>
                Delete
          </Button>
          <Button color='inherit' size='small' onClick={() => handleCancelSelection()}>
                Cancel
          </Button>
        </div>
        }
        className={classes.snackbar}
      />

      <DeleteDialog
        open={deleteConfirm}
        onConfirm={onDeleteConfirm}
        title={`Delete ${selectedItems.length} Photos`}
        message='Are you sure you want to delete these photos?'
      />
    </div>

  )
}

PhotoGrid.propTypes = {
  photos: PropTypes.array.isRequired,
  columns: PropTypes.number,

  multiSelect: PropTypes.bool,
  onDelete: PropTypes.func,
  onTileClick: PropTypes.func.isRequired
}
PhotoGrid.defaultProps = {
  multiSelect: true,
  columns: 2
}

export default PhotoGrid
