import React, { useState, useRef, useEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import SpeedDial from '@material-ui/lab/SpeedDial'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction'
import Fab from '@material-ui/core/Fab'
import { fade } from '@material-ui/core/styles/colorManipulator'

// import useNewImageMenu from '../PhotoSeries/views/controls/useNewImageMenu'
import AddCameraIcon from '@material-ui/icons/AddAPhoto'
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => {
  const pulseCol = fade(theme.palette.secondary.light, 0.4)
  return {
    root: {
    },
    speedDialFab: {
      background: theme.palette.secondary.main,
      color: theme.palette.secondary.contrastText,
      '&::before': {
        content: '"  "',
        display: 'block',
        position: 'absolute',
        width: '100%',
        height: '100%',
        borderRadius: 'inherit',
        boxShadow: `0 0 0 0 ${pulseCol}`,
        animation: '$fabPulse 4s',
        animationIterationCount: 5,
        animationTimingFunction: 'linear'
      }
    },
    '@keyframes fabPulse': {
      '0%': {
        boxShadow: `0 0 0 0px ${pulseCol}`
      },
      '90%': {
        boxShadow: `0 0 0 0px ${pulseCol}`
      },
      '100%': {
        boxShadow: `0 0 0 10px transparent`
      }
    },
    directionUp: {
    },
    actionsClosed: {
      display: 'none'
    }
  }
})

const AddFab = ({ className, children, onCameraClick, onLibraryClick, isIos }) => {
  const [open, setOpen] = useState(false)
  const [down, setDown] = useState(false)
  const timerRef = useRef()
  const classes = useStyles()
  // const theme = useTheme()
  const downRef = useRef(down)
  const elRef = useRef()
  downRef.current = down

  // Make sure to clean up the timer
  useEffect(() => () => {
    // clean up the timer if we no longer need it
    timerRef.current = null
    downRef.current = null
  }, [])

  const startTimer = (el) => {
    if (!elRef.current) return

    setDown(true)
    if (!open && !timerRef.current) {
      timerRef.current = setTimeout(() => {
        if (downRef.current) setOpen(true)
        timerRef.current = null
      }, 300)
    }
  }

  const onDown = (e) => {
    // console.log('onDown', e.currentTarget)
    // setSeries(series)
    startTimer(e.currentTarget)
  }

  const block = (e) => {
    if (e && e.stopPropagation) {
      e.stopPropagation()
      e.preventDefault()
    }
  }

  const cameraAction = (e) => {
    console.log('cameraclick')
    setOpen(false)
    setDown(false)
    onCameraClick()
    block(e)
  }
  const libraryAction = (e) => {
    console.log('libraryClick')
    setOpen(false)
    setDown(false)
    onLibraryClick()
    block(e)
  }

  if (isIos) return <AddFabIos {...{ className, onLibraryClick, children }} />

  return (
    <div className={classNames(classes.rootContainer, className)} ref={elRef}>
      <SpeedDial
        ariaLabel='SpeedDial photo adder'
        onBlur={(e) => {
          setTimeout(() => setOpen(false), 250)
        }}
        ButtonProps={{
          disableFocusRipple: true
        }}
        classes={{
          directionUp: classes.directionUp,
          actionsClosed: classes.actionsClosed,
          root: classes.root,
          fab: classNames(classes.speedDialFab, 'fabanimation')
        }}
        icon={children}
        onClick={(e) => {
          block(e)
          setDown(false)
          if (!open) {
            console.log('cameraclick MAIN')
            onCameraClick()
          }
        }}
        onMouseDown={onDown}
        // onMouseUp={onUp}
        onTouchStart={onDown}
        // onTouchEnd={onUp}

        open={open}
        direction='up'
      >
        <SpeedDialAction
        // Bit of a hack here. This function isn't quite ready for primetime
          icon={<AddCameraIcon className={classes.speedDialIcon} />}
          tooltipPlacement='right'
          tooltipTitle='From Camera'
          tooltipOpen
          ButtonProps={{
            disableFocusRipple: true
          }}
          onClick={cameraAction}
          onMouseDown={onDown}
          onTouchStart={cameraAction}
        />
        <SpeedDialAction
        // Bit of a hack here. This function isn't quite ready for primetime
          icon={<PhotoLibraryIcon className={classes.speedDialIcon} />}
          tooltipPlacement='right'
          tooltipTitle='From Photo Library'
          tooltipOpen
          ButtonProps={{
            disableFocusRipple: true
          }}
          onClick={libraryAction}
          onMouseDown={onDown}
          onTouchStart={libraryAction}
        />
      </SpeedDial>
    </div>

  )
}

AddFab.propTypes = {
  className: PropTypes.string,
  children: PropTypes.any,
  isIos: PropTypes.bool,
  onCameraClick: PropTypes.func.isRequired,
  onLibraryClick: PropTypes.func.isRequired
}

export default AddFab

const useIosStyles = makeStyles(theme => {
  const pulseCol = fade(theme.palette.secondary.light, 0.4)
  return {
    root: {
      background: theme.palette.secondary.main,
      color: theme.palette.secondary.contrastText,
      '&::before': {
        content: '"  "',
        display: 'block',
        position: 'absolute',
        width: '100%',
        height: '100%',
        borderRadius: 'inherit',
        boxShadow: `0 0 0 0 ${pulseCol}`,
        animation: '$fabPulse 4s',
        animationIterationCount: 5,
        animationTimingFunction: 'linear'
      }
    },
    '@keyframes fabPulse': {
      '0%': {
        boxShadow: `0 0 0 0px ${pulseCol}`
      },
      '90%': {
        boxShadow: `0 0 0 0px ${pulseCol}`
      },
      '100%': {
        boxShadow: `0 0 0 10px transparent`
      }
    }
  }
})

export const AddFabIos = ({ className, children, onLibraryClick }) => {
  const classes = useIosStyles()
  return (
    <Fab className={classNames(classes.root, className)} onClick={onLibraryClick} disableFocusRipple>
      {children}
    </Fab>
  )
}

AddFabIos.propTypes = {
  className: PropTypes.string,
  children: PropTypes.any,
  onLibraryClick: PropTypes.func.isRequired
}
