import { useState, useEffect, useCallback } from 'react'
import uuid from 'uuid/v4'
import WebMercatorViewport from 'viewport-mercator-project'
import rbush from 'rbush'
import { point, centroid, featureCollection } from '@turf/turf'
import { debounce } from './util'

const useClusteredData = (data, filterBounds, currentZoom, viewport, sizeScale, queryVersion) => {
  const [clusteredData, setClusteredData] = useState([])
  const [filteredData, setFilteredData] = useState({ single: [], cluster: [] })
  const [version, setVersion] = useState(0)

  // We promisify and debounce the expensive stuff so it runs outside our render loop
  const updateDataDebounce = useCallback(debounce(async (...innerArgs) => {
    const updatedData = await updateData(...innerArgs)
    setClusteredData(updatedData)
    // setClusteredData(updatedData)
    setVersion(version + 1)
  }, 1000), [])

  const filterDataDebounce = useCallback(debounce(async (...innerArgs) => {
    const filtered = await filterData(...innerArgs)
    setFilteredData(filtered)
  }, 300), [])

  // Here's the really expensive call. Make sure this only runs when the data changes
  useEffect(() => {
    updateDataDebounce(data, sizeScale, viewport)
  }, [data.length, queryVersion])

  useEffect(() => {
    filterDataDebounce(clusteredData, filterBounds, currentZoom)
  }, [clusteredData, currentZoom, filterBounds, version])

  return { filteredClusteredData: filteredData }
}

export default useClusteredData

// Compute icon clusters
// We use the projected positions instead of longitude and latitude to build
// the spatial index, because this particular dataset is distributed all over
// the world, we can't use some fixed deltaLon and deltaLat
const updateData = (data, sizeScale, viewport) => {
  // console.log('----------------------------------UPDATE CLUSTER----------------------------------', { data, sizeScale, viewport })

  if (!data) {
    return []
  }
  const tree = rbush(9, ['.x', '.y', '.x', '.y'])

  const transform = new WebMercatorViewport({
    ...viewport,
    zoom: 0
  })

  // console.log('DATA', data)
  // The root of this is GeoJSON so that's what we have to build
  const newData = data.map(p => {
    // Create some GeoJSON that we can render as a dot
    let screenCoords = [0, 0]
    try {
      screenCoords = transform.project(p.coords)
    }
    catch (e) {
      console.error(e)
    }
    const { longitude, latitude, ...properties } = p
    return {
      x: screenCoords[0],
      y: screenCoords[1],
      photoCount: properties.photoCount,
      zoomLevels: [],
      geoJSON: p.coords ? point(p.coords, { ...properties }) : null
    }
  })

  tree.clear()
  tree.load(newData)

  for (let z = 0; z <= 20; z++) {
    const radius = sizeScale / Math.sqrt(2) / Math.pow(2, z)
    // const radius = 60 / Math.sqrt(2) / Math.pow(2, z)

    newData.forEach(p => {
      if (p.zoomLevels[z] === undefined) {
        // this point does not belong to a cluster
        const { x, y } = p

        // find all points within radius that do not belong to a cluster
        const neighbors = tree
          .search({
            minX: x - radius,
            minY: y - radius,
            maxX: x + radius,
            maxY: y + radius
          })
          .filter(neighbor => neighbor.zoomLevels[z] === undefined)

        // only show the center point at this zoom level
        neighbors.forEach(neighbor => {
          if (neighbor === p) {
            const newGeoJSON = neighbors.length === 1
              ? p.geoJSON
              : centroid(featureCollection(neighbors.map(n => n.geoJSON)))

            newGeoJSON.properties = {
              ...newGeoJSON.properties,
              id: newGeoJSON.properties.id || uuid(),
              count: neighbors.length,
              reducedPhotoCount: neighbors.reduce((acc, { photoCount }) => photoCount ? acc + photoCount : acc, 0),
              points: neighbors.map(n => n.geoJSON)
            }
            p.zoomLevels[z] = newGeoJSON
          }
          else {
            neighbor.zoomLevels[z] = null
          }
        })
      }
    })
  }

  // console.log('----------------------------------UPDATE CLUSTER DONE----------------------------------')
  return newData.map(({ zoomLevels }) => zoomLevels)
}

function filterData (clusteredData, filterBounds, newZ) {
  // console.log('----------------------------------FILTER CLUSTER----------------------------------', { clusteredData, filterBounds, newZ })

  return clusteredData
    // Filter out anything not relevant to this zoom level
    .filter(d => !!d[newZ])
    // Restructure the object to be a little more JSON-friendly
    .reduce((acc, zoomLevels) => {
      const geoJSON = zoomLevels[newZ]
      const coords = geoJSON.geometry.coordinates
      const { count } = geoJSON.properties

      if (!filterBounds || filterBounds.length < 4 || (
        filterBounds[0] < coords[0] && filterBounds[1] < coords[1] &&
        filterBounds[2] > coords[0] && filterBounds[3] > coords[1]
      )) {
        if (count > 1) acc.cluster.push(geoJSON)
        else acc.single.push(geoJSON)
      }
      return acc
    }, {
      single: [],
      cluster: []
    })
}
