/**
 * Valid Roles are: admin, user, nonuser
 *
 * Valid Resources are: series, series_public, series_open, photo
 *
 * For more see:
 * https://docs.google.com/spreadsheets/d/1oXsZrsuyOSFs_tufRluSjQiR6OSTObWiGsKDUKb5vLk/edit#gid=0
 *
 * RTFM:
 * https://onury.io/accesscontrol/?content=guide#resources-and-resource-attributes
 */
export default {
  // Admin is god. Be careful who you give this to.
  admin: {
    series: {
      'create:own': ['*'],
      'read:any': ['*'],
      'update:any': ['*'],
      'delete:any': ['*']
    },
    photo: {
      'create:any': ['*'],
      'read:any': ['*'],
      'update:any': ['*'],
      'delete:any': ['*']
    }
  },
  // Logged out users can see public stuff but nothing esle
  nonuser: {
    photo: {
      'read:any': ['*'],
      'update:any': ['*'],
      'delete:any': ['*']
    },
    series_public: {
      'read:any': ['*']
    },
    series_open: {
      'read:any': ['*']
    }
  },
  // Users are the key permissions role in V0
  // Note: we could inherit from user but we keep it explicit for now
  user: {
    series: {
      'create:own': ['*'],
      'read:own': ['*'],
      'update:own': ['*'],
      'delete:own': ['*']
    },
    series_public: {
      'read:any': ['*']
    },
    series_open: {
      'read:any': ['*'],
      'update:any': ['photos']
    },
    photo: {
      'read:any': ['*'],
      'create:own': ['*'],
      'update:own': ['*'],
      'delete:own': ['*']
    }
  }
}
