import React, { useRef } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'

import { List } from 'react-virtualized' // InfiniteLoader
import useOnline from '../../lib/useOnline'
import FeedEnder from './FeedEnder'

import ErrorIcon from '@material-ui/icons/Error'
import { makeStyles } from '@material-ui/core/styles'
import FeedItem from './FeedItem'

import TopBar from '../TopBar'

import useComponentSize from '../../lib/useComponentSize'
import { CircularProgress } from '@material-ui/core'

const ROWHEIGHT = 300

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%'
  },
  listContainer: {
    flexGrow: 1,
    overflow: 'hidden'
  },
  vList: {
    height: '100%',
    width: '100%'
  },
  feedContainer: {
    flexGrow: 1,
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: 'translateZ(0)'
  },
  grow: {
    flexGrow: 1
  }
}))

const MyFeed = ({
  events, loading, onMapClick, onImageClick, onProfileClick,
  onSeriesClick, fetchNewest, fetchMore, window
}) => {
  const classes = useStyles()
  const listRef = useRef()
  const { size } = useComponentSize(listRef)
  const { online } = useOnline()

  const rowRenderer = ({
    index, // Index of row
    isScrolling, // The List is currently being scrolled
    isVisible, // This row is visible within the List (eg it is not an overscanned row)
    key, // Unique key within array of rendered rows
    parent, // Reference to the parent List (instance)
    style // Style object to be applied to row (to position it);
    // This must be passed through to the rendered row element.
  }) => {
    const row = events[index]
    let content
    if (index < events.length && row) {
      content = <FeedItem
        event={events[index]}
        onSeriesClick={onSeriesClick}
        onImageClick={onImageClick}
        onProfileClick={onProfileClick}
        onMapClick={onMapClick}
      />
    }
    else if (index === events.length) {
      // content = <div className={classes.placeholder} style={{ width: row.size }} />
      const wEnd = window && window[0] === 0
      content = <FeedEnder icon={<CircularProgress size={30} thickness={2} color='inherit' />} text='Loading...' />

      const almostAtEnd = ROWHEIGHT * 4 + parent.state.scrollTop > style.top

      if (!online) {
        content = <FeedEnder icon={<ErrorIcon />} text='Cannot Refresh Offline' />
      }
      else if (wEnd) {
        content = <FeedEnder text='End of feed' />
      }
      // Only if we're online, not loading and visible
      else if (!loading && almostAtEnd) {
        fetchMore()
      }
    }

    return (
      <div className={classes.row} key={key} style={style}>
        {content}
      </div>
    )
  }
  rowRenderer.propTypes = {
    index: PropTypes.any,
    key: PropTypes.any,
    style: PropTypes.any,
    isScrolling: PropTypes.any,
    isVisible: PropTypes.any,
    parent: PropTypes.any
  }

  // console.log('DEBUGLIST', size)

  return (
    <div className={classes.root}>
      <TopBar>
        <Typography className={classes.grow}>Home Feed</Typography>
      </TopBar>
      <div className={classes.listContainer} ref={listRef}>
        <List
          height={size.height}
          width={size.width}
          className={classes.vList}
          rowCount={events.length + 1}
          rowHeight={ROWHEIGHT}
          rowRenderer={rowRenderer}
        />
      </div>
    </div>
  )
}

MyFeed.propTypes = {
  events: PropTypes.array.isRequired,
  loading: PropTypes.bool,
  window: PropTypes.arrayOf(PropTypes.number),
  onSeriesClick: PropTypes.func.isRequired,
  onImageClick: PropTypes.func.isRequired,
  onProfileClick: PropTypes.func.isRequired,
  onMapClick: PropTypes.func.isRequired,
  fetchNewest: PropTypes.func.isRequired,
  fetchMore: PropTypes.func.isRequired
}

export default MyFeed
