import React, { useRef, useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import { makeStyles } from '@material-ui/core/styles'
import BrokenImg from './BrokenImg'

const useStyles = makeStyles(theme => ({
  root: {
    display: 'block',
    height: '100%',
    width: '100%'
  },
  img: {
    display: 'block',
    height: '100%',
    width: '100%'
  },
  imgBroken: {
    display: 'none'
  }
}))

export const RawImage = ({ className, getPhotoUrl, size, dataUrl, photo, cover, style, ...props }) => {
  const classes = useStyles()
  const [imgUrl, setImgUrl] = useState(dataUrl)
  const refImgWrap = useRef(null)
  const [urlRetryCounter, setURLRetryCounter] = useState(0)
  const [broken, setBroken] = useState(false)

  useEffect(() => {
    if (getPhotoUrl) {
      getPhotoUrl(photo, size || 'large').then(setImgUrl)
    }
  }, [photo])

  const urlWithRetries = urlRetryCounter > 0 ? `${imgUrl}?retry=${urlRetryCounter}` : imgUrl
  return (
    <div ref={refImgWrap} className={classNames(classes.root, className)}>
      {(!imgUrl || broken) && <BrokenImg loading={!broken} />}
      <img className={classNames(broken ? classes.imgBroken : classes.img)}
        alt={size || imgUrl}
        style={{
          ...style,
          objectFit: cover ? 'cover' : 'contain'
        }}
        onError={(err) => {
          if (urlRetryCounter < 10) {
            setBroken(true)
            console.log('Image Retry', urlRetryCounter, urlWithRetries)
            console.error(err)
            setTimeout(() => {
              if (refImgWrap.current) setURLRetryCounter(urlRetryCounter + 1)
            }, 4000)
          }
        }}
        // onLoad={onLoad}
        src={imgUrl}
        {...props}
      />
    </div>
  )
}

RawImage.propTypes = {
  className: PropTypes.string,
  size: PropTypes.string,
  getPhotoUrl: PropTypes.func,
  cover: PropTypes.bool,
  dataUrl: PropTypes.func,
  // onLoad: PropTypes.func,
  photo: PropTypes.object.isRequired,
  style: PropTypes.object
}

export default RawImage
