import React from 'react'
import { PersistGate } from 'redux-persist/integration/react'
import { Provider } from 'react-redux'
import storeConfig from './storeConfig'
import Loading from '../components/auth/Loading'
/**
 * In this example, 'store' is the configured Redux store and
 *'persist' is a function that returns a Promise to rehydrate the
 * Redux store using redux-persist
 *
 * FROM: https://medium.com/hexient-labs/redux-store-provider-hoc-3495281905c
 */
const withReduxProvider = BaseComponent => (props) => {
  const { store, persistor } = storeConfig()
  // console.log('withReduxProvider', { props, store, persistor })
  return (
    <Provider store={store}>
      <PersistGate loading={<Loading />} persistor={persistor}>
        <BaseComponent {...props} />
      </PersistGate>
    </Provider>
  )
}

export default withReduxProvider
