import React, { useEffect, useRef } from 'react'

// https://developers.google.com/web/fundamentals/media/capturing-images/
const useFileInput = (onChange = () => {}, anyFile = false) => {
  const inputRef = useRef()
  const clickRef = useRef()

  const element = (
    <input style={{ display: 'none' }} ref={inputRef} type='file' accept={!anyFile ? 'image/*' : null} />
  )

  const forceClick = () => {
    if (inputRef.current) inputRef.current.click()
  }

  useEffect(() => {
    const handleChange = evt => {
      const [file] = [...inputRef.current.files]
      if (file) {
        console.log('file', file)
        const reader = new FileReader()
        reader.onloadend = () => {
          const { type, name, size } = file
          onChange({ dataUrl: reader.result, type, name, size, ext: getExtFromFile(file) })
        }
        reader.readAsDataURL(file)
      }
      inputRef.current.value = null
    }

    if (inputRef.current) {
      inputRef.current.addEventListener('change', handleChange)
      if (!clickRef.current) {
        inputRef.current.click()
        clickRef.current = true
      }
    }
    return () => {
      if (inputRef.current) {
        inputRef.current.removeEventListener('change', handleChange)
      }
    }
  }, [inputRef.current])

  return { fileClickEl: element, forceClick }
}

export default useFileInput

const getExtFromFile = (fileBlob) => {
  switch (fileBlob.type) {
    case 'image/jpeg':
      return 'jpeg'
    case 'image/jpg':
      return 'jpeg'
    case 'image/png':
      return 'png'
    default:
      return 'jpg'
  }
}
