import { useRef, useEffect } from 'react'

const useAuthCommon = () => {
  const containerRef = useRef()
  const handleKeyPress = (event) => {
    if (event.key === 'Enter') {
      if (containerRef.current) {
        try {
          const found = containerRef.current.getElementsByClassName('enterAction')
          if (found && found[0]) found[0].click()
        }
        catch (e) {
          console.error('useAuthCommon::handleKeyPress', e)
        }
      }
    }
  }
  useEffect(() => {
    if (containerRef.current) {
      try {
        document.addEventListener('keypress', handleKeyPress, false)
      }
      catch (e) {
        console.error('useAuthCommon::useEffect', e)
      }
      return () => document.removeEventListener('keypress', handleKeyPress, false)
    }
  }, [])

  return containerRef
}

export default useAuthCommon
