import React, { useState } from 'react'
import Amplify, { Auth } from 'aws-amplify'
import { I18n } from '@aws-amplify/core' // ConsoleLogger as Logger
import countryDialCodes from 'aws-amplify-react/dist/Auth/common/country-dial-codes'
import {
  Authenticator,
  SignIn as SignInOrig,
  SignUp as SignUpOrig,
  ConfirmSignIn as ConfirmSignInOrig,
  ConfirmSignUp as ConfirmSignUpOrig,
  RequireNewPassword as RequireNewPasswordOrig,
  Loading as LoadingOrig,
  VerifyContact as VerifyContactOrig,
  ForgotPassword as ForgotPasswordOrig
} from 'aws-amplify-react'

import CompSignIn from '../components/auth/SignIn'
import CompConfirmSignIn from '../components/auth/ConfirmSignIn'
import CompRequireNewPassword from '../components/auth/RequireNewPassword'
import CompSignUp from '../components/auth/SignUp'
import CompLoading from '../components/auth/Loading'
import CompConfirmSignUp from '../components/auth/ConfirmSignUp'
// import CompVerifyContact from '../components/auth/VerifyContact'
import CompForgotPassword from '../components/auth/ForgotPassword' // or 'aws-amplify-react-native';
import { aws } from '../config'

Amplify.configure(aws)

const defaultAuthState = { authState: 'init', authData: {}, signedIn: null }

const useAuthentication = () => {
  const [{ authState, authData, signedIn }, setAuth] = useState(defaultAuthState)

  // Set up the trinary
  // if (authState === 'signedIn') signedIn = true

  const handleAuthStateChange = (state, data) => {
    // console.log('handleAuthStateChange', { state, data })
    setAuth({
      authState: state,
      authData: data,
      // true = signedIn, false = beAnonymous, null = showLoginScreen
      signedIn: !state || state.length === 0
        ? null
        : state === 'signedIn'
          ? true
          : signedIn
    })
  }

  const getCurrentToken = async () => {
    const currSession = await Auth.currentSession()
    console.log('here', currSession.accessToken)
    return currSession && currSession.accessToken ? currSession.accessToken : null
  }

  const signOut = () => Auth.signOut()
    .then(data => console.log(data))
    .catch(err => console.log(err))

  const beAnonymous = () => {
    setAuth({ authState, authData, signedIn: false })
  }

  const authScreen = signedIn ? null
    : <Authenticator onStateChange={handleAuthStateChange} hideDefault>
      <SignIn handleBeAnonymous={beAnonymous} />
      <Loading message='Loggin in' />
      <RequireNewPassword />
      {/* <RequireNewPasswordOrig /> */}
      <ConfirmSignIn />
      <ConfirmSignUp />
      {/* <VerifyContact /> NOT READY */}
      <VerifyContactOrig />
      <ForgotPassword />
      <SignUp />
      {/* <SignUpOrig /> */}
    </Authenticator>

  let isGod = false
  try {
    isGod = authData.signInUserSession.idToken.payload['cognito:groups'].indexOf('GodUsers') > -1
  }
  catch (e) {}

  return {
    authState,
    authData,
    isGod,
    signOut,
    getCurrentToken,
    forceSignIn: () => setAuth(defaultAuthState),
    signedIn,
    authScreen
  }
}

export default useAuthentication

class SignIn extends SignInOrig {
  showComponent () {
    const { loading } = this.state
    const newProps = {
      ...this.props,
      loading,
      handleForgotPassword: () => this.changeState('forgotPassword'),
      handleSignup: () => this.changeState('signUp'),
      handleInputChange: this.handleInputChange,
      handleSignIn: this.signIn
    }
    return <CompSignIn {...newProps} />
  }
}

class SignUp extends SignUpOrig {
  showComponent () {
    const { hide } = this.props
    if (hide && hide.includes(SignUp)) {
      return null
    }
    if (this.checkCustomSignUpFields()) {
      this.signUpFields = this.props.signUpConfig.signUpFields
    }
    this.sortFields()

    const newProps = {
      ...this.props,
      countryDialCodes,
      defaultDialCode: this.getDefaultDialCode(),
      handleInputChange: this.handleInputChange,
      signUpFields: this.signUpFields,
      signUpFieldLabels: this.signUpFields.map(field => I18n.get(field.label)),
      signUpFieldPlaceholders: this.signUpFields.map(field => I18n.get(field.placeholder)),
      handleSignup: this.signUp,
      handleBack: () => this.changeState('signIn')
    }
    return <CompSignUp {...newProps} />
  }
}

class ConfirmSignIn extends ConfirmSignInOrig {
  showComponent () {
    const newProps = {
      ...this.props,
      handleBack: () => this.changeState('signIn'),
      handleInputChange: this.handleInputChange,
      handleConfirm: this.confirm
    }
    return <CompConfirmSignIn {...newProps} />
  }
}

class ConfirmSignUp extends ConfirmSignUpOrig {
  showComponent () {
    const newProps = {
      ...this.props,
      handleBack: () => this.changeState('signIn'),
      handleInputChange: this.handleInputChange,
      handleConfirm: this.confirm,
      handleResend: this.resend
    }
    return <CompConfirmSignUp {...newProps} />
  }
}

class RequireNewPassword extends RequireNewPasswordOrig {
  showComponent () {
    const user = this.props.authData
    const { requiredAttributes } = user.challengeParam
    const placeholders = Array.isArray(requiredAttributes)
      ? requiredAttributes.map(attribute => I18n.get(convertToPlaceholder(attribute)))
      : []

    const newProps = {
      ...this.props,
      handleInputChange: this.handleInputChange,
      requiredAttributes,
      placeholders,
      change: this.change
    }
    return <CompRequireNewPassword {...newProps} />
  }
}

class Loading extends LoadingOrig {
  showComponent () {
    return <CompLoading {...this.props} />
  }
}
