class ForgotPassword extends ForgotPasswordOrig {
  showComponent () {
    const newProps = {
      ...this.props,
      handleBack: () => this.changeState('signIn'),
      handleInputChange: this.handleInputChange,
      handleConfirm: this.confirm,
      handleResend: this.send,
      handleSubmit: this.submit,
      handleSendCode: this.send
    }
    return <CompForgotPassword {...newProps} />
  }
}

// const verifyLogger = new Logger('VerifyContact')

function convertToPlaceholder (str) {
  return str.split('_').map(part => part.charAt(0).toUpperCase() + part.substr(1).toLowerCase()).join(' ')
}

