
import React, { useState } from 'react'
import PropTypes from 'prop-types'

import TextField from '@material-ui/core/TextField'
import FormLabel from '@material-ui/core/FormLabel'
import Typography from '@material-ui/core/Typography'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Switch from '@material-ui/core/Switch'

import { makeStyles } from '@material-ui/core/styles'
import MapDot from '../../../maps/MapDot'

import MapDirPicker from '../dialogs/MapDirPicker'
import BigTextEdit from '../dialogs/BigTextEdit'

import style from './SeriesEditInner.style'
import { ACCESS } from '../../../../config'
import ShortLongText from '../controls/ShortLongText'

const DIALOGS = {
  MAPPICK: 'mapPicker',
  NOTEEDIT: 'noteEdit'
}

const useStyles = makeStyles(style)

/**
 * SeriesEdit appears in two places so we need this to be a bit reusable
 */
const SeriesEditInner = ({ series, delta, onChange }) => {
  const classes = useStyles()
  const [activeDialog, setActiveDialog] = useState(null)

  const viewport = { zoom: 18 }
  if ((delta.latitude || series.coords[1]) && (delta.longitude || series.coords[0])) {
    viewport.longitude = delta.longitude || series.coords[0]
    viewport.latitude = delta.latitude || series.coords[1]
  }

  const access = delta.access || series.access || 'PRIVATE'
  // console.log('ACESS', access)
  return (
    <div>
      <TextField
        label='Series Name'
        fullWidth
        placeholder='New Series'
        className={classes.field}
        value={delta.name || series.name || ''}
        onChange={e => onChange({ ...delta, name: e.target.value })}
      />
      <div className={classes.mapWrapper} onClick={() => setActiveDialog(DIALOGS.MAPPICK)}>
        <FormLabel className={classes.mapFormLabel}>Location</FormLabel>
        <div className={classes.mapInnerWrap}>
          <MapDot
            staticMap
            className={classes.map}
            direction={delta.direction || series.direction}
            viewport={viewport}
          />
        </div>
      </div>
      <div className={classes.field}>
        <FormLabel>Series Access</FormLabel>
        <div className={classes.access}>
          <FormControlLabel
            className={classes.accessRadio}
            control={
              <Switch
                checked={['OPEN', 'PUBLIC'].indexOf(access) > -1}
                onChange={(e) => {
                  onChange({ ...delta, access: e.target.checked ? 'PUBLIC' : 'PRIVATE' })
                }}
                value={'PUBLIC'}
              />
            }
            label={<div>
              <Typography variant='body2' component='div'>{ACCESS.PUBLIC.label}</Typography>
              <Typography variant='caption' color='textSecondary' component='div'>{ACCESS.PUBLIC.description}</Typography>
            </div>}

          />
          <FormControlLabel
            className={classes.accessRadio}
            disabled={['PRIVATE'].indexOf(access) > -1}
            control={
              <Switch
                checked={['OPEN'].indexOf(access) > -1}
                onChange={(e) => onChange({ ...delta, access: e.target.checked ? 'OPEN' : 'PUBLIC' })}
                value={'OPEN'}
              />
            }
            label={<div>
              <Typography variant='body2' component='div'>{ACCESS.OPEN.label}</Typography>
              <Typography variant='caption' color='textSecondary' component='div'>{ACCESS.OPEN.description}</Typography>
            </div>}
          />
        </div>
      </div>

      <ShortLongText
        onClick={() => setActiveDialog(DIALOGS.NOTEEDIT)}
        className={classes.field}
        label='Series Note'
        text={delta.note || series.note}
      />
      {activeDialog === DIALOGS.MAPPICK && (
        <MapDirPicker
          open
          title='Series Location & Direction'
          latitude={delta.latitude || series.coords[1]}
          longitude={delta.longitude || series.coords[0]}
          direction={delta.direction || series.direction}
          onOk={(data) => {
            onChange({ ...delta, ...data })
            setActiveDialog(null)
          }}
          onCancel={() => setActiveDialog(null)}
        />
      )}
      {activeDialog === DIALOGS.NOTEEDIT && (
        <BigTextEdit
          open
          title='Series Note'
          body={delta.note || series.note}
          multiline
          onOk={(newNote) => {
            onChange({ ...delta, note: newNote })
            setActiveDialog(null)
          }}
          onCancel={() => setActiveDialog(null)}
        />
      )}
    </div>
  )
}

SeriesEditInner.propTypes = {
  // now some functions
  series: PropTypes.object.isRequired,
  delta: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired
}

export default SeriesEditInner
