import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import SeriesView from './views/SeriesView'
import SeriesEdit from './views/SeriesEdit'
import Slideshow from './views/Slideshow'
import PhotoViewTool from './views/PhotoViewTool'
import NewPhoto from './views/NewPhoto'
import AccesHelper from '../../lib/AccessHelper'

import { SeriesContext } from './contexts'
import SeriesNotFound from './views/SeriesNotFound'
import SeriesLoading from './views/SeriesLoading'

export const MODES = {
  SERIES_VIEW: 'series',
  SERIES_EDIT: 'editseries',
  NEWPHOTO: 'newphoto',
  NEWSERIES: 'newseries',
  REVIEW: 'review',
  PHOTO_VIEW: 'photoviewer',
  PHOTO_VIEW_NEW: 'newphotoviewer',
  SLIDESHOW: 'slideshow'
}
const VIEW_ONLY_MODES = [MODES.SERIES_VIEW, MODES.PHOTO_VIEW, MODES.SLIDESHOW]
const VALID_STARTMODES = [MODES.SERIES_VIEW, MODES.NEWPHOTO, MODES.NEWSERIES, MODES.PHOTO_REVIEW, MODES.PHOTO_VIEW]

/**
 * This component ties all the Repeat Photo components together
 */
const RepeatablePhotos = ({
  modeName, series, seriesConfig, inputMethod: inputMethodINIT,
  currentUserId, currentUserRole, accessControl, initialPhoto,
  onChange, onClose, onDelete, onNewSeries, onChangePhoto,
  onNewPhoto, onDeletePhoto, loading, onFindOnMap,
  onWatch, onUnWatch, watching, owned
}) => {
  // Manage view modes separately
  // This is a pretty normal pattern for context
  const [seriesState, setState] = useState({
    series,
    seriesConfig,
    ac: accessControl ? new AccesHelper(currentUserId, currentUserRole, accessControl) : null
  })

  const [mode, setMode] = useState({
    current: {
      name: modeName || MODES.SERIES_VIEW,
      pid: initialPhoto,
      inputMethod: inputMethodINIT
    },
    history: []
  })

  useEffect(() => {
    setState({ ...seriesState, series })
    setMode({
      current: {
        name: modeName || MODES.SERIES_VIEW,
        pid: initialPhoto,
        inputMethod: inputMethodINIT
      },
      history: []
    })
  }, [series])

  // When anything changes about a photo
  const onUpdatePhoto = (photo, photoDelta) => {
    const newSeries = { ...seriesState.series }
    newSeries.photos = newSeries.photos.map(ph => ph.id === photoDelta.id ? { ...ph, ...photoDelta } : ph)
    onChangePhoto(photo, { ...photoDelta, seriesId: newSeries.id })
  }

  const onApprovePhoto = async (newSeries, newPhoto, newPhotoData) => {
    // Pass this all back so that graphQl can handle it
    console.log('RepeatablePhotos: waiting for photo ID', { newSeries, newPhoto, newPhotoData })
    await onNewPhoto(newPhoto, newPhotoData)

    setState({
      ...seriesState,
      series: {
        ...series,
        photos: [...series.photos, newPhoto]
      }
    })
    changeMode(MODES.PHOTO_VIEW_NEW, newPhoto.id)
  }

  // const onCreateSeries = async (newSeries, newPhoto, newPhotoData) => {
  //   console.log('RepeatablePhotos: waiting for series ID', { newSeries, newPhoto, newPhotoData })

  //   await onNewSeries(newSeries, newPhoto, newPhotoData)
  //   console.log('RepeatablePhotos done. showing the series')
  //   changeMode(MODES.SERIES_VIEW)
  // }

  // When a photo is removed
  const onDeletePhotos = (photoIdArr) => {
    series.photos.filter(sp => photoIdArr.indexOf(sp.id) > -1).forEach(p => onDeletePhoto(p))
  }

  // Here's our modeChanger. My guess it will get a lot more complicated
  // once the reducers get in and working
  const changeMode = (newModeName, newPid, newInputMethod) => {
    const canUpdate = seriesState.ac.canUser('update', series, 'series')
    if (seriesState === null) onClose()
    if (!canUpdate && VIEW_ONLY_MODES.indexOf(newModeName) < 0) {
      console.error('ILLEGAL MODE CHANGE!!!!!', { from: mode.current, to: newModeName, canUpdate })
    }
    else {
      // console.log('CHANGING MODE TO', newModeName)
      setMode({
        current: {
          name: newModeName,
          pid: newPid,
          inputMethod: newInputMethod
        },
        history: [...mode.history, mode.current]
      })
    }
  }
  const lastMode = () => {
    // console.log('REPEATABLEPHOTOS lastMode', mode)
    if (mode.history.length > 0) {
      const oldHistory = [...mode.history]
      const oldMode = oldHistory.pop()
      setMode({
        current: oldMode,
        history: oldHistory
      })
    }
    else {
      onClose()
    }
  }

  // The action to initiate the photo viewer
  const viewPhoto = (pid) => {
    // console.log('DEBUG', mode, series.photos.find(p => p.id === pid))
    changeMode(MODES.PHOTO_VIEW, pid)
  }

  let component = null

  if (loading) return <SeriesLoading onMenuBack={onClose} />
  else if (!seriesState.series && mode.current.name !== MODES.NEWSERIES) return <SeriesNotFound onMenuBack={onClose} />

  switch (mode.current.name) {
    case MODES.SERIES_VIEW:
      component = (
        <SeriesView
          // This one has lots of functionsgit add
          onSlideshowStart={() => changeMode(MODES.SLIDESHOW)}
          onChange={() => {
            console.log('onChange NOT IMPLEMENTED')
          }}
          watching={watching}
          owned={owned}
          initialPhoto={initialPhoto}
          onDeletePhotos={onDeletePhotos}
          onDeleteSeries={onDelete}
          onFindOnMap={onFindOnMap}
          onCameraAdd={() => changeMode(MODES.NEWPHOTO, null, 'camera')}
          onEditSeries={() => changeMode(MODES.SERIES_EDIT)}
          onLibraryAdd={() => changeMode(MODES.NEWPHOTO, null, 'library')}
          onTileClick={viewPhoto}
          onMenuBack={onClose}
          onWatch={onWatch}
          onUnWatch={onUnWatch}
        />
      )
      break
    case MODES.SERIES_EDIT:
      component = (
        <SeriesEdit
          onCancel={() => changeMode(MODES.SERIES_VIEW)}
          onOk={(seriesDelta) => {
            onChange(series, { ...seriesDelta, id: series.id })
            changeMode(MODES.SERIES_VIEW)
          }}
        />
      )
      break
    case MODES.PHOTO_VIEW:
      component = (
        <PhotoViewTool
          photos={seriesState.series.photos}
          initPhotoId={mode.current.pid}
          onMenuBack={lastMode}
          onUpdatePhoto={onUpdatePhoto}
        />
      )
      break
    case MODES.PHOTO_VIEW_NEW:
      component = (
        <PhotoViewTool
          photos={seriesState.series.photos}
          initPhotoId={mode.current.pid}
          newPhoto
          onMenuBack={() => changeMode(MODES.SERIES_VIEW)}
          onUpdatePhoto={onUpdatePhoto}
        />
      )
      break
    case MODES.SLIDESHOW:
      component = (
        <Slideshow photos={seriesState.series.photos} onCancel={lastMode} />
      )
      break
    case MODES.NEWPHOTO:
      component = (
        <NewPhoto
          inputMethod={mode.current.inputMethod}
          photos={series ? series.photos : null}
          onCreate={onApprovePhoto}
          onCancel={lastMode}
        />
      )
      break
    case MODES.NEWSERIES:
      component = (
        <NewPhoto
          inputMethod={mode.current.inputMethod}
          isNewSeries
          onCreate={onNewSeries}
          onCancel={lastMode}
        />
      )
      break
    default:
      console.log('REPEATABLEPHOTO: default state. you should NEVER see this', seriesState)
      component = null
  }

  return (
    <SeriesContext.Provider value={seriesState}>
      {component}
    </SeriesContext.Provider>
  )
}

RepeatablePhotos.propTypes = {
  modeName: PropTypes.oneOf(VALID_STARTMODES),
  seriesConfig: PropTypes.object.isRequired,
  watching: PropTypes.bool,
  owned: PropTypes.bool,

  currentUserId: PropTypes.string,
  currentUserRole: PropTypes.string,
  accessControl: PropTypes.object,

  inputMethod: PropTypes.oneOf(['camera', 'library']),
  series: PropTypes.object,
  initialPhoto: PropTypes.string,
  loading: PropTypes.bool,

  // Now some functions to handle actions we might take
  onChange: PropTypes.func.isRequired,
  onFindOnMap: PropTypes.func.isRequired,
  onChangePhoto: PropTypes.func.isRequired,
  onDeletePhoto: PropTypes.func.isRequired,
  onNewPhoto: PropTypes.func.isRequired,
  onNewSeries: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
  onWatch: PropTypes.func,
  onUnWatch: PropTypes.func

}
RepeatablePhotos.defaultProps = {
  mode: MODES.SERIES_VIEW
}

export default RepeatablePhotos
