import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import Draggable from 'react-draggable'

import Chip from '@material-ui/core/Chip'
import Avatar from '@material-ui/core/Avatar'

import CancelIcon from '@material-ui/icons/Cancel'
import MyLocation from '@material-ui/icons/MyLocation'

import { makeStyles, useTheme } from '@material-ui/core/styles'
import style from './XYTagChip.style'

const useStyles = makeStyles(style)

const XYTagChip = ({
  color, edit, tag, newSize,
  onTagDragOver, onTagDragStop, onNewTag, onTagDelete, ...props
}) => {
  const isReversed = tag.relCoords.x > newSize.newWidth / 2
  const classes = useStyles()
  const theme = useTheme()
  const stopBadness = (e) => {
    e.preventDefault()
    e.stopPropagation()
  }

  const handleDrag = (e, data) => {
    stopBadness(e)
    onTagDragOver(data, tag)
  }
  const handleStopDrag = (e, data) => {
    stopBadness(e)
    onTagDragStop(data, tag)
  }

  // Draggable chip if it is editable
  return (
    <Draggable
      disabled={!edit}
      bounds={{ left: 10, top: 10, right: newSize.newWidth - 10, bottom: newSize.newHeight - 10 }}
      position={{ x: tag.relCoords.x, y: tag.relCoords.y }}
      onDrag={handleDrag}
      onStop={handleStopDrag}
      onClick={stopBadness}
    >
      <div className={classNames(classes.chipBucket, isReversed && classes.reversedChipBucket)}>
        <Chip
          style={{
            background: color ? `${color}!important` : null,
            color: color ? `${theme.palette.getContrastText(color)}!important` : null
          }}
          color={edit ? 'secondary' : 'default'}
          label={tag.label}
          data={tag.id}
          onClick={stopBadness}
          onDelete={edit ? onTagDelete(tag) : null}
          deleteIcon={edit ? <CancelIcon className={classNames(classes.deleteIcon, isReversed && classes.reversedDelete)} /> : <div />}
          className={classNames(
            classes.xyTagChip,
            isReversed && classes.reversedChip,
            edit && classes.editing
          )}
          {...props}
          avatar={<Avatar className={classNames(classes.chipAvatar, edit && classes.editing)}>
            <MyLocation className={classNames(classes.chipAvatarIcon, edit && classes.editing)} />
          </Avatar>}
        />
      </div>
    </Draggable>
  )
}

XYTagChip.propTypes = {
  color: PropTypes.string,
  edit: PropTypes.bool,
  tag: PropTypes.object.isRequired,
  newSize: PropTypes.object,
  onTagDragOver: PropTypes.func,
  onTagDragStop: PropTypes.func,
  onNewTag: PropTypes.func,
  onTagDelete: PropTypes.func
}

export default XYTagChip
