import React from 'react'
import PropTypes from 'prop-types'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import Toolbar from '@material-ui/core/Toolbar'

import { makeStyles } from '@material-ui/core/styles'
import style from './auth.style'
import AuthContainer from './AuthContainer'

const useStyles = makeStyles(style)

const RequireNewPassword = ({ change, requiredAttributes, placeholders, handleInputChange }) => {
  const classes = useStyles()
  return (
    <AuthContainer>
      <Typography>A new password is required</Typography>
      <TextField
        id='newassword'
        fullWidth
        autoFocus
        label='New Password'
        className={classes.textField}
        type='password'
        autoComplete='new-password'
        margin='normal'
        onChange={handleInputChange}
      />
      {requiredAttributes
        .map((attribute, idx) => (
          <TextField
            fullWidth
            key={attribute}
            name={attribute}
            className={classes.textField}
            placeholder={placeholders[idx]}
            type='text'
            margin='normal'
            onChange={handleInputChange}
          />
        ))}

      <Toolbar disableGutters>

        <Button color='secondary' variant='contained'>
          Back
        </Button>
        <div className={classes.spacer} />
        <Button
          color='primary'
          variant='contained'
          onClick={change}
          className='enterAction'
        >
          Change
        </Button>
      </Toolbar>
    </AuthContainer>
  )
}

RequireNewPassword.propTypes = {
  change: PropTypes.func.isRequired,
  handleInputChange: PropTypes.func.isRequired,
  requiredAttributes: PropTypes.array,
  placeholders: PropTypes.array
}

export default RequireNewPassword
