import React from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Badge from '@material-ui/core/Badge'
import Avatar from '@material-ui/core/Avatar'
import Camera from '@material-ui/icons/Camera'
import { makeStyles } from '@material-ui/core/styles'
import { ACCESS } from '../../../config'

const style = theme => ({
  root: {
    color: theme.palette.primary.main,
    width: 150,
    position: 'absolute',
    top: '50%',
    left: '50%',
    textAlign: 'center',
    transformOrigin: '50% 50%',
    transform: 'translateY(-50%) translateX(-50%)'
  },
  rootActive: {
    zIndex: 60
  },
  icon: {
    // height: 16,
    // width: 16
  },
  avatar: {
    position: 'relative',
    margin: '0 auto',
    background: 'green',
    color: 'white',
    border: '2px solid white',
    height: 40,
    width: 40,
    zIndex: 61,
    '&.PUBLIC': { background: ACCESS.PUBLIC.color },
    '&.PRIVATE': { background: ACCESS.PRIVATE.color },
    '&.OPEN': { background: ACCESS.OPEN.color }
  },
  avatarActive: {
    background: '#FFFF00',
    color: 'black',
    border: `2px solid black`,
    '&.PUBLIC': { background: '#FFFF00' },
    '&.PRIVATE': { background: '#FFFF00' },
    '&.OPEN': { background: '#FFFF00' }
  },
  badge: {
    zIndex: 62,
    color: theme.palette.secondary.contrastText,
    background: theme.palette.secondary.main,
    transform: 'scale(1) translate(30%, -30%)'
  },
  badgeActive: {
    color: theme.palette.primary.contrastText,
    background: theme.palette.primary.main
  },
  directionIndicator: {
    position: 'absolute',
    top: 20,
    left: '50%',
    // transformOrigin: '50% 50%',
    // transform: 'translateY(-50%) translateX(-50%)',
    zIndex: 59
  },
  name: {
    color: 'white',
    fontWeight: 'bold',
    textShadow: `
        0px 0px 4px #000000, 
        0px 0px 4px #000000 
    `,
    zIndex: 60,
    position: 'relative'
    // textStroke: '1px white'
  },
  nameActive: {
    color: 'black',
    textShadow: `
    0px 0px 2px #FFFFFF, 
    0px 0px 2px #FFFFFF,
    0px 0px 4px #FFFFFF 
`
  }
})
const useStyles = makeStyles(style)

const Single = ({ name, access, onClick, active, photoCount, direction }) => {
  const classes = useStyles()
  return (
    <div className={classNames(classes.root, active ? classes.rootActive : null, access)}>
      <Badge classes={{ badge: classNames(classes.badge, active ? classes.badgeActive : null, access) }} badgeContent={photoCount}>
        <Avatar className={classNames(classes.avatar, active ? classes.avatarActive : null, access)} onClick={onClick}>
          <Camera className={classes.icon} color='inherit' />
        </Avatar>
      </Badge>
      {direction && direction >= 0 ? (
        <div className={classes.directionIndicator}>
          <svg className={classes.svgArc}
            version='1.1'
            width='100'
            height='100'
            viewBox='0, 0, 100, 100'
            style={{ transform: `translateY(-50%) translateX(-50%) rotateZ(${direction}deg)` }}>
            <path d='M50,0 C60.946,0.151 72.136,3.514 80.675,10.56 L50,50 L19.325,10.56 C27.938,3.461 39,0.278 50,0 z'
              fill='#009DC1'
              fillOpacity='0.444'
              strokeWidth='1'
              strokeOpacity='0.6'
              stroke='#FFFFFF'
            />
          </svg>
        </div>
      ) : null}
      <Typography variant='body1' className={classNames(classes.name, active ? classes.nameActive : null)}>
        {name}
      </Typography>
    </div>
  )
}

Single.propTypes = {
  access: PropTypes.string,
  name: PropTypes.string,
  photoCount: PropTypes.number,
  onClick: PropTypes.func.isRequired,
  direction: PropTypes.number,
  active: PropTypes.bool
}

export default Single
