const TYPES = {
  // Series meta gets appended automatically to the seriees
  SERIES: 'SERIES',
  // Same but now only gets appended to the photo
  PHOTO: 'PHOTO'
}
class SeriesConfig {
  constructor (seriesConfig) {
    if (!seriesConfig) throw new Error('No seriesConfig object provided to SeriesConfig class')
    this.config = seriesConfig
  }

  getTagType = (typeId) => this.config.tags.find(tt => tt.id === typeId)
  // getTagTypeIndex = (typeId) => this.config.tags.findIndex(tt => tt.id === typeId)
  getTagTypeIndex = (typeId) => {
    // console.log('HERE', typeId)
    return this.config.tags.findIndex(tt => tt.id === typeId)
  }

  getXYTagTypes = () => this.config.tags.filter(tt => tt.allowXY)
  getNonXYTagTypes = () => this.config.tags.filter(tt => tt.allowNonXY)

  getSeriesMeta = () => Object.keys(this.config.meta)
    .filter(key => this.config.meta[key].type === TYPES.SERIES)
    .reduce((acc, key) => ({
      ...acc,
      [key]: this.config.meta[key].default
    }), {})

  getPhotoMeta = () => Object.keys(this.config.meta)
    .filter(key => this.config.meta[key].type === TYPES.PHOTO)
    .reduce((acc, key) => ({
      ...acc,
      [key]: this.config.meta[key].default
    }), {})
}

export default SeriesConfig
