import React, { useContext, useState } from 'react'
import PropTypes from 'prop-types'

import Typography from '@material-ui/core/Typography'
import Divider from '@material-ui/core/Divider'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'

import { makeStyles } from '@material-ui/core/styles'

import TagView from '../controls/TagView'
import ShortLongText from '../controls/ShortLongText'
import DateControl from '../controls/DateControl'
import UserListItem from '../controls/UserListItem'
import TagClass from '../../../../lib/Tags'
import BigTextEdit from '../dialogs/BigTextEdit'
import BigText from '../dialogs/BigText'
import TagEdit from '../dialogs/TagEdit'

import { SeriesContext } from '../../contexts'
import DateView from '../controls/DateView'

const style = theme => ({
  root: {
    height: '90%',
    width: '85%'
  },
  dlgTitleContainer: {
    display: 'flex',
    padding: 0
  },
  catTagTitle: {
    marginTop: theme.spacing(2)
  },
  dlgTitle: {
    flexGrow: 1,
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(1),
    paddingLeft: theme.spacing(3)
  },
  field: {
    marginBottom: theme.spacing(2)
  }
})
const useStyles = makeStyles(style)

const DIALOGS = {
  NOTEEDIT: 'noteEdit',
  NOTEVIEW: 'noteView',
  PHOTOTAKEN: 'taken',
  TAGEDIT: 'tag',
  CANCEL: 'cancel'
}

const PhotoInfo = ({ canEdit, photo, delta, onChange }) => {
  const classes = useStyles()
  const { seriesConfig } = useContext(SeriesContext)
  const [{ activeDialog, activeDialogContext }, setActiveDialog] = useState({})

  const tagInst = new TagClass(seriesConfig, delta.tags || photo.tags)

  let metaKeys = []
  try {
    metaKeys = photo.meta ? Object.keys(JSON.parse(photo.meta)) : []
  }
  catch (e) {
    console.error(e)
  }
  return (
    <div>
      {photo.createdUser && (
        <React.Fragment>
          <UserListItem
            user={photo.createdUser}
            onAvatarClick={() => seriesConfig.onAvatarClick(photo.createdUser.username)}
            secondary='uploaded this photo'
          />
          <Divider />
        </React.Fragment>
      )}
      {canEdit ? (
        <DateControl
          className={classes.field}
          label='Photo taken on'
          value={delta.takenOn || photo.takenOn}
          onChange={takenOn => onChange({ takenOn })}
          canEdit={canEdit}
        />
      )
        : (
          <DateView
            label='Photo taken on'
            value={delta.takenOn || photo.takenOn}
          />
        )}

      {(canEdit || photo.note) && (
        <ShortLongText
          label='Photo Note'
          text={delta.note || photo.note}
          onClick={() => {
            if (canEdit) setActiveDialog({ activeDialog: DIALOGS.NOTEEDIT })
            else setActiveDialog({ activeDialog: DIALOGS.NOTEVIEW })
          }}
        />
      )}
      <Typography variant='subtitle1' className={classes.catTagTitle}>Category tags</Typography>
      <Divider />
      {tagInst.seriesConfig.getNonXYTagTypes().map((tt, idx) => (
        <div
          key={`tagtype-${idx}`}
          onClick={() => canEdit ? setActiveDialog({ activeDialog: DIALOGS.TAGEDIT, activeDialogContext: tt }) : null}
        >
          <TagView
            className={classes.field}
            label={tt.label}
            tags={delta.tags || photo.tags}
            tagConfig={tt}
          />
        </div>
      ))}

      {metaKeys.length > 0 && Object.keys(seriesConfig.meta).length > 0 && (
        <React.Fragment>
          <Typography className={classes.heading}>Metadata</Typography>
          <Divider />
          <List className={classes.list}>
            {metaKeys.map((key, idx) => seriesConfig.meta[key] ? (
              <ListItem key={`metadata${idx}`}>
                <ListItemText
                  primary={seriesConfig.meta[key].label}
                  secondary={seriesConfig.meta[key].render(photo.meta[key])}
                />
              </ListItem>
            ) : null
            )}
          </List>
        </React.Fragment>
      )}
      {activeDialog && activeDialog === DIALOGS.NOTEEDIT && (
        <BigTextEdit
          open
          multiline
          onOk={note => {
            onChange({ note })
            setActiveDialog({})
          }}
          title='Edit Photo Note'
          body={delta.note || photo.note}
        />
      )}
      {activeDialog && activeDialog === DIALOGS.NOTEVIEW && (
        <BigText
          open
          multiline
          onClose={() => setActiveDialog({})}
          title='Photo Note'
          body={delta.note || photo.note}
        />
      )}
      {activeDialog && activeDialog === DIALOGS.TAGEDIT && (
        <TagEdit
          open
          title={`Edit Tags: ${activeDialogContext.label}`}
          onOk={tags => {
            setActiveDialog({})
            onChange({ tags })
          }}
          onCancel={() => setActiveDialog({})}
          tags={delta.tags || photo.tags}
          tagConfig={activeDialogContext}
        />
      )}
    </div>
  )
}

PhotoInfo.propTypes = {
  canEdit: PropTypes.bool,
  onChange: PropTypes.func.isRequired,
  photo: PropTypes.object.isRequired,
  delta: PropTypes.object.isRequired
}

export default PhotoInfo
