import React from 'react'
import PropTypes from 'prop-types'
import Chip from '@material-ui/core/Chip'
import CancelIcon from '@material-ui/icons/Cancel'
import { makeStyles, useTheme } from '@material-ui/core/styles'

export const tagChips = {
  height: 20
}

const style = theme => ({
  root: {
    fontSize: '0.6em',
    height: tagChips.height,
    marginRight: theme.spacing(1) / 2,
    marginTop: theme.spacing(1) / 2
  },
  deleteIcon: {
    width: tagChips.height - 1,
    height: tagChips.height - 1
  },
  chipAvatar: {
    width: tagChips.height,
    height: tagChips.height
  }
})
const useStyles = makeStyles(style)

const TagChip = ({ edit, tag, color, onDelete, ...props }) => {
  const classes = useStyles()
  const theme = useTheme()
  return (
    <Chip
      style={{
        background: color || null,
        color: color ? `${theme.palette.getContrastText(color)}` : null
      }}
      label={tag.label}
      data={tag.id}
      onDelete={edit ? () => onDelete(tag) : null}
      deleteIcon={(
        <CancelIcon
          className={classes.deleteIcon}
          onClick={e => {
            console.log('this click')
          }}
          style={{
            opacity: 0.5,
            color: color ? `${theme.palette.getContrastText(color)}` : null
          }}
        />
      )}
      className={classes.root}
      {...props}
    />
  )
}

TagChip.propTypes = {
  color: PropTypes.string.isRequired,
  edit: PropTypes.bool,
  tag: PropTypes.object.isRequired,
  onDelete: PropTypes.func
}

export default TagChip
