import React, { useState, useContext } from 'react'
// import PropTypes from 'prop-types'
import classNames from 'classnames'
import Divider from '@material-ui/core/Divider'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import Avatar from '@material-ui/core/Avatar'
import Typography from '@material-ui/core/Typography'
import FormLabel from '@material-ui/core/FormLabel'

import { makeStyles } from '@material-ui/core/styles'

import TagChip from '../tags/TagChip'
import style from './SeriesInfo.style'

import ShortLongText from '../controls/ShortLongText'
import UserListItem from '../controls/UserListItem'
import BigText from '../dialogs/BigText'
import { formatDate } from '../../../../lib/datelib'
import { ACCESS } from '../../../../config'
import SeriesConfigClass from '../../../../lib/SeriesConfig'

import { SeriesContext } from '../../contexts'

const DIALOGS = {
  NOTEVIEW: 'NOTEVIEW'
}
const useStyles = makeStyles(style)

const SeriesInfo = () => {
  const classes = useStyles()
  const { series, seriesConfig } = useContext(SeriesContext)
  const seriesConfigInst = new SeriesConfigClass(seriesConfig)
  const [activeDialog, setActiveDialog] = useState(null)

  const newTags = series.photos.reduce((acc, { tags }) => {
    tags.forEach(tag => {
      if (!acc[tag.typeId]) {
        acc[tag.typeId] = {
          [tag.label]: { ...tag, count: 1 }
        }
      }
      else if (acc[tag.typeId][tag.label]) {
        const existing = acc[tag.typeId][tag.label]
        acc[tag.typeId][tag.label] = { ...existing, count: existing.count + 1 }
      }
      else acc[tag.typeId][tag.label] = { ...tag, count: 1 }
    })
    return acc
  }, {})

  let metaKeys = []
  try {
    metaKeys = series.meta ? Object.keys(JSON.parse(series.meta)) : []
  }
  catch (e) {
    console.error(e)
  }

  return (
    <div className={classes.root}>
      <div className={classes.field}>
        <FormLabel className={classes.formLabel}>
      Series Name
        </FormLabel>
        <Typography variant='body1' color='inherit'>
          {series.name}
        </Typography>
      </div>
      {series.note && (
        <ShortLongText
          label='Series Note'
          text={series.note}
          onClick={() => setActiveDialog(DIALOGS.NOTEVIEW)}
        />
      )}

      <Typography className={classes.heading} variant='subtitle1'>Series Summary</Typography>
      <Divider />
      <List className={classes.list} dense>
        <ListItem className={classNames(classes.access, classes[series.access])}>
          <ListItemText
            color='inherit'
            primary={`Series is ${ACCESS[series.access].label}`}
            secondary={ACCESS[series.access].description}
          />
        </ListItem>
        {series.createdUser && (
          <UserListItem
            user={series.createdUser}
            onAvatarClick={() => seriesConfig.onAvatarClick(series.createdUser.username)}
            secondary='created this series'
          />
        )}
        <ListItem>
          <ListItemAvatar>
            <Avatar className={classes.numPhotos}>
              {series.photos.length}
            </Avatar>
          </ListItemAvatar>
          <ListItemText
            primary={series.photos.length > 1 ? 'Photos' : 'Photo'}
            secondary={series.photos.length > 0 && (
              <span>
                {formatDate(series.photos[0].takenOn, 'LL')}
                <span>&nbsp;&nbsp;</span>to<span>&nbsp;&nbsp;</span>
                {formatDate(series.photos[series.photos.length - 1].takenOn, 'LL')}
              </span>
            )}
          />
        </ListItem>
        <ListItem>
          <ListItemAvatar>
            <Avatar className={classes.watchers}>
              {series.watchers}
            </Avatar>
          </ListItemAvatar>
          <ListItemText primary='Users Watching' />
        </ListItem>
        <ListItem>
          <ListItemText>
            <FormLabel className={classes.formLabel}>
              Tags in use (across all photos)
            </FormLabel>
            <Divider />
            {Object.keys(newTags).length === 0 && (
              <Typography variant='caption'>No photos in this series have tags</Typography>
            )}
            {Object.keys(newTags).map((tagKey, idtk) => (
              <div key={`tagchipkey${idtk}`} className={classes.tagListItem}>
                <Typography className={classes.subheading} variant='overline'>{seriesConfigInst.getTagType(tagKey).label}</Typography>
                <Divider />
                {Object.values(newTags[tagKey]).length > 0
                  ? (
                    Object.values(newTags[tagKey]).map((tag, idx) => (
                      <TagChip
                        key={`tagchip-${idtk}-${idx}`}
                        tag={tag}
                        color={seriesConfigInst.getTagType(tagKey).color}
                      />
                    ))
                  )
                  : (
                    <Typography variant='caption'>No photos in this series have tags</Typography>
                  )
                }
              </div>
            ))}
          </ListItemText>
        </ListItem>

      </List>

      {metaKeys.length > 0 && Object.keys(seriesConfig.meta).length > 0 && (
        <React.Fragment>
          <FormLabel className={classes.formLabel}>
            Metadata
          </FormLabel>
          <Divider />
          <List className={classes.list}>
            {metaKeys.map((key, idx) => seriesConfig.meta[key] ? (
              <ListItem key={`metadata${idx}`}>
                <ListItemText
                  primary={seriesConfig.meta[key].label}
                  secondary={seriesConfig.meta[key].render(series.meta[key])}
                />
              </ListItem>
            ) : null
            )}
          </List>
        </React.Fragment>
      )}
      {activeDialog === DIALOGS.NOTEVIEW && (
        <BigText
          open
          multiline
          onClose={() => setActiveDialog(null)}
          title='Series Note'
          body={series.note}
        />
      )}
    </div>
  )
}

SeriesInfo.propTypes = {
}

export default SeriesInfo
