import { Actions } from '../actions'
import { home } from '../../config'

const homeInit = {
  history: [],
  current: {
    domain: home.AppStateTypes.FEED
    // search: null // { tab, text }
    // defaultSearchTab:
    // defaultSearchText:
    // activeSeries: 'fda17bae-0335-4f0d-9065-dc8d7aa23ed1',
    // initialPhoto: 'someguid'
  }
}

export default (state = homeInit, action) => {
  const { type, data } = action
  const { history, current } = state

  switch (type) {
    case Actions.HOME_STATE_BACK:
      const newStateHistory = [...history]
      const newCurrentState = newStateHistory.length > 0 ? newStateHistory.pop() : homeInit.current
      return {
        ...state,
        current: newCurrentState,
        history: newStateHistory
      }

    case Actions.HOME_CHANGE_ACTIVE_SERIES:
      const { activeSeries, initialPhoto } = current
      return {
        ...state,
        history,
        current: {
          ...current,
          activeSeries: activeSeries || null,
          initialPhoto: activeSeries && initialPhoto ? initialPhoto : null
        }
      }

    case Actions.HOME_CHANGE_STATE:
      const { newDomain, params } = data
      const newParams = params || {}

      // We don't save certain states
      const saveState = [
        home.AppStateTypes.QRCAPTURE,
        home.AppStateTypes.NEW_SERIES_CAMERA,
        home.AppStateTypes.NEW_SERIES_LIBRARY
      ].indexOf(current.domain) !== 0

      const newHistory = saveState ? [...history, current] : history
      return {
        ...state,
        history: newHistory,
        current: {
          ...current,
          domain: newDomain,
          ...newParams
        }
      }

    default:
      return state
  }
}
