import React, { useRef, useEffect, useState, useContext } from 'react'
import Typography from '@material-ui/core/Typography'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
import moment from 'moment'
import { AppContext } from '../../contexts'

const CHECKINTERVAL = 5000

const useStyles = makeStyles(theme => ({
  root: {
    background: 'black',
    color: 'white',
    textAlign: 'center'
  }
}))

const OfflineBar = () => {
  const classes = useStyles()
  const { online, offlineSince } = useContext(AppContext)
  const [sinceStr, setSinceStr] = useState()
  const timerRef = useRef()

  const calcSinceStr = () => {
    const newText = moment(offlineSince, 'x').fromNow()
    // console.log('OFFLINE SINCE', since, newText)
    setSinceStr(newText)
  }
  const clearTimer = () => {
    if (timerRef.current) {
      clearInterval(timerRef.current)
      timerRef.current = null
    }
  }

  useEffect(() => {
    if (offlineSince && !timerRef.current) {
      calcSinceStr()
      timerRef.current = setInterval(calcSinceStr, CHECKINTERVAL)
    }
    else if (!offlineSince)clearTimer()
    return () => {
      clearTimer()
    }
  }, [offlineSince])

  if (online) return null
  return (
    <Typography className={classes.root} variant='caption' component='div'>
      Offline ({sinceStr})
    </Typography>
  )
}

export default OfflineBar
