import { useCallback, useState, useLayoutEffect, useRef } from 'react'
import { debounce } from './util'

const getSize = (el, minSize, defaultSize) => {
  if (!el) return defaultSize || minSize || { width: 0, height: 0 }

  if (minSize) {
    return {
      width: Math.max(minSize.width, el.offsetWidth),
      height: Math.max(minSize.height, el.offsetHeight)
    }
  }
  else {
    return {
      width: el.offsetWidth,
      height: el.offsetHeight
    }
  }
}

const useComponentSize = (ref, minSize, defaultSize) => {
  const resizeActiveRef = useRef()
  const [resizeActive, setResizeActive] = useState(true)

  resizeActiveRef.current = resizeActive
  const [size, setComponentSize] = useState(
    getSize(ref ? ref.current : null, minSize, defaultSize)
  )

  const handleResize = () => {
    if (!resizeActiveRef.current || !ref.current) return
    const newSize = getSize(ref.current, minSize, defaultSize)
    if (newSize.height !== size.height || newSize.width !== size.width) {
      // console.log('handleResize Called', { newSize, size })
      setComponentSize(newSize)
    }
  }

  const handleResizeDebounced = useCallback(
    debounce(handleResize, 500),
    []
  )

  useLayoutEffect(() => {
    if (!ref.current) return
    handleResize()

    if (typeof ResizeObserver === 'function') {
      let resizeObserver = new ResizeObserver(handleResizeDebounced)
      resizeObserver.observe(ref.current)

      return () => {
        resizeObserver.disconnect(ref.current)
        resizeObserver = null
      }
    }
    else {
      window.addEventListener('resize', handleResizeDebounced)

      return () => {
        window.removeEventListener('resize', handleResizeDebounced)
      }
    }
  },
  [ref.current])

  return { size, setResizeActive }
}

export default useComponentSize
