import React, { useState, useRef, useMemo } from 'react'
import { StaticMap } from 'react-map-gl'
import PropTypes from 'prop-types'
import { getDistanceScales } from 'viewport-mercator-project'
import useComponentSize from '../../lib/useComponentSize'
import StyleButtons from './controls/StyleButtons'
import { mapConfig } from '../../config'
import classNames from 'classnames'
import 'mapbox-gl/dist/mapbox-gl.css'
import './BaseMap.css'

/**
 * Note that this component technically has state but it is designed to be functionally stateless
 */
const BaseStaticMap = (props) => {
  const {
    controlPadding,
    viewport,
    children,
    mapStyles,
    onMapDataEvent,
    ...passthroughProps
  } = props

  const mapRef = useRef(null)
  const { size } = useComponentSize(mapRef, { width: 100, height: 50 })

  const [currStyle, setCurrStyle] = useState(0)
  const [projection, setProjection] = useState(null)

  const recalcProjection = () => {
    if (!size || size.width < 10 || size.height < 10) return
    try {
      const projection = getDistanceScales(viewport)
      // console.log('recalcProjection', { projection, size })
      setProjection(projection)
    }
    catch (e) {
      // Sometimes we don't have a valid bound. Don't make a big deal out of it.
      console.error(e)
    }
  }

  if (!projection) recalcProjection()
  useMemo(recalcProjection, [size])

  return (
    <div className={classNames('BaseMap', props.className)} ref={mapRef} style={{ height: '100%', width: '100%' }}>
      <StaticMap
        {...viewport}
        height={size.height}
        width={size.width}
        mapStyle={mapStyles[currStyle].style}
        onData={onMapDataEvent}
        mapboxApiAccessToken={mapConfig.mapBoxToken}
        {...passthroughProps}
      >
        { mapStyles && mapStyles.length > 1 ? (
          <StyleButtons
            controlPadding={controlPadding}
            currStyle={currStyle}
            mapStyles={mapStyles}
            setCurrStyle={setCurrStyle}
          />
        ) : null }
        {
          children && Array.isArray(children)
            ? children.map(c => handleChild(c, viewport, size.height, size.width, projection))
            : handleChild(children, viewport, size.height, size.width, projection)
        }
      </StaticMap>
    </div>
  )
}

function handleChild (child, viewport, height, width, projection) {
  return typeof child === 'function'
    ? child(viewport, height, width, projection)
    : child
}

BaseStaticMap.propTypes = {
  // Anything else we want to add to the map
  children: PropTypes.any,
  deckelements: PropTypes.array,
  data: PropTypes.array,
  // Maybe we want map controls to be padded
  controlPadding: PropTypes.object,

  minPitch: PropTypes.number,
  maxPitch: PropTypes.number,
  pitch: PropTypes.number,

  // Lock the bounds of a map to a rectangle [minlng, minlat], [maxlng, maxlat]
  bounds: PropTypes.array,
  // Lock the possible zoom levels to a range: [minZoom, maxZoom]
  zoomBounds: PropTypes.array,
  // InitialBounds of the map.
  InnerBounds: PropTypes.array,

  // List of valid styles that will get picker buttons
  mapStyles: PropTypes.array,

  // Just some default props we probably won't set.
  latitude: PropTypes.number,
  longitude: PropTypes.number,
  zoom: PropTypes.number,

  onMapDataEvent: PropTypes.func
}
BaseStaticMap.defaultProps = {
  controlPadding: {},
  stylechooser: true,
  // viewport: {
  //   zoom: 12,
  //   latitude: 49.3050394,
  //   longitude: -123.1398254
  // },
  initStyle: 0,
  mapStyles: mapConfig.styles
}

export default BaseStaticMap
