import { Actions } from '../actions'
import { home } from '../../config'
const mapInit = [
  {
    mode: home.MapModeTypes.FREE,
    // selectedItem: null
    viewport: {
      // 'latitude': 49.2707848, // vancouver
      // 'longitude': -123.0714866,
      latitude: 36.80191008366581, // Grand Canyon
      longitude: -111.63999999999784,
      zoom: 11
    }
  }
]

export default (state = mapInit, action) => {
  const { type, data } = action

  switch (type) {
    case Actions.MAP_HISTORY_CLEAR:
      const newClearerHistory = state.length > 0 ? [[state.length - 1]] : []
      return newClearerHistory

    case Actions.MAP_HISTORY_POP:
      return state.slice(0, -1)

    case Actions.MAP_HISTORY_PUSH:
      return [...state, data]
    default:
      return state
  }
}
