import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'

import CssBaseline from '@material-ui/core/CssBaseline'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'

import ArrowBackIcon from '@material-ui/icons/ArrowBack'

import AddCameraIcon from '@material-ui/icons/AddAPhoto'
import MoreIcon from '@material-ui/icons/MoreVert'

import AddAlertIcon from '@material-ui/icons/AddAlert'
import SlideshowIcon from '@material-ui/icons/Slideshow'
import EditIcon from '@material-ui/icons/Edit'
// import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary'
// import CameraIcon from '@material-ui/icons/Camera'
import DeleteIcon from '@material-ui/icons/Delete'
import PlaceIcon from '@material-ui/icons/Place'
import QRDisplay from './QRDisplay'
import QRCodeIcon from '../../../icons/QRCode'

import { makeStyles } from '@material-ui/core/styles'

// import MapDot from '../../maps/MapDot'
import AddFab from './controls/AddFab'

import ContextMenu from './controls/ContextMenu'

import SeriesInfo from './subViews/SeriesInfo'
import SwipeShow from './subViews/SwipeShow'
import PhotoGrid from './subViews/PhotoGrid'

import DeleteDialog from './dialogs/DeleteDialog'

import { SeriesContext } from '../contexts'

import style from './SeriesView.style'
const useStyles = makeStyles(style)

const SeriesView = ({
  onSlideshowStart, onChange,
  onEditSeries, onDeletePhotos, onCameraAdd,
  onDeleteSeries, onLibraryAdd, onTileClick, onMenuBack,
  onWatch, onUnWatch, initialPhoto, onFindOnMap,
  watching, owned
}) => {
  const classes = useStyles()
  const [contextEl, setContextEl] = useState()
  const [showQR, setShowQR] = useState(false)
  const [deleteConfirm, setDeleteConfirm] = useState(false)

  const { series, seriesConfig, ac } = useContext(SeriesContext)

  const onDeleteConfirm = (confirmation) => {
    setDeleteConfirm(false)
    if (confirmation === true) onDeleteSeries(series.id)
  }

  const viewport = { zoom: 16 }
  if (series && series.coords && series.coords.length === 2) {
    viewport.longitude = series.coords[0]
    viewport.latitude = series.coords[1]
  }

  const can = {
    update: ac.canUser('update', series, 'series', '*'),
    delete: ac.canUser('delete', series, 'series', '*'),
    contribute: ac.canUser('update', series, 'series', ['photos'])
  }

  const [listTypeTab, setListTypeTab] = useState(0)
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position='static' className={classes.appBar}>
        <Toolbar disableGutters>
          <IconButton className={classes.menuButton} color='inherit' onClick={onMenuBack} aria-label='Menu'>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant='subtitle1' color='inherit' className={classes.toolbarTitle}>
            {series.name || 'Series'}
          </Typography>

          {/* <IconButton className={classes.menuButton} color='inherit' onClick={onSlideshowStart} aria-label='Menu'>
            <SlideshowIcon />
          </IconButton> */}
          <IconButton
            aria-haspopup='true'
            color='inherit'
            onClick={event => setContextEl(event.currentTarget)}
          >
            <MoreIcon />
          </IconButton>

          <ContextMenu anchorEl={contextEl} onClose={() => setContextEl(null)} menuItems={[
            {
              text: 'Find on map',
              icon: PlaceIcon,
              hide: !onFindOnMap,
              action: onFindOnMap
            },
            {
              text: 'Watch Series',
              icon: AddAlertIcon,
              hide: !onWatch || owned || watching,
              action: onWatch
            },
            {
              text: 'Unwatch Series',
              icon: AddAlertIcon,
              hide: !onUnWatch || owned || !watching,
              action: onUnWatch
            },
            {
              text: 'Series QR Code',
              icon: QRCodeIcon,
              action: () => setShowQR(true)
            },
            {
              text: 'Start Slideshow',
              icon: SlideshowIcon,
              hide: !onSlideshowStart,
              action: onSlideshowStart
            },
            { divider: true },
            {
              text: 'Edit Series',
              icon: EditIcon,
              hide: !onEditSeries || !can.update,
              action: onEditSeries
            },
            {
              text: 'Delete Series',
              icon: DeleteIcon,
              hide: !setDeleteConfirm || !can.delete,
              color: 'error',
              action: () => setDeleteConfirm(true)
            }
          ]} />
        </Toolbar>
        <Tabs variant='fullWidth'
          value={listTypeTab}
          onChange={(e, value) => setListTypeTab(value)}
        >
          <Tab label='Series' />
          {/* <Tab label='Map' /> */}
          <Tab label='Photos' />
        </Tabs>
      </AppBar>

      <div className={classes.inner}>
        {listTypeTab === 0 && (
          <React.Fragment>
            {series.photos && series.photos.length > 0 && (
              <SwipeShow
                photos={series.photos}
                initialPhoto={initialPhoto}
                onImageSelect={onTileClick}
              />
            )}
            <SeriesInfo onChange={onChange} />
          </React.Fragment>
        )}
        {series.photos && listTypeTab === 1 && (
          <PhotoGrid
            photos={series.photos}
            onTileClick={onTileClick}
            onDelete={onDeletePhotos}
          />
        )}
      </div>

      {/* {listTypeTab === 1 && (
        <MapDot
          showMe
          longitude={series.longitude}
          latitude={series.latitude}
          viewport={viewport}
          direction={series.direction}
        />
      )} */}

      {listTypeTab === 0 && can.contribute && (
        <AddFab
          isIos={seriesConfig.isIos}
          className={classes.AddFab}
          onCameraClick={onCameraAdd}
          onLibraryClick={onLibraryAdd}
        >
          <AddCameraIcon />
        </AddFab>
      )}
      <QRDisplay open={showQR} onClose={() => setShowQR(false)} />
      <DeleteDialog
        open={deleteConfirm}
        onConfirm={onDeleteConfirm}
        title='Delete Series'
        message='Are you sure you want to delete this series? The record and all images associated with it will be gone forever.'
      />
    </div>
  )
}

SeriesView.propTypes = {
  initialPhoto: PropTypes.string,
  watching: PropTypes.bool,
  owned: PropTypes.bool,
  // Action functions
  onSlideshowStart: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  onFindOnMap: PropTypes.func.isRequired,
  onDeletePhotos: PropTypes.func.isRequired,
  onDeleteSeries: PropTypes.func.isRequired,
  onCameraAdd: PropTypes.func.isRequired,
  onEditSeries: PropTypes.func.isRequired,
  onLibraryAdd: PropTypes.func.isRequired,
  onTileClick: PropTypes.func.isRequired,
  onMenuBack: PropTypes.func.isRequired,
  onWatch: PropTypes.func,
  onUnWatch: PropTypes.func
}

export default SeriesView
