import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import CssBaseline from '@material-ui/core/CssBaseline'
import Button from '@material-ui/core/Button'
import MobileStepper from '@material-ui/core/MobileStepper'
import Typography from '@material-ui/core/Typography'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import Badge from '@material-ui/core/Badge'
import Chip from '@material-ui/core/Chip'

import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import InfoIcon from '@material-ui/icons/Info'
import LabelIcon from '@material-ui/icons/Label'

import { makeStyles, useTheme } from '@material-ui/core/styles'

import PhotoInfo from './subViews/PhotoInfo'
import Photo from './subViews/Photo'

import { formatDate } from '../../../lib/datelib'
import Tags from '../../../lib/Tags'
import SwipeableViews from 'react-swipeable-views'
import { SeriesContext } from '../contexts'

import style from './PhotoViewTool.style'
const useStyles = makeStyles(style)

const STATES = {
  VIEWPIC: 0,
  SHOWINFO: 1,
  SHOWTAGS: 2,
  EDITTAGS: 3,
  FIRSTAGEDIT: 4
}

const PhotoViewTool = ({ photos, initPhotoId, onMenuBack, onUpdatePhoto, newPhoto }) => {
  const classes = useStyles()
  const theme = useTheme()
  const { series, seriesConfig, ac } = useContext(SeriesContext)
  const sortedPhotos = [...photos]
  sortedPhotos.sort((a, b) => parseInt(b.takenOn, 10) - parseInt(a.takenOn, 10))
  const [activePhoto, setActivePhoto] = useState(initPhotoId ? sortedPhotos.findIndex(p => p.id === initPhotoId) : 0)
  // This is a special copy of the photo so we can play around with the XY tags and undo
  // if we need to
  const photo = photos[activePhoto]
  const [delta, setDelta] = useState(newPhoto ? photo : {})
  const [mode, setMode] = useState({
    current: newPhoto ? STATES.FIRSTAGEDIT : STATES.VIEWPIC,
    last: null
  })

  const tagsClass = new Tags(seriesConfig, delta.tags || photo.tags)

  const changeMode = (desired) => {
    setMode({ current: desired, last: mode.current })
  }

  const onCommitTags = () => {
    changeMode(STATES.SHOWTAGS)
    onUpdatePhoto(photo, { id: photo.id, ...delta })
  }

  const onCommitTagsFirst = () => {
    onUpdatePhoto(photo, { id: photo.id, ...delta })
    onMenuBack()
  }

  const onCancelTagEdit = () => {
    changeMode(STATES.SHOWTAGS)
    setDelta(photo)
  }

  const photoClick = (e) => {
    if (mode.current === STATES.VIEWPIC) {
      e.preventDefault()
      e.stopPropagation()
      changeMode(STATES.SHOWINFO)
    }
  }

  const can = {
    update: ac.canUser('update', series, 'series') ||
            ac.canUser('update', photo, 'photo')
  }

  return (
    <div className={classes.root}>
      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      <AppBar position='static' className={classNames(classes.appBar, mode.current !== STATES.VIEWPIC ? classes.appBarEdit : null)}>
        <Toolbar disableGutters>
          {mode.current !== STATES.FIRSTAGEDIT && (
            <IconButton
              className={classes.menuButton}
              color='inherit'
              aria-label='Menu'
              onClick={() => onMenuBack()}
            >
              <ArrowBackIcon />
            </IconButton>
          )}
          <div className={classes.grow}>
            <Typography variant='subtitle2' color='inherit' >
              {newPhoto ? 'Add Tags' : `Photo View (${activePhoto + 1}/${photos.length})`}
            </Typography>
            <Typography variant='subtitle2' color='inherit'>
              {formatDate(photo.takenOn, 'lll')}
            </Typography>
          </div>

          { mode.current === STATES.EDITTAGS &&
          <Button color='inherit' onClick={onCancelTagEdit}>
            Cancel
          </Button>
          }

          { mode.current === STATES.EDITTAGS &&
          <Button color='inherit' onClick={onCommitTags}>
            Save
          </Button>
          }
          { mode.current === STATES.FIRSTAGEDIT &&
          <Button color='inherit' onClick={onCommitTagsFirst}>
            Next
          </Button>
          }

          { can.update && mode.current === STATES.SHOWTAGS &&
          <Button color='inherit' onClick={() => changeMode(STATES.EDITTAGS)}>
            Edit
          </Button>
          }

          { (mode.current !== STATES.EDITTAGS && mode.current !== STATES.FIRSTAGEDIT) &&
          <IconButton color='inherit'
            className={classNames(classes.tagButton, mode.current === STATES.SHOWTAGS && classes.activeButton)}
            onClick={() => changeMode(mode.current === STATES.SHOWTAGS ? STATES.VIEWPIC : STATES.SHOWTAGS)}
          >
            <Badge className={classes.margin} badgeContent={tagsClass.getXYTags().length} color='primary'>
              <LabelIcon color='inherit' />
            </Badge>
          </IconButton>
          }

          { (mode.current !== STATES.EDITTAGS && mode.current !== STATES.FIRSTAGEDIT) &&
          <IconButton color='inherit'
            className={classNames(classes.infoButton, mode.current === STATES.SHOWINFO && classes.activeButton)}
            onClick={() => changeMode(mode.current === STATES.SHOWINFO ? STATES.VIEWPIC : STATES.SHOWINFO)}
          >
            <InfoIcon />
          </IconButton>
          }

        </Toolbar>
      </AppBar>
      <div className={classes.inner} onClick={photoClick}>
        <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={activePhoto}
          containerStyle={{ height: '100%' }}
          style={{ height: '100%' }}
          disabled={mode.current !== STATES.VIEWPIC}
          onChangeIndex={(val) => setActivePhoto(val)}
          enableMouseEvents
        >
          {sortedPhotos.map((photoidx, idx) => (
            <div className={classes.photoWrapper} key={`swipeshowpic-${idx}`}>
              <Photo
                showTags={mode.current === STATES.SHOWTAGS}
                photo={{ ...photoidx, ...delta }}
                edit={can.update && (mode.current === STATES.EDITTAGS || mode.current === STATES.FIRSTAGEDIT)}
                onChange={setDelta}
                onCommitTags={onCommitTags}
              />
            </div>
          ))}
        </SwipeableViews>

        {mode.current === STATES.VIEWPIC && (
          <MobileStepper
            steps={sortedPhotos.length}
            position='bottom'
            activeStep={activePhoto}
            classes={{
              root: classes.mobileStepper,
              dots: classes.dots,
              dot: classes.dot,
              dotActive: classes.dotActive
            }}
          />
        )}
      </div>
      <PhotoInfo
        open={mode.current === STATES.SHOWINFO}
        photo={photo}
        canEdit={can.update}
        onOk={(newDelta) => {
          onUpdatePhoto(photo, { id: photo.id, ...newDelta })
          changeMode(mode.last)
        }}
        onCancel={() => changeMode(mode.last)}
      />
      { (mode.current === STATES.EDITTAGS || mode.current === STATES.FIRSTAGEDIT) && (
        <Chip
          label='Tap to tag features...'
          onClick={() => {}}
          className={classes.tipChip}
        />
      )}
    </div>
  )
}

PhotoViewTool.propTypes = {
  photos: PropTypes.array.isRequired,
  initPhotoId: PropTypes.string,
  newPhoto: PropTypes.bool,
  // Now some action functions
  onMenuBack: PropTypes.func.isRequired,
  onUpdatePhoto: PropTypes.func.isRequired
}

export default PhotoViewTool
