import localforage from 'localforage'
import createSeries from './mutations/createSeries'
import uuidv4 from 'uuid/v4'

localforage.config({
  storeName: 'MorPhoto', // Should be alphanumeric, with underscores.
  description: 'image blobs'
})

export const imgStore = localforage.createInstance({
  name: 'imgStore'
})
export const stateStore = localforage.createInstance({
  name: 'stateStore'
})

const OFFLINEMUTATIONS = 'offlineMutations'

// Pull serialized mutations from localstorage
function getPendingPromise () {
  return stateStore.getItem(OFFLINEMUTATIONS)
    .then(mutations => {
      console.log('offline::getPendingPromise', mutations)
      return JSON.parse(mutations) || []
    })
}

// Store serialized mutations in localstorage
function setPendingPromise (mutations) {
  console.log('offline::setPendingPromise', mutations)
  return localStorage.setItem(OFFLINEMUTATIONS, JSON.stringify(mutations))
}

/**
 * Push a mutation onto the stack
 * @param {*} params {variables, optimisticResponse etc}
 * @param {*} mutation
 */
export function pushMutationPromise (params, mutation) {
  const id = uuidv4()
  return getPendingPromise()
    .then(mutations => mutations.concat({ id, params, mutation }))
    .then(data => {
      console.log('offline::pushMutationPromise', data)
      return data
    })
    .then(setPendingPromise)
    .then(() => id)
}

export function popMutationPromise (id) {
  return getPendingPromise()
    .then(mutations => mutations.filter(pm => pm.id !== id))
    .then(data => {
      console.log('offline::popMutationPromise', data)
      return data
    })
    .then(setPendingPromise)
}

// Delegate incoming responses to the correct update function
function updateHandler (resp) {
  // IMPORTANT: You need one of these for every custom update function you use!
  // There's no post-update step at this level so the thunk params are empty
  console.log('offline::updateHandler', resp)
  if (resp.data.createSeries) return createSeries.updateThunk()
  else return () => {}
}

// Return a dummy update function scoped to a request with a specific id
function proxyUpdateForId (id) {
  return (proxy, resp) => {
    updateHandler(resp)(proxy, resp)
    // If this is the real deal and we're online then clean it up
    if (!resp.data.__optimistic) return popMutationPromise(id)
    else return Promise.resolve()
  }
}

// Restore pending requests
// This should be called after a page refresh
export function restoreRequests (client) {
  return getPendingPromise()
    .then(mutations => mutations.forEach(pendingMutation => {
      console.log('offline::restoreRequests', mutations, pendingMutation)
      const { id, params, mutation } = pendingMutation
      params.optimisticResponse.__optimistic = true
      params.update = proxyUpdateForId(id)
      params.mutation = mutation
      client.mutate(params)
    }))
}
