import React, { useState } from 'react'
import PropTypes from 'prop-types'
import CssBaseline from '@material-ui/core/CssBaseline'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import Typography from '@material-ui/core/Typography'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'

import SeriesEditInner from './subViews/SeriesEditInner'

// import { SeriesContext } from '../contexts'

import { makeStyles } from '@material-ui/core/styles'
import style from './NewPhotoInfo.style'
import PhotoInfoInner from './subViews/PhotoInfoInner'

const useStyles = makeStyles(style)

const NewPhotoInfo = ({ photo, newSeries, onCancel, onSave }) => {
  const classes = useStyles()
  // const { series } = useContext(SeriesContext)
  const [seriesDelta, setSeriesDelta] = useState(newSeries || {})
  const [photoDelta, setPhotoDelta] = useState(photo)

  return (
    <div className={classes.root}>
      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      <Dialog
        fullScreen
        fullWidth
        scroll='paper'
        disableBackdropClick
        disableEscapeKeyDown
        onClose={onCancel}
        aria-labelledby='customized-dialog-title'
        open
      >
        <DialogTitle disableTypography className={classes.dlgTitleContainer}>
          <Typography variant='h5' className={classes.dlgTitle}>
          New Photo Properties
          </Typography>
        </DialogTitle>
        <DialogContent>

          {!!newSeries && (
            <SeriesEditInner
              series={newSeries}
              delta={seriesDelta}
              onChange={setSeriesDelta}
            />
          )}

          <PhotoInfoInner
            photo={photo}
            canEdit
            delta={photoDelta}
            onChange={(newAttr) => setPhotoDelta({ ...photoDelta, ...newAttr })}
          />

        </DialogContent>
        <DialogActions>
          <Button onClick={onCancel} color='primary'>
            Cancel
          </Button>
          <Button onClick={() => onSave(seriesDelta, photoDelta)} color='primary'>
            Next
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}

NewPhotoInfo.propTypes = {
  photo: PropTypes.object.isRequired,
  newSeries: PropTypes.object,
  // Standard functions
  onCancel: PropTypes.func.isRequired,
  onSave: PropTypes.func
}

export default NewPhotoInfo
