/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { current: { search, activeSeries, domain } } = state.home
  return {
    defaultTab: search ? search.tab : null,
    defaultText: search ? search.text : null,
    activeSeries,
    domain
  }
}

const mapDispatchToProps = dispatch => ({
  onBack: () => {
    dispatch(actions.homeStateBack())
  },
  queueUpload: (uploadObj) => {
    dispatch(actions.queueUpload(uploadObj))
  },
  jumpToSeries: (seriesId) => {
    dispatch(actions.setHomeState(home.AppStateTypes.SERIES, { activeSeries: seriesId }))
  },

  jumpToMap: ({ bbox, center }) => {
    let bounds = bbox
    if (!bounds && center) {
      const bufferPts = buffer(point(center), 500, { units: 'meters' })
      bounds = bboxFunc(bufferPts)
    }

    if (bounds) {
      dispatch(actions.pushMapHistory({
        mode: home.MapModeTypes.FREE,
        bounds: bounds
      }))
      dispatch(actions.setHomeState(home.AppStateTypes.MAP, { activeSeries: null, initialPhoto: null }))
    }
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SearchConnect)
