import React, { useState } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import TextField from '@material-ui/core/TextField'
import DialogActions from '@material-ui/core/DialogActions'
import CssBaseline from '@material-ui/core/CssBaseline'
import BugReportIcon from '@material-ui/icons/BugReport'
import ErrorIcon from '@material-ui/icons/Error'
import { makeStyles } from '@material-ui/core/styles'

export const SEND_STATES = {
  UNSENT: 'UNSENT',
  SENDING: 'SENDING',
  SENT: 'SENT',
  ERROR: 'ERROR'
}

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    overflow: 'hidden',
    padding: theme.spacing(1),
    display: 'flex',
    flexDirection: 'column',
    textAlign: 'center',
    color: theme.palette.text.primary,
    border: `20px solid ${theme.palette.text.secondary}`
  },
  button: {
    // background: theme.palette.success.main,
    // color: theme.palette.success.contrastText
  },
  icon: {
    opacity: 0.5,
    margin: 'auto',
    flex: '0 1 10%',
    // height: '50%',
    width: '50%'
  },
  text: {
  },
  spacer: {
    flexGrow: 1
  },
  stack: {
    padding: theme.spacing(2),
    marginBottom: theme.spacing(2),
    fontSize: '8px',
    flexGrow: 1,
    textAlign: 'left',
    whiteSpace: 'pre-wrap',
    overflow: 'scroll',
    color: theme.palette.error.main,
    fontFamily: 'Consolas, monaco, monospace'
  }
}))

const ErrorScreen = ({ error, sendState, reportError }) => {
  const classes = useStyles()
  const [dlgOpen, setDialogOpen] = useState(false)
  const [note, setNote] = useState('')

  let stackEl = <div className={classes.spacer} />
  try {
    if (process.env.NODE_ENV === 'development') {
      stackEl = (
        <Typography className={classes.stack}>
          {error.stack}
        </Typography>
      )
    }
  }
  catch (e) {}

  let buttonText = 'Report this bug'
  let disabled = false
  let color = 'primary'
  switch (sendState) {
    case SEND_STATES.UNSENT:
      buttonText = 'Report this bug'
      break
    case SEND_STATES.SENDING:
      disabled = true
      color = 'default'
      buttonText = 'Sending...'
      break
    case SEND_STATES.SENT:
      color = 'default'
      disabled = true
      buttonText = 'Thank you for your feedback'
      break
    case SEND_STATES.ERROR:
      color = 'secondary'
      disabled = true
      buttonText = 'ERROR SENDING BUG REPORT'
      break
    default:
      break
  }

  return (
    <div className={classes.root}>
      <CssBaseline />
      <ErrorIcon className={classes.icon} color='error' />
      <Typography variant='subtitle1' className={classes.text} color='inherit'>
        Something went wrong
      </Typography>
      {error && error.message && <Typography variant='body1' className={classes.text} color='inherit'>
        {error.message}
      </Typography>}
      <Button
        className={classes.button}
        variant='contained'
        disabled={disabled}
        color={color}
        onClick={() => setDialogOpen(true)}
      >
        <BugReportIcon />
        {buttonText}
      </Button>
      {stackEl}
      <Dialog open={dlgOpen} onClose={() => setDialogOpen(false)} aria-labelledby='form-dialog-title'>
        <DialogTitle id='form-dialog-title'>Report a bug</DialogTitle>
        <DialogContent>
          <DialogContentText>
            What were you trying to do when the error happened?
          </DialogContentText>
          <TextField
            autoFocus
            margin='dense'
            multiline
            onChange={(e) => setNote(e.target.value)}
            rowsMax='6'
            id='note'
            placeholder='I tried to X and then Y happened...'
            label='Note'
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDialogOpen(false)} color='primary'>
            Cancel
          </Button>
          <Button onClick={() => {
            reportError(note)
            setDialogOpen(false)
          }} color='primary'>
            Send
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}

ErrorScreen.propTypes = {
  error: PropTypes.object,
  sendState: PropTypes.string,
  reportError: PropTypes.func.isRequired
}

export default ErrorScreen
