import React, { useRef, useLayoutEffect, useCallback } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import { fade } from '@material-ui/core/styles/colorManipulator'
import InputBase from '@material-ui/core/InputBase'

import SearchIcon from '@material-ui/icons/Search'
import { makeStyles } from '@material-ui/core/styles'
import { debounce } from '../../lib/util'

const useSearchBarStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.3),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.5)
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto'
    }
  },
  transparent: {
    backgroundColor: fade(theme.palette.common.white, 0.7),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.9)
    }
  },
  inputRoot: {
    color: 'inherit',
    width: '100%'
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 4),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: 200
    }
  },
  searchIcon: {
    width: theme.spacing(4),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  }
}))

/**
 *
 */
export const SearchInput = ({ autoFocus, disabled, onChange, onClick, defaultValue, isTransparent }) => {
  const inputRef = useRef()
  const classes = useSearchBarStyles()

  const debounceSearchText = useCallback(
    debounce(onChange, 500),
    [onChange]
  )

  useLayoutEffect(() => {
    if (inputRef.current && !autoFocus) {
      console.log('Setting blur')
      inputRef.current.blur()
    }
  }, [autoFocus])
  return (
    <div className={classNames(classes.root, isTransparent ? classes.transparent : null)} onClick={onClick}>
      <div className={classes.searchIcon}>
        <SearchIcon />
      </div>
      <InputBase
        autoFocus={!disabled && autoFocus}
        inputProps={{
          onBlur: () => {
            // console.log('onBlur')
            // inputRef.current.focus()
          }
        }}
        onChange={(e) => {
          // console.log('onChange', e.target.value)
          debounceSearchText(e.target.value)
        }}
        placeholder='Search'
        disabled={disabled}
        defaultValue={defaultValue}

        inputRef={inputRef}
        classes={{
          root: classes.inputRoot,
          input: classes.inputInput
        }}
      />
    </div>
  )
}

SearchInput.propTypes = {
  autoFocus: PropTypes.bool,
  isTransparent: PropTypes.bool,
  disabled: PropTypes.bool,
  onChange: PropTypes.func,
  defaultValue: PropTypes.string,
  onClick: PropTypes.func
}

export default SearchInput
