
import React from 'react'
import PropTypes from 'prop-types'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import DialogActions from '@material-ui/core/DialogActions'

import AppleIcon from '../../icons/Apple'
import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {

  },
  icon: {
    height: 50,
    width: '100%',
    margin: '0 auto',
    color: theme.palette.error.main
  },
  title: {

  }
})

const useStyles = makeStyles(style)

const FeatureDisabled = ({ title, message, onClose, open, icon }) => {
  const classes = useStyles()
  const IconComp = icon || AppleIcon
  return (
    <Dialog
      className={classes.root}
      disableBackdropClick
      fullWidth
      open={open}
      disableEscapeKeyDown
      maxWidth='xl'
      aria-labelledby='confirmation-dialog-title'
    >
      <DialogTitle id='confirmation-dialog-title' className={classes.title}>{title}</DialogTitle>
      <DialogContent>
        <IconComp className={classes.icon} />
        <Typography component='div'>
          {message}
        </Typography>

      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} color='primary'>
      Ok
        </Button>
      </DialogActions>
    </Dialog>
  )
}

FeatureDisabled.propTypes = {
  open: PropTypes.bool,
  title: PropTypes.string.isRequired,
  icon: PropTypes.any,
  message: PropTypes.string.isRequired,
  onClose: PropTypes.func.isRequired
}

export default FeatureDisabled
