import React from 'react'
import PropTypes from 'prop-types'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import Divider from '@material-ui/core/Divider'

// import { Menu as MenuIcon } from '@material-ui/icons'

const ContextMenu = ({ anchorEl, onClose, menuItems }) => {
  return (
    <Menu
      anchorEl={anchorEl}
      open={!!anchorEl}
      onClose={onClose}
    >
      {menuItems.map(({ icon, color, text, hide, disabled, action, divider }, idx) => {
        const Icon = icon || null
        if (hide) return null
        if (divider) return <Divider key={`contextMenu-${idx}`} />
        return (
          <MenuItem
            key={`contextMenu-${idx}`}
            disabled={disabled}
            onClick={() => {
              onClose()
              action()
            }}>
            {Icon && (
              <ListItemIcon>
                <Icon color={disabled ? 'disabled' : color} />
              </ListItemIcon>
            )}
            <ListItemText primary={text} primaryTypographyProps={{ color }} />
          </MenuItem>
        )
      })}
    </Menu>
  )
}

ContextMenu.propTypes = {
  anchorEl: PropTypes.object,
  onClose: PropTypes.func,
  menuItems: PropTypes.arrayOf(PropTypes.shape({
    text: PropTypes.string,
    icon: PropTypes.any,
    divider: PropTypes.any,
    hide: PropTypes.bool,
    disabled: PropTypes.bool,
    color: PropTypes.string,
    action: PropTypes.func
  }))
}

export default ContextMenu
