import React from 'react'
import PropTypes from 'prop-types'
// import { makeStyles } from '@material-ui/core/styles'
import { Marker } from 'react-map-gl'
import BaseMap from './BaseMap'
import Dot from './markers/Dot'
import BaseStaticMap from './BaseStaticMap'
// import style from './MapDot.style'

// const useStyles = makeStyles(style)

const MapDot = ({ staticMap, editable, latitude, longitude, onChange, direction, ...props }) => {
  const Map = staticMap ? BaseStaticMap : BaseMap
  // const classes = useStyles()
  const mapProps = staticMap ? {} : {
    touchRotate: false,
    dragRotate: false,
    showMe: editable,
    lockPan: !editable,
    onViewportChange: onChange
  }

  return (
    <Map
      {...mapProps}
      {...props}
    >
      {latitude && longitude ? (
        <Marker
          className={'singleMarker'}
          latitude={latitude}
          longitude={longitude}
        >
          <Dot direction={direction} />
        </Marker>
      )
        : <Dot direction={direction} />
      }

    </Map>
  )
}

MapDot.propTypes = {
  direction: PropTypes.number,
  latitude: PropTypes.number,
  longitude: PropTypes.number,
  editable: PropTypes.bool,
  staticMap: PropTypes.bool,
  onChange: PropTypes.func
}

export default MapDot
