import React, { useState, useLayoutEffect, useContext } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Dialog from '@material-ui/core/Dialog'
// import MenuIcon from '@material-ui/icons/Menu'
import QRCode from 'qrcode'
import { makeStyles } from '@material-ui/core/styles'
import { Paper } from '@material-ui/core'
import { SeriesContext } from '../contexts'

const useStyles = makeStyles(theme => ({
  root: {
  },
  title: {
    textAlign: 'center'
  },
  qrPaper: {
    padding: theme.spacing(2)
  },
  qrCode: {
    width: '100%',
    height: 400
  }
}))

const QRDisplay = ({ onClose, open }) => {
  const classes = useStyles()
  const { series } = useContext(SeriesContext)
  const [qrCodeEl, setQRCodeEl] = useState(null)
  const obj = { type: 'series', id: series.id }
  const encodedId = encodeURI(JSON.stringify(obj))
  const url = `${process.env.PUBLIC_URL}/?fromqr=${encodedId}`

  useLayoutEffect(() => {
    QRCode.toString(url, (err, svgStr) => {
      if (err) console.error(err)
      else setQRCodeEl(svgStr)
    })
  }, [series])

  return (
    <Dialog
      open={open}
      onClose={onClose}
      className={classes.root
      }>
      <Paper className={classes.qrPaper}>
        <Typography variant='subtitle1' component='div' className={classes.title}>{series.name}</Typography>
        <svg className={classes.qrCode} dangerouslySetInnerHTML={{ __html: qrCodeEl }} />
        {series.coords && <Typography component='div' className={classes.title}>[ {series.coords[0]}, {series.coords[1]} ]</Typography>}
      </Paper>
    </Dialog>
  )
}

QRDisplay.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool
}

export default QRDisplay
