import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer'
import Avatar from '@material-ui/core/Avatar'

import List from '@material-ui/core/List'
import Divider from '@material-ui/core/Divider'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import CloudDownload from '@material-ui/icons/CloudDownload'
import { AppContext } from '../contexts'

import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import QRCodeIcon from '../icons/QRCode'
import AccountCircleIcon from '@material-ui/icons/AccountCircle'

const useStyles = makeStyles(theme => ({
  drawerInner: {
    width: 250,
    display: 'flex',
    flexDirection: 'column',
    height: '100%'
  },
  disableListItem: {
    color: 'rgba(0, 0, 0, 0.26)'
  },
  avatar: {
    height: theme.spacing(7),
    width: theme.spacing(7),
    border: `3px solid ${theme.palette.primary.main}`,
    margin: theme.spacing(0, 1)
  },
  gravatarImg: {
    width: '100%'
  },
  spacer: {
    flexGrow: 1
  },
  fullList: {
    width: 'auto'
  }
}))

const SideDrawer = ({ open, onOpen, onClose, onDisabledClick }) => {
  const classes = useStyles()
  const { signOut, signedIn, forceSignIn, user, onQRClick, addToHomeScreen, isIos } = useContext(AppContext)
  return (
    <SwipeableDrawer
      open={open}
      onClose={onClose}
      onOpen={onOpen}
    >
      <div
        className={classes.drawerInner}
        role='presentation'
        onClick={onClose}
        onKeyDown={onClose}
      >
        {user && user.username && (
          <List>
            <ListItem className={classes.root} onClick={() => {}} disableGutters>
              <Avatar className={classes.avatar}>
                {signedIn && user
                  ? <img alt='profile' className={classes.gravatarImg} src={user.gravatar} />
                  : <AccountCircleIcon color='inherit' className={classes.loginButton} />
                }
              </Avatar>
            </ListItem>
            <ListItem className={classes.root} onClick={() => {}}>
              <ListItemText
                primary={user.username}
              />
            </ListItem>
          </List>

        )}
        <Divider />
        <List>
          <ListItem
            className={isIos ? classes.disableListItem : null}
            button
            onClick={isIos ? onDisabledClick : onQRClick}
          >
            <ListItemIcon>
              <QRCodeIcon />
            </ListItemIcon>
            <ListItemText primary={'Scan QR Code'} />
          </ListItem>
          {addToHomeScreen && (
            <ListItem button onClick={addToHomeScreen}>
              <ListItemIcon>
                <CloudDownload />
              </ListItemIcon>
              <ListItemText primary={'Install this app'} secondary={'It works better!'} />
            </ListItem>
          )}
        </List>
        <div className={classes.spacer} />
        <Divider />
        <List>
          {signedIn
            ? (
              <ListItem button onClick={signOut}>
                <ListItemIcon>
                  <ExitToAppIcon />
                </ListItemIcon>
                <ListItemText primary={'Sign Out'} />
              </ListItem>
            )
            : (
              <ListItem button onClick={forceSignIn}>
                <ListItemIcon>
                  <AccountCircleIcon />
                </ListItemIcon>
                <ListItemText primary={'Sign In'} />
              </ListItem>
            )}

        </List>
      </div>
    </SwipeableDrawer>
  )
}

SideDrawer.propTypes = {
  open: PropTypes.bool,
  onOpen: PropTypes.func,
  onClose: PropTypes.func,
  onDisabledClick: PropTypes.func
}

export default SideDrawer

