import React, { useState } from 'react'
import PropTypes from 'prop-types'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Toolbar from '@material-ui/core/Toolbar'
import FormControl from '@material-ui/core/FormControl'
import InputLabel from '@material-ui/core/InputLabel'
import Select from '@material-ui/core/Select'
import AuthContainer from './AuthContainer'

// import MenuIcon from '@material-ui/core/Menu'
import { makeStyles } from '@material-ui/core/styles'
import style from './auth.style'
// import AuthPhoneNum from './AuthPhoneNum'
const useStyles = makeStyles(style)

const SignUp = ({ handleInputChange, signUpFields, signUpFieldLabels, signUpFieldPlaceholders,
  handleSignup, handleBack, countryDialCodes, defaultDialCode }) => {
  console.log('SIGNUPPROPS', { defaultDialCode, countryDialCodes, signUpFields, signUpFieldLabels, signUpFieldPlaceholders })
  const [selectVal, setSelectVal] = useState(defaultDialCode)
  const classes = useStyles()
  const handleDialCodeChange = e => {
    handleInputChange(e)
    setSelectVal(e.target.value)
  }
  return (
    <AuthContainer>
      {
        signUpFields.map((field, idx) => {
          return field.key !== 'phone_number' ? (
            <TextField
              fullWidth
              autoFocus={signUpFields.findIndex(f => f.key === field.key) === 0}
              type={field.type}
              name={field.key}
              key={field.key}
              placeholder={signUpFieldPlaceholders[idx]}
              label={signUpFieldLabels[idx]}
              className={classes.textField}
              margin='normal'
              onChange={handleInputChange}
            />
          ) : (
            <FormControl
              className={classes.phoneNumDropdown}
              fullWidth
              key='phonenum'
            >
              <InputLabel htmlFor='dial_code' shrink>
                {signUpFieldLabels[idx]}
                {field.required ? '*' : ''}
              </InputLabel>
              <div className={classes.phoneNumInner}>
                <Select
                  value={selectVal}
                  onChange={handleDialCodeChange}
                  inputProps={{
                    name: 'dial_code',
                    id: 'dial_code'
                  }}
                >
                  {countryDialCodes.map(dialCode =>
                    <option key={dialCode} value={dialCode}>
                      {dialCode}
                    </option>
                  )}
                </Select>
                <TextField
                  placeholder={signUpFieldPlaceholders[idx]}
                  type='tel'
                  fullWidth
                  id='phone_line_number'
                  key='phone_line_number'
                  name='phone_line_number'
                  onChange={handleInputChange}
                />
              </div>
            </FormControl>
          )
        })
      }
      <Toolbar disableGutters>
        <Button color='secondary' variant='outlined' onClick={handleBack}>
          Back
        </Button>
        <div className={classes.spacer} />
        <Button
          className='enterAction'
          component='button'
          color='primary'
          variant='contained'
          onClick={handleSignup}
        >
          Create Account
        </Button>
      </Toolbar>
    </AuthContainer>
  )
}

SignUp.propTypes = {
  handleInputChange: PropTypes.func.isRequired,
  signUpFields: PropTypes.array.isRequired,
  signUpFieldLabels: PropTypes.array.isRequired,
  signUpFieldPlaceholders: PropTypes.array.isRequired,
  countryDialCodes: PropTypes.array.isRequired,
  defaultDialCode: PropTypes.string.isRequired,
  handleSignup: PropTypes.func.isRequired,
  handleBack: PropTypes.func.isRequired
}

export default SignUp
