import React from 'react'
import PropTypes from 'prop-types'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Link from '@material-ui/core/Link'
import Toolbar from '@material-ui/core/Toolbar'
// import MenuIcon from '@material-ui/core/Menu'
import { makeStyles } from '@material-ui/core/styles'
import style from './auth.style'
import AuthContainer from './AuthContainer'

const useStyles = makeStyles(style)

const ConfirmSignUp = ({ handleInputChange, handleBack, handleResend, handleConfirm }) => {
  const classes = useStyles()
  return (
    <AuthContainer>
      <TextField
        id='username'
        fullWidth
        autoFocus
        name='username'
        label='Username'
        className={classes.textField}
        margin='normal'
        onChange={handleInputChange}
      />
      <TextField
        id='confirmationCode'
        fullWidth
        name='code'
        label='Confirmation Code'
        className={classes.textField}
        margin='normal'
        onChange={handleInputChange}

      />
      <Link component='button' variant='body2' className={classes.resendCode} onClick={handleResend}>
          Resend Code
      </Link>
      <Toolbar disableGutters>

        <Button color='secondary' variant='outlined' onClick={handleBack}>
          Back
        </Button>
        <div className={classes.spacer} />
        <Button
          color='primary'
          variant='contained'
          className='enterAction'
          onClick={handleConfirm}
        >
          Sign In
        </Button>
      </Toolbar>
    </AuthContainer>
  )
}

ConfirmSignUp.propTypes = {
  handleInputChange: PropTypes.func.isRequired,
  handleBack: PropTypes.func.isRequired,
  handleResend: PropTypes.func.isRequired,
  handleConfirm: PropTypes.func.isRequired
}

export default ConfirmSignUp
