import React from 'react'
import PropTypes from 'prop-types'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Toolbar from '@material-ui/core/Toolbar'
// import MenuIcon from '@material-ui/core/Menu'
// import { FederatedButtons } from 'aws-amplify-react'
import { makeStyles } from '@material-ui/core/styles'
import style from './auth.style'
import AuthContainer from './AuthContainer'
import { Typography } from '@material-ui/core'

const useStyles = makeStyles(style)

const SignIn = ({ loading,
  handleSignup,
  federated,
  handleForgotPassword,
  handleInputChange,
  handleBeAnonymous,
  handleSignIn, ...props }) => {
  const classes = useStyles()
  return (
    <AuthContainer>
      {/* {props.federated && (
        <FederatedButtons {...props} />
      )} */}
      <TextField
        id='username'
        fullWidth
        key='username'
        name='username'
        label='Username'
        color='inherit'
        autoFocus
        className={classes.textField}
        margin='normal'
        onChange={handleInputChange}
      />
      <TextField
        fullWidth
        id='password'
        label='Password'
        color='inherit'
        key='password'
        name='password'
        type='password'
        className={classes.textField}
        autoComplete='current-password'
        margin='normal'
        onChange={handleInputChange}
      />
      <Toolbar disableGutters>

        <Button component='button' color='secondary' variant='contained' disabled={loading} onClick={handleSignup}>
          New Account
        </Button>
        <div className={classes.spacer} />
        <Button
          className='enterAction'
          component='button'
          color='primary'
          variant='contained'
          disabled={loading}
          onClick={handleSignIn}
        >
          Sign In
        </Button>
      </Toolbar>
      <Button color='primary' onClick={handleForgotPassword} size='small'>
      Forgot Password
      </Button>
      <div className={classes.noSignin}>
        <Button fullWidth size='small' variant={'outlined'} onClick={handleBeAnonymous} color='inherit'>
          Continue without logging in
        </Button>
        <Typography variant='caption' className={classes.warnText} color='textSecondary' component='div'>
          You will not be able to add content and many features will be unavailable.
        </Typography>
      </div>
    </AuthContainer>
  )
}

SignIn.propTypes = {
  loading: PropTypes.bool,
  federated: PropTypes.object,
  handleSignup: PropTypes.func.isRequired,
  handleForgotPassword: PropTypes.func.isRequired,
  handleInputChange: PropTypes.func.isRequired,
  handleBeAnonymous: PropTypes.func.isRequired,
  handleSignIn: PropTypes.func.isRequired
}

export default SignIn
