
import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'

import CssBaseline from '@material-ui/core/CssBaseline'
import DialogContent from '@material-ui/core/DialogContent'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

import { makeStyles } from '@material-ui/core/styles'

import { SeriesContext } from '../contexts'

import style from './SeriesEdit.style'
import SeriesEditInner from './subViews/SeriesEditInner'

const useStyles = makeStyles(style)

const SeriesEdit = ({ onOk, onCancel }) => {
  const classes = useStyles()
  const { series } = useContext(SeriesContext)
  const [delta, setDelta] = useState({})

  return (
    <Dialog
      scroll='paper'
      className={classes.root}
      fullScreen
      open
      fullWidth
      onClose={onCancel}
    >
      <CssBaseline />
      <DialogTitle disableTypography className={classes.dlgTitle}>
        <Typography variant='subtitle1' color='inherit' className={classes.grow}>
           Edit Series Details
        </Typography>
      </DialogTitle>
      <DialogContent className={classes.inner}>
        <SeriesEditInner
          series={series}
          delta={delta}
          onChange={setDelta}
        />
      </DialogContent>
      <DialogActions>
        <Button className={classes.menuButton} color='inherit' onClick={() => setDelta({})} aria-label='Menu'>
            Reset
        </Button>
        <Button className={classes.menuButton} color='inherit' onClick={onCancel} aria-label='Menu'>
            Cancel
        </Button>
        <Button className={classes.menuButton} color='inherit' onClick={() => onOk({ id: series.id, ...delta })} aria-label='Menu'>
            Ok
        </Button>
      </DialogActions>
    </Dialog>
  )
}

SeriesEdit.propTypes = {
  // now some functions
  onOk: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired
}

export default SeriesEdit
